// import { Image as CImage } from 'cloudinary-react'
import { Loading } from 'components/utils/Icon'
import * as React from 'react'
import * as Loadable from 'react-loadable'
import { Image, ImageProps, Loader } from 'semantic-ui-react'

const CImage = Loadable({
  loader: () => import('./CloudinaryWrapper'),
  loading: Loading,
})

export interface IResponsiveImageProps {
  semanticProps?: ImageProps
  cloudinaryProps?: any
  publicId?: string
  grow?: true
  style?: any
  onLoad?: (e: React.SyntheticEvent<HTMLImageElement>) => any
}

interface IState {
  loaded: boolean
}

export default class extends React.Component<IResponsiveImageProps, IState> {
  public state = {
    loaded: false,
  }
  public render() {
    const { grow, semanticProps, cloudinaryProps, publicId, children, style } = this.props
    return (
      <Image {...semanticProps} style={{ overflow: 'hidden', ...style }}>
        <CImage
          cloud_name={process.env.REACT_APP_CLOUDINARY_CLOUD_NAME}
          dpr="auto"
          className={grow && 'grow'}
          responsive={true}
          width="auto"
          crop="scale"
          onLoad={(e: React.SyntheticEvent<HTMLImageElement>) => {
            this.props.onLoad && this.props.onLoad(e)
            this.setState({ loaded: true })
          }}
          secure={true}
          publicId={publicId}
          quality="auto"
          fetchFormat="auto"
          {...cloudinaryProps}
        />
        {!this.state.loaded && (
          <Loader
            style={{
              marginLeft: 'auto',
              marginRight: 'auto',
              display: 'block',
            }}
            size="huge"
            inline={true}
          />
        )}
        {children}
      </Image>

    )
  }
}



