import { IAd } from 'api/interfaces'
import Icon from 'components/utils/Icon'
import ResponsiveImage from 'components/utils/ResponsiveImage'
import * as React from 'react'
import { Header, Table, TableProps } from 'semantic-ui-react'
import { prettyDate } from 'utils/date'
const isFuture = require('date-fns/is_future')
const differenceInSeconds = require('date-fns/difference_in_seconds')

interface IProps {
  auctions: IAd[]
  onClick: (id: number) => any
  tableProps?: TableProps
}

class AuctionTable extends React.Component<IConnectProps> {
  public render() {
    const { auctions, onClick, morphs, categories, tableProps, user, userbBidAuctionIds, unreadMessagesAuctionIds,
    } = this.props
    if (!categories || !user) {
      return null
    }
    return (
      <Table
        selectable={true}
        {...tableProps}
        unstackable={true}
      >
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>Navn</Table.HeaderCell>
            <Table.HeaderCell>Kategori</Table.HeaderCell>
            <Table.HeaderCell>Pris</Table.HeaderCell>
            <Table.HeaderCell>Bud</Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {auctions
            .sort(
              (a, b) => {
                if (a.deadline && b.deadline) {
                  return differenceInSeconds(b.deadline, a.deadline)
                }
                return -differenceInSeconds(b.date, a.date)
              },
          )
            .map(
              ({
                highestBidderId,
                currentPrice,
                bidCount,
                id,
                date,
                images,
                deadline,
                category: categoryID,
                morph: morphId,
              }) => {
                const category = categories[Number(categoryID)]
                const morph = morphs[Number(morphId)]
                const deadlineInFuture = isFuture(deadline)
                const userIsHighBidder = user.id === highestBidderId
                const userDidBid = userbBidAuctionIds && userbBidAuctionIds.indexOf(id) !== -1
                return (
                  <Table.Row key={id} positive={!!deadlineInFuture}>
                    <Table.Cell
                      onClick={() => onClick(id)}
                    >
                      <Header
                        as="h4"
                        style={{}}
                        image={true}
                      >
                        {!!images && !!images.length && (

                          <ResponsiveImage
                            style={{ overflow: 'none', width: 75 }}
                            publicId={images[0].public_id}
                            cloudinaryProps={{
                              width: 75,
                            }}
                          />
                        )}
                        <Header.Content
                        >
                          {userDidBid && (
                            userIsHighBidder ? (
                              <Icon color="green" icon="badge-check" />
                            ) : (
                                <Icon color="orange" icon="badge" />
                              )
                          )}
                          {unreadMessagesAuctionIds.indexOf(id) !==   -1 &&   <Icon color="red" icon="envelope" />}
                          {morph && morph.title}
                          <Header.Subheader>
                            Opprettet {prettyDate(date)}
                            {deadlineInFuture && `, Avsluttes ${prettyDate(deadline)}`}
                          </Header.Subheader>
                        </Header.Content>
                      </Header>
                    </Table.Cell>
                    <Table.Cell>{category && category.title}</Table.Cell>
                    <Table.Cell>{currentPrice}</Table.Cell>
                    <Table.Cell>{bidCount}</Table.Cell>
                  </Table.Row>
                )
              },
          )}
        </Table.Body>
      </Table>
    )
  }
}

import { connect } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ base, user, conversation }: IStoreState) => {
    const messages = Object.keys(conversation.message).map((k) => conversation.message[Number(k)])
    const unreadMessagesAuctionIds = messages.filter(
      ({ unread }) => unread,
    ).map(
      ({ auction_id }) => auction_id,
    )
    const userbBidAuctionIds = user.user && user.user.bid_auctions
    return {
      unreadMessagesAuctionIds,
      categories: base.categories,
      morphs: base.morphs,
      user: user.user,
      userbBidAuctionIds,
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(AuctionTable)



