import Icon from 'components/utils/Icon'
import * as React from 'react'
import { Link, RouteComponentProps } from 'react-router-dom'
import { Dropdown, Menu, Responsive } from 'semantic-ui-react'
import { route } from 'utils/misc'
import Logo from './Logo'

interface MenuItem {
  name?: string,
  path?: string
  action?: () => any
  display?: string
  render?: (props: IConnectProps) => any
  requiresLogin?: boolean
  disableOnLogin?: boolean
}
interface IProps {
  onNavigate?: (path: string) => any
  flatten?: boolean
}

class Nav extends React.Component<IConnectProps> {

  public performLogout = () => {
    this.props.logoutApi()
  }
  public navigate = (path: string) => {
    this.props.onNavigate && this.props.onNavigate(path)
    // Makes it seem snappier, since onNavigate usually has some visual
    // feedback on click, so make sure we show that before we navigate
    setTimeout(
      () => this.props.history.push(path),
      10,
    )

  }
  public menuItems: MenuItem[] = [
    // tslint:disable-line
    {
      name: 'auction',
      path: route.auctionList(),
      display: 'Reptil Auksjonen',
    },
    {
      name: 'newAd',
      path: route.newAd(),
      display: 'Opprett annonse',
      requiresLogin: true,
    },
    {
      name: 'login',
      path: route.login(),
      display: 'Logg inn/Bli medlem',
      disableOnLogin: true,
    },
    {
      name: 'user-dropdown',
      render: ({ user, unreadMessagesCount, flatten }) => {
        if (!user) {
          return null
        }
        const ThisItem = flatten ? Menu.Item : Dropdown.Item
        const ThisHeader = flatten ? Menu.Header : Dropdown.Header
        return (
          <>
            {!flatten && (
              <>
                <ThisHeader
                  icon={<Icon icon="user" />}
                  disabled={true}
                  content={user.email}
                />
                <Dropdown.Divider />
              </>

            )}
            <ThisItem
              onClick={() => this.navigate(route.member())}
            >
              <Icon icon="ticket-alt" />
              Medlemsskap
            </ThisItem>
            <ThisItem
              description={`${unreadMessagesCount} nye`}
              onClick={() => this.navigate(!!unreadMessagesCount ? route.unreadMessages() : route.messages())}
              content={(
                <>
                  <Icon icon="envelope" {...unreadMessagesCount > 0 && { color: 'red' }} />
                  Meldinger
                  </>
              )}
            />
            <ThisItem
              onClick={() => this.navigate(route.userAuctions())}
            >
              <Icon icon="bug" />
              Mine auksjoner
            </ThisItem>
            <ThisItem
              onClick={() => this.navigate(route.userBids())}
            >
              <Icon icon="bug" />
              Mine bud
            </ThisItem>
            <ThisItem
              onClick={() => {
                this.performLogout()
                this.props.onNavigate && this.props.onNavigate(route.logout())
              }}
            >
              <Icon icon="sign-out-alt" />
              Logg ut
            </ThisItem>
          </>
        )
      },
      requiresLogin: true,
    },
  ]

  public navFilter = ({ disableOnLogin, requiresLogin }: MenuItem, isLoggedIn: boolean) => {
    if (disableOnLogin && isLoggedIn) {
      return false
    }
    if (requiresLogin && !isLoggedIn) {
      return false
    }
    return true
  }

  public LogoLink = () => (
    <Menu.Item
      name="reptilesOfNorway"
      as="a"
      href="https://www.reptilesofnorway.no"
      target="_blank"
    >
      Reptiles Of Norway
    </Menu.Item>
  )

  public MenuItems = () => {
    const { unreadMessagesCount, user } = this.props
    return (
      <>
        {this.menuItems
          .filter((m) => this.navFilter(m, !!this.props.user))
          .map((item) => {
            const { name, display, path, action, render } = item
            if (render) {
              if (this.props.flatten) {

                return render(this.props)
              }
              return (
                <Dropdown
                  floating={true}
                  key={'user-dropdown'}
                  inline={true}
                  item={true}
                  {...unreadMessagesCount > 0 && {
                    icon: (
                      <div className="pl-2" ><Icon icon="envelope" color="red" />{unreadMessagesCount}</div>
                    ),
                  }}
                  text={user && user.given_name}
                  style={{ borderBottomColor: 'rgba(0,0,0,0)' }}
                >
                  <Dropdown.Menu>
                    {render(this.props)}
                  </Dropdown.Menu>
                </Dropdown>
              )
            }
            return (
              <Menu.Item
                key={name}
                name={name}
                active={this.props.location.pathname === path}
                onClick={() => {
                  path && this.navigate(path)
                  action && action()
                }}
              >
                {display}
              </Menu.Item>
            )
          },
        )}
      </>
    )
  }

  public render() {
    const { LogoLink, MenuItems } = this
    return (
      <>
        <Responsive
          minWidth={Responsive.onlyTablet.minWidth}
        >
          <Link to={route.auctionList()}>
            <Logo semanticProps={{ size: 'small' }} />
          </Link>
        </Responsive>
        <Menu
          inverted={true}
          stackable={true}
          pointing={true}
          secondary={true}
        >
          <LogoLink />
          <MenuItems />
        </Menu>
      </>
    )
  }
}

import { logoutApi } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, conversation }: IStoreState) => {
    const messages = Object.keys(conversation.message).map((k) => conversation.message[Number(k)])
    const unreadMessages = messages.filter(
      ({ unread, user: fromUserId }) => unread && user.user && user.user.id !== fromUserId)
    return {
      user: user.user,
      unreadMessagesCount: unreadMessages.length,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      logoutApi: () => {
        return dispatch(logoutApi())
      },
    }
  },
)
type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any> & IProps
export default connectCreator(Nav)



