import { DefconLevel, IMessage, IMessageItem } from 'interfaces'
import { Dispatch } from 'react-redux'

export enum MessageR {
  newMessage = 'newMessage',
  dismissMessage = 'dismissMessage',
}

export type MessageT =
  { type: MessageR.dismissMessage, id: string } |
  { type: MessageR.newMessage, message: IMessage }

export const newMessage = (message: IMessage): MessageT => {
  return {
    type: MessageR.newMessage,
    message,
  }
}

export const dismissMessage = (id: string): MessageT => {
  return {
    type: MessageR.dismissMessage,
    id,
  }
}

// In case there is an unknown, unhandled error on the server.
const defaultMessage = (message: string): IMessageItem[] => ([{
  defcon_level: DefconLevel.warning,
  message: `En feil oppstod: ${message}`,
}])

export const errorCatcher = (dispatch: Dispatch<any>, id: string, callback?: any) => (e: any) => {
  callback && dispatch(callback())
  const messages = e.messages || defaultMessage(JSON.stringify(e))
  return dispatch(newMessage({
    type: id,
    messages,
    code: '',
    id,
  }))
}

export const newShortMessage = (message: string, id: string) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(newMessage({
      type: 'msg',
      code: 'msg',
      messages: [{
        defcon_level: DefconLevel.info,
        message,
      }],
      id,
    }))
  }
}



