import FourOhFour from 'components/utils/404'
import { Loading } from 'components/utils/Icon'
import * as  React from 'react'

interface IState {
  html: string
  loading: boolean
  error: boolean
}

interface IProps {
  url: string
}

class HtmlRender extends React.Component<IProps, IState> {
  public state = {
    html: '',
    loading: false,
    error: false,
  }
  public componentDidMount() {
    this.setState({ loading: true })
    fetch(this.props.url).then(
      (r) => r.text().then((html) => this.setState({ html, loading: false })),
    ).catch(
      () => this.setState({ error: true, loading: false }),
    )
  }
  public render() {
    const { html, loading, error } = this.state
    if (loading) {
      return <Loading active={true} />
    }
    if (error || !html) {
      return <FourOhFour />
    }
    return (
      <div dangerouslySetInnerHTML={{ __html: html }} />
    )
  }
}

export default HtmlRender



