/* eslint new-cap: 0 */

import FourOFour from 'components/utils/404'
import errorBoundaryWrapper from 'components/utils/ErrorBoundary'
import { Loading } from 'components/utils/Icon'
import Ad from 'containers/Ad'
import Auction from 'containers/Auction'
import HtmlRender from 'containers/HtmlRender'
import Login from 'containers/Login'
import Member from 'containers/Member'
import Messages from 'containers/Messages'
import Premium from 'containers/Premium'
import ProfileForm from 'containers/ProfileForm'
import UserAuctions from 'containers/UserAuctions'
import UserBids from 'containers/UserBids'
import * as React from 'react'
import { Route, RouteProps, Switch } from 'react-router-dom'
import { route } from 'utils/misc'
export default () => {
  return (
    <Switch>
      {appRoutes.map(({component, render, ...rest}: any) => (
        <Route
          component={errorBoundaryWrapper(component || render )}
          key={rest.path}
          {...rest}
        />
      ))}
    </Switch>
  )
}
import * as Loadable from 'react-loadable'

const appRoutes: RouteProps[] & any = [
  {
    render: () => <HtmlRender url="/tos.html" />,
    path: route.tos(),
  },
  {
    render: () => <HtmlRender url="/privacy.html" />,
    path: route.privacy(),
  },
  {
    component: ProfileForm,
    path: route.profileForm(),
  },
  {
    component: Loadable({
      loader: () => import('components/Forms/ReptileDocForm'),
      loading: Loading,
    }),
    path: route.reptileForm(),
  },
  {
    component: Loadable({
      loader: () => import('containers/NewAd'),
      loading: Loading,
    }),
    path: route.newAd(),
  },
  {
    component: UserAuctions,
    path: route.userAuctions(),
  },
  {
    component: Messages,
    path: route.messages(),
  },
  {
    component: UserBids,
    path: route.userBids(),
  },
  {
    component: Premium,
    path: route.premiumRoute(),
  },
  {
    component: Member,
    path: route.member(),
  },
  {
    component: Login,
    path: route.login(),
  },
  {
    component: Ad,
    path: route.auction(':id' as any),
  },
  {
    component: Auction,
    path: route.auctionList(),
  },
  {
    component: FourOFour,
    path: route.fourOFour(),
  },
]



