import { UserInfo } from 'interfaces'

import { IProfileForm } from 'forms/profile'
import { postHeaders, VERBS } from './constants'
import { myFetch } from './helpers'
import { IAdResponseMulti, IBid, ITransaction } from './interfaces'

interface IBidResponse extends IAdResponseMulti {
  bids: IBid[]
}

export const fetchUserInfo = () => myFetch(
  '/json/v1/user/',
  {
    headers: postHeaders,
  },
) as Promise<{ user: UserInfo }>

export const userCancelSubscription = () => myFetch(
  '/json/v1/user/cancel_subscription',
  {
    headers: postHeaders,
  },
) as Promise<{ user: UserInfo }>

export const userPostProfile = (profile: IProfileForm) => myFetch(
  '/json/v1/user/profile',
  {
    method: VERBS.PATCH,
    body: profile,
    headers: postHeaders,
  },
) as Promise<{ user: UserInfo }>

export const fetchUserTransactions =
  () => myFetch('/json/v1/user/transactions') as Promise<{
    transactions: ITransaction[],
  }>

export const fetchUserBids =
  () => myFetch('/json/v1/user/bid') as Promise<IBidResponse>

export const fetchUserAuctions =
  () => myFetch('/json/v1/user/auction') as Promise<IAdResponseMulti>

export const postAuthToken = (idToken: string, provider: string) => myFetch(
  `/json/v1/auth/${provider}`,
  {
    method: VERBS.POST,
    headers: {
      ...postHeaders,
      Authorization: idToken,
      provider,
    },
  },
)
export const logout = () => myFetch('/logout') as Promise<{}>



