const cloudName = process.env.REACT_APP_CLOUDINARY_CLOUD_NAME

export
  interface ICurrency {
  currency?: string
  position?: 'before' | 'after' | null
  decimals?: number
}

export const prettyCurrency = (amount: string | number, userOptions?: ICurrency) => {
  const { currency, position, decimals } = {
    currency: 'kr.',
    position: 'before',
    decimals: 0,
    ...userOptions,
  }
  const cash = Number(amount).toFixed(decimals).toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')
  if (position === 'before') {
    return `${currency} ${cash}`
  }
  if (position === 'after') {
    return `${cash} ${currency}`
  }
  return cash
}

export const route = {
  premium: (pathName?: string) => `/premium${pathName ? '/' + pathName : ''}`,
  premiumRoute: () => `/premium/:path_name?`,
  profileForm: () => '/member/form',
  unreadMessages: () => '/member/messages/unread',
  messagesOverview: () => '/member/messages',
  messages: () => '/member/messages',
  member: () => '/member',
  userAuctions: () => '/member/auctions',
  userBids: () => '/member/bids',
  auction: (id: number) => `/${id}`,
  auctionList: () => '/',
  home: () => '/',
  fourOFour: () => '/',
  login: () => '/login',
  logout: () => '/logout',
  newAd: () => '/new',
  reptileForm: () => '/form/reptile',
  register: () => '/register',
  tos: () => '/tos',
  privacy: () => '/privacy',
}

export const stripHTML = (html: string) => {
  const doc = new DOMParser().parseFromString(html, 'text/html')
  return doc.body.textContent || ''
}

export const getImageUrl = (publicId: string, args = '') =>
  `https://res.cloudinary.com/${
  cloudName
  }/image/upload/${
  args ? args + '/' : ''
  }${publicId}`

export const getCloudinaryUrlWidthFraction = (
  publicId: string, fraction: number = 1, height?: number) => {
  return getImageUrl(
    publicId,
    `c_fill,dpr_${
    window.devicePixelRatio ? window.devicePixelRatio.toFixed(0) : 1
    },f_auto,q_auto,w_${
    window.innerWidth * fraction
    }${
    height ? `,h_${height}` : ''
    }`,
  )
}

export const getViewPortSize = () => ({
  width: Math.max(
    document.documentElement.clientWidth,
    window.innerWidth || 0),
  height: Math.max(
    document.documentElement.clientHeight,
    window.innerHeight || 0),
})

export const range = (a: number, b: number, step: number = 1) => {
  const A = []
  if (typeof a === 'number') {
    A[0] = a
    step = step || 1
    while (a + step <= b) {
      A[A.length] = a += step
    }
  }
  return A
}

function noscroll() {
  window.scrollTo(0, 0)
}

export function disableScroll() {
  document.body.style.overflow = 'hidden'
  window.addEventListener('scroll', noscroll)
}
export function enableScroll() {
  document.body.style.overflow = 'visible'
  window.removeEventListener('scroll', noscroll)
}

export const isEmpty = (obj: any) => Object.keys(obj).length === 0 && obj.constructor === Object



