// import { postHeaders, VERBS } from './constants'
import { postHeaders, VERBS } from './constants'
import { myFetch, serialize } from './helpers'
import {
  IConversationResponse,
  IMessageFull, IMessagesResponse, IReadStatus,
} from './interfaces'

interface Excludables {
  users?: string[]
  ads?: string[]
}
export const fetchConversation = (conversationId: number) => myFetch(
  `/json/v1/conversation/${conversationId}`,
) as Promise<IConversationResponse>

export const fetchWithExludes = (url: string, excludes?: Excludables) =>
  myFetch(`${url}${serialize(excludes)}`)

export const fetchOverview = (excludes?: Excludables) =>
  fetchWithExludes(`/json/v1/conversation/overview`, excludes) as Promise<IMessagesResponse>

export const fetchUnread = (excludes?: Excludables) =>
  fetchWithExludes(`/json/v1/conversation/`, excludes) as Promise<IMessagesResponse>

export const postMessageInConversation =
  (conversationId: number, message: string) => myFetch(
    '/json/v1/conversation/',
    {
      body: { conversation_id: conversationId, message },
      method: VERBS.POST,
      headers: postHeaders,
    },
  ) as Promise<{ message: IMessageFull }>

export const updateReadStatusMultiple =
  (updates: IReadStatus[]) => myFetch(
    '/json/v1/conversation/set_read',
    {
      body: { updates },
      method: VERBS.PUT,
      headers: postHeaders,
    },
  ) as Promise<{ status: 'OK' }>

export const fetchNewMessagesUpdateReadStatus =
  (conversationId: number, readAt: Date) => myFetch(
    `/json/v1/conversation/${conversationId}`,
    {
      body: { conversation_id: conversationId, read_at: readAt },
      method: VERBS.PUT,
      headers: postHeaders,
    },
  ) as Promise<IConversationResponse>



