import { UsersInfo } from 'interfaces'
import { IAuctionStatus, VERBS } from './constants'

export interface FetchOptions {
  body?: {}
  headers?: {}
  method?: VERBS
}

export enum Sex {
  m = 'male',
  f = 'female',
  o = 'other',
}

export interface IImage {
  public_id: string
}

export interface IReviewResponse {
  auction: number
  date: Date
  id: number
  message: string
  modified: Date
  rating: number
  reviewee: number
  reviewer: number
}

export interface IReadStatus {
  id: number
  read_at: Date
}

export interface IAd {
  bidCount: number
  category: string
  currentPrice: number
  buy_now_price?: number
  date: Date
  date_of_birth: Date
  deadline: Date
  conversations?: number[]
  description_html: string
  highestBidderId?: number
  id: number
  images: IImage[]
  askingPrice: number
  minBid: number
  misconceptions_html: string
  morph: number
  status: IAuctionStatus
  diet: string
  active?: boolean
  sex: Sex
  weight: number
  reviews: number[]
  user: number
  proven_breeder: boolean
  genetic_bomb: boolean
  ready_for_breeding: boolean
}
export interface IAdNew {
  category: string
  currentPrice: number
  date_of_birth: Date
  buy_now_price: number
  interval: number
  description: string
  images: IImage[]
  askingPrice: number
  minBidIncrease: number
  misconceptions: string
  morph: string
  diet: string
  sex: Sex
  weight: number
  user: number
  agreedTOS: boolean
  proven_breeder: boolean
  genetic_bomb: boolean
  ready_for_breeding: boolean
}

export interface ITransaction {
  date: string
  id: number
  transaction_id: string
  premium: number
  price: number
}

export interface IMessageFrom {
  date: Date
  id: number
  user: number
}
export interface IMessageFromServer extends IMessageFrom {
  message_html: string
  unread: boolean
  auction_id?: number
}
export interface IMessageNew extends IMessageFrom {
  message: string
}

export interface IMessageFull extends IMessageFromServer {
  conversation: number
}

export interface IConversation {
  id: number
  date: Date
  modified: Date
  buyer_read_at: Date
  seller_read_at: Date
  buyer: number
  seller: number
  messages: IMessageFromServer[]
  updated_at: Date
}

export interface IConversationWithMessages extends IConversation {
  messages: IMessageFromServer[]
}

export interface IBid {
  auction_id: number
  ammount: number
  date?: Date
  id?: number
}

export interface IAdResponse {
  ad: IAd,
  users: UsersInfo[],
}
export interface IAdResponseMulti {
  ads: IAd[],
  users: UsersInfo[],
}

export interface IConversationResponse {
  conversation: IConversationWithMessages,
  users: UsersInfo[],
}

export interface IMessagesResponse {
  messages: IMessageFull[],
  users: UsersInfo[],
  ads: IAd[],
}



