import { ReviewR, ReviewT } from 'actions/review'
import { IReviewResponse } from 'api/interfaces'
import { parseDateNull } from 'utils/date'

export const initialReviewState: IReviewState = {
  isFetching: false,
  isPosting: false,
  reviews: {},
}

export interface IReviewState {
  isFetching: boolean
  isPosting: boolean
  reviews: {
    [n: number]: IReviewResponse,
  }
}

const parseReview = (review: IReviewResponse) => {
  return {
    ...review,
    date: parseDateNull(review.date),
    modified: parseDateNull(review.date),
  }
}

const reducer = (state: IReviewState = initialReviewState, action: ReviewT) => {
  switch (action.type) {
    case ReviewR.recieveReviews: {
      const { reviews } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        reviews:
          reviews.reduce(
            (r, thisReview) => {
              r[thisReview.id] = parseReview(thisReview)
              return r
            },
            { ...state.reviews },
          ),
      }
    }
    case ReviewR.recieveSingleReview: {
      const { review } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        reviews: {
          ...state.reviews,
          [review.id]: parseReview(review),
        },
      }
    }
    case ReviewR.getReviews:
    case ReviewR.getReviewsForAuction:
      return {
        ...state,
        isFetching: true,
      }
    case ReviewR.postReview:
      return {
        ...state,
        isPosting: true,
      }
    case ReviewR.resetIsFetching:
      return {
        ...state,
        isFetching: false,
      }
    case ReviewR.resetIsPosting:
      return {
        ...state,
        isPosting: false,
      }
    default:
      return state
  }
}

import { persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'
export default persistReducer(
  {
    key: 'review',
    storage,
    version: 2,
    blacklist: ['isFetching', 'isPosting'],
  },
  reducer)



