import { fetchBaseInfo } from 'api/base'
import { Category, IPromotion, MainCategory, Morph } from 'interfaces'
import { Dispatch } from 'react-redux'
import { errorCatcher } from './message'
import { recievePremiums } from './premium'

export enum BaseR {
  getBaseInfo = 'getBaseInfo',
  recieveBaseInfo = 'recieveBaseInfo',
  resetIsFetching = 'resetIsFetchingBase',
}

const _resetFetching = (): BaseT => {
  return {
    type: BaseR.resetIsFetching,
  }
}

export type BaseT =
  { type: BaseR.resetIsFetching } |
  { type: BaseR.getBaseInfo } |
  {
    type: BaseR.recieveBaseInfo,
    categories: Category[],
    mainCategories: MainCategory[],
    morphs: Morph[],
    promotions: IPromotion[],
  }

export const recieveBaseInfo = (
  categories: Category[],
  mainCategories: MainCategory[],
  morphs: Morph[],
  promotions: IPromotion[],
): BaseT => {
  return {
    type: BaseR.recieveBaseInfo,
    categories,
    mainCategories,
    morphs,
    promotions,
  }
}

export const _getBaseInfo = (): BaseT => {
  return {
    type: BaseR.getBaseInfo,
  }
}

export const getBaseInfo = () => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_getBaseInfo())
    fetchBaseInfo()
      .then(({ categories, main_categories, morphs, premiums, promotions }) => {
        dispatch(recieveBaseInfo(categories, main_categories, morphs, promotions))
        dispatch(recievePremiums(premiums))
      },
    )
      .catch(errorCatcher(dispatch, 'makeBid', _resetFetching))
  }
}



