import {
  fetchAd,
  fetchAds,
  postAd,
  postAdChangeStatus,
  postBid,
  postBuyNow,
} from 'api/base'
import { auctionStatusInfo, IAuctionStatus } from 'api/constants'
import { IAd, IAdNew, IBid, IReviewResponse } from 'api/interfaces'
import { Dispatch } from 'react-redux'
import { errorCatcher, newShortMessage } from './message'
import { recieveUsers } from './users'

export enum AdR {
  getAds = 'getAds',
  getSingleAd = 'getSingleAd',
  recieveAds = 'recieveAds',
  recieveSingleAd = 'recieveSingleAd',
  recieveSingleReview = 'recieveSingleReview',
  newAd = 'newAd',
  makeBid = 'makeBid',
  buyNow = 'buyNow',
  setStatus = 'setStatus',
  resetIsFetching = 'resetIsFetching',
}

export type AdT =
  { type: AdR.getAds } |
  { type: AdR.resetIsFetching } |
  { type: AdR.getSingleAd } |
  { type: AdR.recieveAds, ads: IAd[], append?: boolean } |
  { type: AdR.recieveSingleAd, ad: IAd } |
  { type: AdR.recieveSingleReview, review: IReviewResponse } |
  { type: AdR.newAd } |
  { type: AdR.setStatus } |
  { type: AdR.buyNow } |
  { type: AdR.makeBid }

export const recieveAds = (ads: IAd[], append?: boolean): AdT => {
  return {
    type: AdR.recieveAds,
    ads,
    append,
  }
}

export const recieveSingleAd = (ad: IAd): AdT => {
  return {
    type: AdR.recieveSingleAd,
    ad,
  }
}

export const recieveSingleReview = (review: IReviewResponse): AdT => {
  return {
    type: AdR.recieveSingleReview,
    review,
  }
}
const _newAd = (): AdT => {
  return {
    type: AdR.newAd,
  }
}
const _resetFetching = (): AdT => {
  return {
    type: AdR.resetIsFetching,
  }
}

const _getAds = (): AdT => {
  return {
    type: AdR.getAds,
  }
}

const _getSingleAd = (): AdT => {
  return {
    type: AdR.getSingleAd,
  }
}

const _makeBId = (): AdT => {
  return {
    type: AdR.makeBid,
  }
}

const _setStatus = (): AdT => {
  return {
    type: AdR.setStatus,
  }
}

const _buyNow = (): AdT => {
  return {
    type: AdR.buyNow,
  }
}

export const getAds = () => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_getAds())
    return fetchAds()
      .then(({ ads, users }) => {
        dispatch(recieveAds(ads))
        dispatch(recieveUsers(users))
        return ads
      },
    ).catch(errorCatcher(dispatch, 'getAds', _resetFetching))
  }
}

export const getAd = (id: number) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_getSingleAd())
    fetchAd(id)
      .then(({ ad, users }) => {
        dispatch(recieveSingleAd(ad))
        dispatch(recieveUsers(users))

      },
    )
      .catch(errorCatcher(dispatch, 'getAd', _resetFetching))
  }
}

export const newAd = (ad: IAdNew) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_newAd())
    return postAd(ad)
      .then(({ ad: postedAd, users }) => {
        dispatch(recieveUsers(users))
        dispatch(_resetFetching())
        dispatch(newShortMessage(`Anonnsen din ble lagt ut.`, 'newAd'))
        return dispatch(recieveSingleAd(postedAd))
      })
      .catch(errorCatcher(dispatch, 'newAd', _resetFetching))
  }
}

export const makeBid = (bid: IBid) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_makeBId())
    return postBid(bid)
      .then(({ ad: returnedAd, users }) => {
        dispatch(recieveSingleAd(returnedAd))
        dispatch(recieveUsers(users))
        dispatch(newShortMessage(`Ditt bud p ${bid.ammount} ble lagt inn.`, 'makeBid'))
      })
      .catch(errorCatcher(dispatch, 'makeBid', _resetFetching))
  }
}

export const buyNow = (auctionId: number) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_buyNow())
    return postBuyNow(auctionId)
      .then(({ ad: returnedAd, users }) => {
        dispatch(recieveSingleAd(returnedAd))
        dispatch(recieveUsers(users))
        dispatch(newShortMessage(`Kjpet var vellykket`, 'buyNow'))
      })
      .catch(errorCatcher(dispatch, 'buyNow', _resetFetching))
  }
}

export const setStatus = (auctionId: number, status: IAuctionStatus) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_setStatus())
    return postAdChangeStatus(auctionId, status)
      .then(({ ad: returnedAd, users }) => {
        dispatch(recieveSingleAd(returnedAd))
        dispatch(recieveUsers(users))
        dispatch(newShortMessage(`Status ble satt til ${auctionStatusInfo(returnedAd.status).text}`, 'setStatus'))
      })
      .catch(errorCatcher(dispatch, 'setStatus', _resetFetching))
  }
}



