import { initialProfileState, IProfileForm, ProfileFields } from 'forms/profile'
import { UserInfo } from 'interfaces'
import * as React from 'react'
import { actions, Form } from 'react-redux-form'
import { Link } from 'react-router-dom'
import { Button, Checkbox } from 'semantic-ui-react'
import { route } from 'utils/misc'

import BaseField from 'components/utils/BaseField'
import * as validators from 'utils/validators'
const debounce = require('lodash-es/debounce').default

interface IProps {
  onSubmitSuccess: () => any
}

class NewAdForm extends React.PureComponent<IConnectProps> {
  public primaryUploaderOpener: any = null
  public debounced = debounce(
    (e: any) => {
      window.localStorage.setItem('profileForm', JSON.stringify(e))
    },
    1000)

  public componentWillMount() {
    const { user, merge } = this.props
    if (user) {
      const { mobile, address } = user
      const { address1, address2, post_area, post_code } = address || initialProfileState
      merge({
        mobile: String(mobile),
        address1,
        address2,
        post_area,
        post_code,
        agreedTOS: false,
      })
    }
  }
  public handleSubmit = (profile: IProfileForm) => {
    this.props.submit(profile)
      .then((response: UserInfo) => {
        if (response.completed_profile) {
          this.props.resetForms()
          this.props.onSubmitSuccess()
        }
      })
  }

  public render() {
    const { valid, user } = this.props
    if (!user) {
      return
    }
    return (
      <Form
        className="ui form"
        model="profile"
        onSubmit={this.handleSubmit}
        hideNativeErrors={true}
        onChange={this.debounced}
      >
        <BaseField
          label="Telefonnummer"
          placeholder="12345678"
          type="number"
          model={ProfileFields.mobile}
          errors={{
            ...validators.phone(),
          }}
          required={true}
        />
        <BaseField
          label="Adresse"
          placeholder="Kongens gate 4"
          model={ProfileFields.address1}
          required={true}
          maxLength="200"
        />
        <BaseField
          placeholder=""
          model={ProfileFields.address2}
          required={false}
          maxLength="200"
        />
        <BaseField
          label="Postnummer"
          placeholder="1234."
          type="number"
          model={ProfileFields.post_code}
          required={true}
          maxLength={4}
          errors={{
            ...validators.zipCode(),
          }}
        />
        <BaseField
          label="Poststed"
          placeholder="1234."
          model={ProfileFields.post_area}
          required={true}
          maxLength={200}
        />
        <BaseField
          label="Jeg bekrefter  ha lest og godtatt betingelsene"
          typeComponent={Checkbox}
          model={ProfileFields.agreedTOS}
          required={true}
        />
        <p><Link to={route.tos()}>Du kan finne betingelsene her</Link></p>
        <Button
          fluid={true}
          disabled={!valid}
          type="submit"
          primary={true}
          onClick={() => null}
        >
          Send inn
        </Button>
      </Form >

    )
  }
}
import { sendProfile } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ forms, user, profile }: IStoreState) => {
    return {
      user: user.user,
      valid: forms.profile.$form.valid,
      profile,
      touched: forms.profile.$form.touched,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      submit: (profile: IProfileForm) =>
        dispatch(sendProfile(profile)),
      merge: (form: IProfileForm) =>
        dispatch(actions.merge('profile', form)),
      resetForms: () => {
        dispatch(actions.reset('profile'))
        dispatch(actions.reset('images'))
      },
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(NewAdForm)



