import FaceBookLogin from 'components/utils/FaceBookLogin'
import GoogleLogin from 'components/utils/GoogleLogin'
import { Loading } from 'components/utils/Icon'
import * as React from 'react'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { Message } from 'semantic-ui-react'
// TODO: Replace module react-google-login. Possibly with auth0.
// Current implemenation is not the best, as it throws a warning

declare global {
  interface Window { gapi: any }
}

class RequiresLogin extends React.Component<IConnectProps> {
  public render() {
    const { isFetching } = this.props
    return (
      <>
        {isFetching && (
          <Message
            info={true}
            icon={true}
          >
            <Loading active={true} />
            Sjekker innloggingen... vent litt...
          </Message>
        )}
        <div
          style={{
            justifyContent: 'center',
            display: 'flex',
            flexBasis: 0,
            flexGrow: 1,
            flexWrap: 'wrap',
          }}
        >
          <GoogleLogin
            onLogin={(token) => this.props.sendAuthToken(token, 'google')}
          >
            Logg inn med Google
          </GoogleLogin>
          <FaceBookLogin>
            Logg inn med Facebook
          </FaceBookLogin>
        </div>
      </>
    )
  }
}
import { sendAuthToken } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user }: IStoreState) => {
    return {
      isLoggedIn: !!user.user,
      isFetching: user.isFetching,
      hasCompletedProfile: user.user && user.user.completed_profile,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      sendAuthToken: (idToken: string, provider: string) => dispatch(sendAuthToken(idToken, provider)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default withRouter(connectCreator(RequiresLogin))



