import { IAd } from 'api/interfaces'
import { SortKeys } from 'interfaces'
import * as React from 'react'
import { Card, CardGroupProps } from 'semantic-ui-react'
import { sorter } from 'utils/sorting'
import AdCard from './AdCard'

interface IProps {
  ads: IAd[]
  sortBy: SortKeys
  maxEntries: number
  page: number
  sortOrder?: boolean
  sortFunc?: (a: any) => any
  groupProps?: CardGroupProps
}

export default ({ ads, sortBy, sortOrder, sortFunc, maxEntries, page, groupProps }: IProps) => {
  const offset = page * maxEntries
  const sortedAuctions = ads
    .sort(
      sorter(
        sortBy || SortKeys.currentPrice,
        sortOrder,
        sortFunc,
        (a: any, b: any) => b.date - a.date,
      ))
    .slice(offset, offset + maxEntries)
  return (
    <Card.Group centered={true} style={{ margin: 0 }} {...groupProps} >
      {sortedAuctions
        .map((ad) => {
          return (
            <AdCard key={ad.id} ad={ad} />
          )
        },
      )}</Card.Group>
  )
}



