import { IImage, Sex } from 'api/interfaces'

export enum AuctionFields {
  category = 'auction.category',
  currentPrice = 'auction.currentPrice',
  date_of_birth = 'auction.date_of_birth',
  interval = 'auction.interval',
  description = 'auction.description',
  images = 'auction.images',
  askingPrice = 'auction.askingPrice',
  buyNowPrice = 'auction.buy_now_price',
  minBidIncrease = 'auction.minBidIncrease',
  misconceptions = 'auction.misconceptions',
  morph = 'auction.morph',
  diet = 'auction.diet',
  sex = 'auction.sex',
  weight = 'auction.weight',
  agreedTOS = 'auction.agreedTOS',
  enableAuction = 'auction.enableAuction',
  enableBuyNow = 'auction.enableBuyNow',
  proven_breeder = 'auction.proven_breeder',
  ready_for_breeding = 'auction.ready_for_breeding',
  genetic_bomb = 'auction.genetic_bomb',
}

export interface IAuctionForm {
  category: string
  currentPrice: string
  date_of_birth: string | Date
  interval: number
  description: string
  images: IImage[]
  askingPrice: string
  buy_now_price: string
  minBidIncrease: 100
  misconceptions: string
  morph: string
  diet: string
  sex: Sex | ''
  weight: string
  agreedTOS: boolean,
  ready_for_breeding: boolean,
  genetic_bomb: boolean,
  proven_breeder: boolean,
  enableAuction: boolean,
  enableBuyNow: boolean,
}

export const initialAuctionState: IAuctionForm = {
  category: '',
  currentPrice: '',
  date_of_birth: '',
  interval: 7,
  description: '',
  images: [],
  askingPrice: '',
  buy_now_price: '',
  minBidIncrease: 100,
  misconceptions: '',
  morph: '',
  diet: '',
  sex: '',
  weight: '',
  agreedTOS: false,
  ready_for_breeding: false,
  proven_breeder: false,
  genetic_bomb: false,
  enableAuction: true,
  enableBuyNow: true,
}



