const parse = require('date-fns/parse')

export const phonePattern = /^((\+\d{2})|(\d{4}))?\d{8}$/
export const zipCodePattern = /^\d{4}$/
// tslint:disable-next-line
export const emailPattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

export const numberPositive = (value: number, _msg: string = 'Felt') => value > 0

export const required = (msg: string = 'Feltet er pkrevd. ') => ({
  _required: (value: any) => !value && msg,
})

export const minLength = (minL: number, msg: string = `Minst ${minL} tegn. `) =>
  ({ _minLength: (value: string) => value.length < minL && msg })

export const maxLength = (maxL: number, msg: string = `Maksimalt ${maxL} tegn. `) =>
  ({
    _maxLength: (value: string) => value.length > maxL && msg,
  })
export const phone = (msg: string = `Gyldig telefonnummer pkrevd. Eksempel: 004712345678 +4712345678 12345678`) =>
  ({
    _phone: (value: string) => !phonePattern.test(value) && msg,
  })

export const zipCode = (msg: string = `4 siffer `) =>
  ({
    _zipCode: (value: string) => !zipCodePattern.test(value) && msg,
  })
export const email = (msg: string = `M vre en gyldig epost-adresse`) =>
  ({
    _email: (value: string) => !emailPattern.test(value) && msg,
  })

export const max = (
  maxN: number,
  msg: string = `Maksimalt ${maxN}`,
) => ({
  _max: (value: number) => value > maxN && msg,
})

export const min = (
  minN: number,
  msg: string = `Minst ${minN}`,
) => ({
  _min: (value: number) => value < minN && msg,
})

export const minDate = (
  minD: Date,
  msg: string = `Datoen m vre tidligere enn ${minD.toLocaleString()}`,
) => ({
  _minDate: (value: string) => parse(value) < minD && msg,
})

export const maxDate = (
  maxD: Date,
  msg: string = `Datoen m vre senere enn ${maxD.toLocaleString()}`,
) => ({
  _maxDate: (value: string) => parse(value) > maxD && msg,
})



