import ProfileForm from 'components/Forms//ProfileForm'
import Icon from 'components/utils//Icon'
import Meta from 'components/utils//Meta'
import RequiresLogin from 'components/utils//RequiresLogin'
import DividerHeader from 'components/utils/DividerHeader'
import * as  React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container, Header, Message, Segment } from 'semantic-ui-react'
import { route } from 'utils/misc'

class NewAd extends React.PureComponent<IConnectProps> {
  public componentDidMount() {
    const { isFetching } = this.props
    if (!isFetching) {
      this.props.getUserInfo()
    }
  }
  public render() {
    return (
      <Container >
        <Meta
          title="Profilinformasjon"
          description="Skjema"
        />
        <DividerHeader
          as="h1"
          subheader="Informasjonen benyttes kun til dine kjp og salg."
        >
          Profilinformasjon
        </DividerHeader>
        <RequiresLogin
          bypassOnIncompleteProfile={true}
          renderOnNotLoggedIn={(
            <Header inverted={true}>
              Denne siden krever innlogging.
            </Header>
          )}
        >
          <Segment raised={true}>
            <Message info={true} icon={true}>
              <Icon icon="info" />
              <Message.Content>
                <Message.Header>Om profilinfomrasjonen</Message.Header>
                <p>Din profilinformasjon kreves for  kunne kjpe og selge.</p>
                <p>Informasjonen blir ikke delt med andre uten ditt samtykke.</p>
              </Message.Content>
            </Message>
            <ProfileForm onSubmitSuccess={() => this.props.history.push(route.auctionList())} />
          </Segment >
        </RequiresLogin>
      </Container >
    )
  }
}

import { getUserInfo } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user }: IStoreState) => {
    return {
      isFetching: user.isFetching,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getUserInfo: () => dispatch(getUserInfo()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(NewAd)



