import * as React from 'react'
import { Message } from 'semantic-ui-react'

class UserInfo extends React.Component<IConnectProps> {
  public render() {
    const user = this.props.user.user
    if (!user) {
      return <div>No user info</div>
    }
    const {family_name, given_name} = user
    return (
      <Message info={true}>
        <Message.Header>
          Velkommen, {given_name} {family_name}!
        </Message.Header>
        Du er logget inn
      </Message>
    )
  }
}
import { connect } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user }: IStoreState) => {
    return {
      user,
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export default connectCreator(UserInfo)



