import { SemanticCOLORS } from 'semantic-ui-react'

export enum VERBS {
  GET = 'GET',
  POST = 'POST',
  DELETE = 'DELETE',
  PATCH = 'PATCH',
  PUT = 'PUT',
}
export const api = process.env.REACT_APP_API_URL

export enum IAuctionStatus {
  active = 'active',
  pendingPayment = 'pendingPayment',
  talkingItOut = 'talkingItOut',
  inTransit = 'inTransit',
  ended = 'ended',
}
export const headers = {
  Accept: 'application/json',
}
export const postHeaders = {
  ...headers,
  'Content-Type': 'application/json',
}

export interface AuctionStatusInfo {
  text: string
  sortOrder: number
  icon: any
  color: SemanticCOLORS
  description: string
}

export const auctionStatusInfo = (status: IAuctionStatus): AuctionStatusInfo => {
  switch (status) {
    case IAuctionStatus.active:
      return {
        text: 'Aktiv',
        description: 'Aktive auksjoner',
        sortOrder: 5,
        icon: ['fas', 'gavel'],
        color: 'blue',
      }
    case IAuctionStatus.talkingItOut:
      return {
        text: 'kommunikasjon',
        description: 'Auksjon avsluttet, kommunikasjon startet',
        sortOrder: 1,
        icon: ['fas', 'comments'],
        color: 'green',
      }
    case IAuctionStatus.ended:
      return {
        text: 'Avsluttet',
        description: 'Avsluttet',
        sortOrder: 6,
        icon: ['fas', 'hands-helping'],
        color: 'grey',
      }
    case IAuctionStatus.inTransit:
      return {
        text: 'Under transport',
        description: 'Reptilen er under transport.',
        sortOrder: 3,
        icon: ['fas', 'truck-loading'],
        color: 'yellow',
      }
    case IAuctionStatus.pendingPayment:
      return {
        text: 'Venter p betaling',
        description: 'Venter p betaling',
        sortOrder: 2,
        icon: ['fas', 'money-bill'],
        color: 'yellow',
      }
    default:
      return {
        text: `Ukjent status: '${status}'`,
        description: '????',
        sortOrder: 7,
        icon: ['fas', 'question'],
        color: 'red',
      }
  }
}



