import { SettingsAT, SettingsR } from 'actions/settings'
import { IStoreSettings, SortKeys } from 'interfaces'

export const initialSettingsState: IStoreSettings = {
  sortKey: SortKeys.deadline,
  filterDeadline: true,
  filterSex: [],
  filterNegDeadline: true,
  filterNegCategories: false,
  filterCategories: [],
  filterMainCategories: [],
  filterNegMorphs: false,
  filterMorphs: [],
  filterEnded: false,
  filterMinPrice: 0,
  filterMaxPrice: Infinity,
  filterBreed: [],
  filterGeneticBomb: true,
  sortAsc: true,
  searchQuery: '',
  auctionPage: 0,
  auctionsPerPage: 12,
}

const reducer = (state: IStoreSettings = initialSettingsState, action: SettingsAT) => {
  switch (action.type) {
    case SettingsR.setSetting:
      const { key, value } = action
      return {
        ...state,
        auctionPage: 0,
        [key]: value,
      }
    case SettingsR.resetSettings:
      return initialSettingsState

    default:
      return state
  }
}

export default reducer



