/* tslint:disable:max-line-length*/
import DividerHeader from 'components/utils/DividerHeader'
import RequiresLogin from 'components/utils/RequiresLogin'
import * as React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Button, Confirm, Container, List, Message, Statistic } from 'semantic-ui-react'
import { route } from 'utils/misc'
const format = require('date-fns/format')
import Meta from 'components/utils//Meta'

interface IState {
  confirmOpen: boolean
}

class Member extends React.Component<IConnectProps, IState> {
  public state: IState = {
    confirmOpen: false,
  }
  public handleConfirmCancelSubscription = () => {
    this.props.sendCancelSubscription()
    this.setState({ confirmOpen: false })
  }
  public handleCancel = () => {
    this.setState({ confirmOpen: false })
  }
  public componentDidMount() {
    const { isFetching } = this.props
    if (!isFetching) {
      this.props.getUserInfo()
    }
  }

  public render() {
    if (!this.props.isLoggedIn) {
      return <RequiresLogin showLoginButton={true}>...</RequiresLogin>
    }
    if (!this.props.currentPremium || !this.props.premiumPackage) {
      return (
        <Message>
          <Meta
            title="Medlemsskap"
            description={'Medlemmside for Reptiles of Norway, reptilauksjon p nettet.'}
          />
          <Message.Header>
            Du fr mange fordeler av  vre medlem.
          </Message.Header>
          <Message.Content>
            <Button
              onClick={() => this.props.history.push(route.premium())}
              fluid={true}
              primary={true}
            >
              Med deg inn.
            </Button>
          </Message.Content>
        </Message>
      )
    }
    const { name, ammount, price, description } = this.props.currentPremium
    const { status, paid_through_date, cancel_at_period_end } = this.props.premiumPackage
    const { auctions_left } = this.props
    return (
      <Container>
        <Meta
          title="Medlemsskap"
          description={'Medlemmside for Reptiles of Norway, reptilauksjon p nettet.'}
        />
        <DividerHeader>Medlemsskap</DividerHeader>
        <RequiresLogin>
          <Statistic.Group size="small" inverted={true} style={{ display: 'flex', justifyContent: 'center', marginBottom: 40 }}>
            <Statistic color="teal" inverted={true} size="huge">
              <Statistic.Value>{auctions_left}/{ammount}</Statistic.Value>
              <Statistic.Label>Annonser igjen</Statistic.Label>
            </Statistic>
            <Statistic color="green" inverted={true}>
              <Statistic.Value>kr {Number(price)}</Statistic.Value>
              <Statistic.Label>per mned</Statistic.Label>
            </Statistic>
            <Statistic color="blue" inverted={true}>
              <Statistic.Value>{format(paid_through_date, 'D. MMM')}</Statistic.Value>
              <Statistic.Label>Betalt til</Statistic.Label>
            </Statistic>
            <Statistic color="red" inverted={true}>
              <Statistic.Value>{status}</Statistic.Value>
              <Statistic.Label>status {cancel_at_period_end && '(vil avsluttes)'}</Statistic.Label>
            </Statistic>
          </Statistic.Group>
          <Message info={true}>
            <Message.Header subheader={{ status }}>{name}</Message.Header>
            <List>
              <List.Item>
                {ammount} annonser i mneden.
              </List.Item>
              <List.Item>
                {Number(price)} kr per mned, fornyes automatisk
              </List.Item>
              <List.Item>
                Betalt frem til {format(paid_through_date, 'D. MMMM, YYYY')}.
              </List.Item>
            </List>
            <p>{description}</p>
          </Message>
          <Button
            onClick={() => this.props.history.push(route.premium())}
          >
            Endre medlemsskap
          </Button>
          <Button
            onClick={() => this.setState({ confirmOpen: true })}
            negative={true}
          >
            Avslutt medlemsskap
          </Button>
          <Confirm
            // Fixes bug introduced in Semantic-UI 2.3
            style={{ margin: 'auto', marginTop: 200 }}
            open={this.state.confirmOpen}
            cancelButton="Avbryt"
            content="Er du sikker p at du vil avbestille ditt abbonomant?"
            confirmButton="AVBESTILL"
            onCancel={this.handleCancel}
            onConfirm={this.handleConfirmCancelSubscription}
          />
        </RequiresLogin>
      </Container >
    )
  }
}
import { sendCancelSubscription } from 'actions/user'
import { getUserInfo } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, premium }: IStoreState) => {
    const { user: _user } = user
    const { premium: _premium } = premium
    let currentPremium
    let premiumPackage
    if (_user && _premium && _user.premium_package) {
      premiumPackage = _user.premium_package
      currentPremium = _premium[_user.premium_package.premium_id]
    }
    return {
      currentPremium,
      premiumPackage,
      isFetching: user.isFetching,
      isLoggedIn: !!user,
      auctions_left: user.user && user.user.auctions_left,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      sendCancelSubscription: () => dispatch(sendCancelSubscription()),
      getUserInfo: () => dispatch(getUserInfo()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(Member)



