(window as any).updateIsAvailable = (message: string) => {
  if (message === 'updateIsAvailable') {
    store.dispatch(newMessage({
      type: 'update',
      code: 'update',
      messages: [{
        defcon_level: DefconLevel.info,
        message: 'En oppdatering er tilgjengelig',
      }],
      id: 'update',
    }))
  }
}

import errorBoundaryWrapper from 'components/utils/ErrorBoundary'
import ScrollHelper from 'components/utils/ScrollHelper'
import * as React from 'react'
import * as ReactDOM from 'react-dom'
import { Provider } from 'react-redux'
import { BrowserRouter, Route } from 'react-router-dom'
import { PersistGate } from 'redux-persist/es/integration/react'
import { newMessage } from './actions/message'
import App from './App'
import './assets/semantic_dist/semantic.min.css'
import { DefconLevel } from './interfaces'
require('./style.css')

import registerServiceWorker from './registerServiceWorker'
import store, { persistor } from './store/'

require('./utils/fontAwesome')

const loading = () => (
  <div>
    App is loading.....................
  </div>
)

ReactDOM.render(
  <Provider store={store}>
    <PersistGate
      persistor={persistor}
      loading={loading()}
    >
      <BrowserRouter>
        <ScrollHelper>
          <Route path="/" component={errorBoundaryWrapper(App)} />
        </ScrollHelper>
      </BrowserRouter>
    </PersistGate>
  </Provider>,
  document.getElementById('root') as HTMLElement,
)
// import * as WebFont from 'webfontloader'
registerServiceWorker()
const WebFont = require('webfontloader')

//
WebFont.load({
  google: {
    families: [
      'Lato:400,700,400italic,700italic:latin',
      'Allura',
    ],
  },
})



