import { IMessage } from 'interfaces'
import { api, headers, VERBS } from './constants'
import { FetchOptions } from './interfaces'

export const myFetch = (url: string, options?: FetchOptions, stringifyBody: boolean = true) => {
  return new Promise((resolve, reject) => {
    let body = (options && options.body as any)
    if (body && stringifyBody) {
      body = JSON.stringify(body)
    }
    return fetch(`${api}${url}`, {
      method: VERBS.GET,
      headers,
      credentials: 'include',
      ...options,
      body,
    }).then((res) => {
      let r
      try {
        r = res.json()
      } catch {
        return reject(res)
      }
      if (res.status !== 200) {
        return r.then((json) => reject(json))
      }
      return resolve(r)
    }).catch((_e) => {
      return reject(
        {
          type: `fetch`,
          code: '700',
          id: 'fetch',
          messages: [{
            message: `Kunne ikke hente informasjon fra serveren. Kan det vre et problem med internetten? `,
            defcon_level: 2,
          }],
        } as IMessage,
      )
    })
  })
}
export const serialize = (obj: any) => {
  const str = []
  for (const p in obj) {
    if (obj.hasOwnProperty(p)) {
      const value = obj[p]
      if (value) {
        if (value) {
          str.push(encodeURIComponent(p) + '=' + value.join(','))
        } else {
          str.push(encodeURIComponent(p) + '=' + encodeURIComponent(value))
        }
      }
    }
  }
  return '?' + str.join('&')
}



