import * as React from 'react'
import MetaTags from 'react-meta-tags'
import { getImageUrl } from 'utils/misc'

interface IProps {
  title: string
  description: string
  url?: string
  image?: string
}
const siteName = 'Reptiles of Norway'

const Meta = ({ title, description, url: _url, image }: IProps) => {
  const url = (process as any).env.REACT_APP_DOMAIN + (_url ? _url : window.location.pathname)
  const imageUrl = image ? image : getImageUrl('reptile_logo')
  const titleSiteName = `${title}  ${siteName}`
  return (
    <MetaTags>
      <title>{titleSiteName}</title>
      <meta name="description" property="og:description" content={description} />
      <meta name="locale" property="og:locale" content="nb_NO" />
      <meta name="site_name" property="og:site_name" content={siteName}/>
      <meta name="title" property="og:title" content={titleSiteName}/>
      <meta name="url" property="og:url" content={url} />
      <meta name="type" property="og:type" content="website" />
      <meta name="image" property="og:image" content={imageUrl} />
      <meta name="fb_app_id" property="fb:app_id" content={process.env.REACT_APP_FB_APP_ID} />
    </MetaTags>
  )
}

export default Meta



