import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import { IAd } from 'api/interfaces'
import Icon from 'components/utils//Icon'
import ResponsiveImage from 'components/utils/ResponsiveImage'
import SocialSharing from 'components/widgets/SocialSharing'
import * as React from 'react'
import Dotdotdot from 'react-dotdotdot'
import { Link } from 'react-router-dom'
// import ScaleText from 'react-scale-text'
import Textfit from 'react-textfit'
// const Textfit = require('react-textfit')
import {
  Button, Card, Header, Icon as SEIcon, Label, Popup, PopupProps, Responsive, SemanticCOLORS,
} from 'semantic-ui-react'
import { prettyCurrency, route } from 'utils/misc'
import { getImageUrl, stripHTML } from 'utils/misc'
import { sexIcon } from 'utils/sexIcon'
import { adPropertyDescriber } from 'utils/txt'
import AuctionTimeLeft from './AuctionTimeLeft'

interface IProps {
  ad: IAd
  disableInput?: boolean
}

interface IState {
  bidConfirmOpen: boolean
}
interface IBreedProps {
  color: SemanticCOLORS,
  icon: any
  inverted?: boolean
  content: string
}
const getBreedProps = (proven: boolean, ready: boolean, geneticBomb: boolean): IBreedProps | undefined => {
  const geneticBombText = geneticBomb ? adPropertyDescriber.genetic_bomb().header : ''
  if (proven) {
    if (ready) {
      return { color: 'green', icon: 'trophy-alt', content: `Klar og bevist avler. ${geneticBombText}` }
    }
    return { color: 'olive', icon: 'star', content: `Bevist avler, men ikke klar. ${geneticBombText}` }
  }
  if (ready) {
    return { color: 'blue', icon: 'star-half', content: `Klar for avl ${geneticBombText}` }
  }
  return undefined
}
const inverted = true

class AdCard extends React.Component<IConnectProps & IProps, IState> {
  public state: IState = {
    bidConfirmOpen: false,
  }
  public Breeding = () => {
    const { ready_for_breeding, proven_breeder, genetic_bomb } = this.props.ad
    const breedProps = getBreedProps(proven_breeder, ready_for_breeding, genetic_bomb)
    if (!breedProps) {
      return null
    }
    const { content, icon, ...labelProps } = breedProps
    return (
      <Popup
        trigger={(
          <Label
            className="auction-corner-icon"
            corner="right"
            {...labelProps}
          >
            <SEIcon
              icon={icon}
            >
              <span className="fa-layers fa-fw">
                <FontAwesomeIcon
                  icon={icon}
                  color="white"
                  transform={`${genetic_bomb && 'left-5 up-5'}  grow-4`}
                />
                {genetic_bomb && (
                  <FontAwesomeIcon
                    icon="bolt"
                    color="yellow"
                    transform="right-5 down-5 grow-8 rotate-60"
                  />
                )}
              </span>
            </SEIcon>
          </Label>
        )}
        content={content}
        size="mini"
      />
    )
  }
  public Photo = () => {
    const { user: userId, highestBidderId, images, id } = this.props.ad
    const { users } = this.props
    const { user: currentUser } = this.props.user
    const userIsHighBidder = currentUser && highestBidderId && currentUser.id === highestBidderId
    const frontImage = images && images[0]
    const publicId = frontImage && frontImage.public_id
    const user = users[userId]
    const userIsSeller = currentUser && user && currentUser.id === userId
    return (
      <div className="photo">
        <Link
          {...this.props.disableInput && { onClick: (e) => e.preventDefault() }}
          to={route.auction(id)}
        >
          <ResponsiveImage
            grow={true}
            publicId={publicId || 'ad_404'}
            {...!publicId && {
              cloudinaryProps: {
                crop: 'pad',
                aspectRatio:
                  process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET_PRIMARY_CROPPING_ASPECT_RATIO,
                background: 'black',
              },
            }}
          />
          {(userIsHighBidder) && (
            <Popup
              trigger={(
                <Label
                  className="auction-corner-icon"
                  corner="left"
                  color="green"
                >
                  <Icon icon="badge-check" />
                </Label>
              )}
            >
              Du leder auksjonen
            </Popup>
          )}
          {(userIsSeller) && (
            <Popup
              trigger={(
                <Label
                  className="auction-corner-icon"
                  corner="left"
                  color="grey"
                >
                  <Icon icon="user" />
                </Label>
              )}
            >
              Dette er din egen auksjon
            </Popup>
          )}
        </Link>
      </div>
    )
  }
  public CardHeader = () => {
    const { sex, id } = this.props.ad
    const { morph, categoryName } = this.props
    return (
      <div className="card-header">
        <Header
          textAlign="center"
          className={inverted ? 'lightText' : ''}
        >
          <Link to={route.auction(id)} style={{ color: 'inherit' }}>
            <Dotdotdot clamp={1}>
              <Icon icon={sexIcon(sex)} /> {categoryName}
            </Dotdotdot>
          </Link>
        </Header>
        <Card.Meta
          className={inverted ? 'lightText' : ''}
          style={{ marginBottom: 10 }}
        >
          <div style={{ textAlign: 'center' }}>
            <Dotdotdot clamp={1}>
              {morph && morph.title}
            </Dotdotdot>
          </div>
        </Card.Meta>
      </div>)
  }
  public PopupMobile = ({ popupContent, trigger, ...props }: PopupProps & { popupContent: any}) => {
    return (
      <>
        <Responsive
          minWidth={Responsive.onlyComputer.minWidth}
          as={Popup}
          trigger={trigger}
          {...props}
          children={popupContent}
        />
        <Responsive
          maxWidth={Responsive.onlyTablet.maxWidth}
          className="pop-mobile"
        >
          {trigger}
        </Responsive>
      </>
    )
  }
  public CardButtons = () => {
    const { id, askingPrice, minBid, buy_now_price } = this.props.ad
    const { PopupMobile } = this
    return (
      <Link to={route.auction(id)}>
        <Button.Group
          fluid={true}
          style={{ flexWrap: 'wrap' }}
        >
          {askingPrice && (
            <PopupMobile
              popupContent="Budpris"
              trigger={(
                <Button
                  color="teal"
                  className="buyCardButton"
                >
                  <Icon icon="gavel" />
                  {prettyCurrency(minBid)}
                </Button>
              )}
            />
          )}

          {buy_now_price && (
            <PopupMobile
              popupContent="Kjp n"
              trigger={(
                <Button
                  color="blue"
                  className="buyCardButton"
                >
                  <Icon icon="money-bill" />
                  {prettyCurrency(buy_now_price)}
                </Button>
              )}
            />
          )}
        </Button.Group>
      </Link>
    )
  }
  public ShareText = () => {
    const { description_html, images, id } = this.props.ad
    const { categoryName } = this.props
    const frontImagePublicId = images && !!images.length && images[0].public_id
    const meta = {
      title: categoryName,
      description: stripHTML(description_html),
      image: frontImagePublicId ? getImageUrl(frontImagePublicId) : '',
      url: process.env.REACT_APP_DOMAIN + route.auction(id),
    }
    return (
      <div className="flex-center-row share-text" style={{ width: '100%' }}>
        <div className="handwriting" style={{ width: '100%' }}>
          <Textfit mode="multi">
            Share your favourite reptile
          </Textfit>
        </div>
        <div>
          <SocialSharing
            meta={meta}
          />
        </div>
        <div className="timeleft">
          <AuctionTimeLeft
            as="pure"
            deadline={this.props.ad.deadline}
          />
        </div>
      </div>
    )
  }
  public showBidConfirmation = () => this.setState({ bidConfirmOpen: true })
  public handleConfirmBid = () => this.setState({ bidConfirmOpen: false })
  public handleCancelBid = () => this.setState({ bidConfirmOpen: false })
  public render() {
    const { Photo, CardHeader, CardButtons, ShareText, Breeding } = this
    const promoted = false
    return (
      <Card
        raised={true}
        className="auction-card inverted-card"
        style={{
          width: 'unset',
          minWidth: 190,
          margin: 5,
          padding: 0,
          flex: '1 1 30%',
          order: 1,
          ...promoted ? {
            minWidth: '90%',
            order: 0,
          } : {
              maxWidth: 290,
            },
        }}
      >
        <CardHeader />
        <Card.Content className="p-0 pt-2">
          <Breeding />
          <Photo />
          <ShareText />
        </Card.Content>
        <Card.Content extra={true} style={{ padding: 0 }}>
          <CardButtons />
        </Card.Content>
      </Card >
    )
  }
}
import { connect } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ base, user, users }: IStoreState, { ad }: IProps) => {
    const thisCategory = base.categories && base.categories[Number(ad.category)]
    const categoryName = thisCategory ? thisCategory.title : ''
    const morph = base.morphs && base.morphs[Number(ad.morph)]
    return {
      categoryName,
      thisCategory,
      morph,
      users,
      user,
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export default connectCreator(AdCard)



