import { UsersR, UsersT } from 'actions/users'
import { UsersInfo } from 'interfaces'
import { reduce } from 'utils/reduce'

export const initialUserState = {
}

export interface IUsersState {
  [s: number]: UsersInfo
}

export default (state: IUsersState = initialUserState, action: UsersT) => {
  switch (action.type) {
    case UsersR.recieveUsers:
      const { users } = action
      return {
        ...state,
        ...reduce(users),
      }

    default:
      return state
  }
}



