import { Sex } from 'api/interfaces'
import { SortKeys } from './sorting'

export interface Category {
  id: number
  title: string
  main_category_id: number
  min_price: number
  recommended_price: number
}

export interface MainCategory {
  id: number
  title: string
  min_price: number
  recommended_price: number
}
export interface Morph {
  id: number
  title: string
}

export interface PremiumPackage {
  paid_through_date: string
  premium_id: number,
  status: string
  cancel_at_period_end: boolean
}
export interface UsersInfo {
  email: string
  family_name: string
  average_rating: number[]
  given_name: string
  picture_url?: string
  bid_auctions: number[]
  id: number
  verified: boolean
  date: Date
}

export interface IAddress {
  address1: string
  address2: string
  post_code: number
  post_area: string
}

export interface UserInfo extends UsersInfo {
  auctions_left: number
  completed_profile: false
  premium_package?: PremiumPackage
  mobile: string
  address: IAddress
}
export interface IPremium {
  ammount: number
  description: string
  id: number
  name: string
  price: number
  path: string
  recurring: boolean
}

export enum DefconLevel {
  danger = 1,
  warning = 2,
  info = 3,
  success = 4,
  default = 5,
}

export interface IMessageItem {
  defcon_level: DefconLevel
  message: string
  validation_errors?: {
    [s: string]: string[],
  }
}
export interface IMessage {
  type: string
  code: string
  messages: IMessageItem[]
  id: string
}

export enum Breeder {
  proven = 'proven',
  ready = 'ready',
  ready_and_proven = 'ready_and_proven',
}

export interface IStoreSettings {
  sortKey: SortKeys
  searchQuery: string
  sortAsc: boolean
  filterCategories: number[]
  filterMainCategories: number[]
  filterNegCategories: boolean
  filterMorphs: number[]
  filterNegMorphs: boolean
  filterDeadline: boolean
  filterNegDeadline: boolean
  filterEnded: boolean
  filterMinPrice: number
  filterMaxPrice: number
  filterSex: Sex[]
  auctionPage: number
  auctionsPerPage: number
  filterBreed: Breeder[]
  filterGeneticBomb: boolean
}

export interface IPromotion {
  public_image_id: string
  header: string
  description?: string
  short_description: string
  id: number
  href: string
}



