import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import { IAd } from 'api/interfaces'
import * as React from 'react'
import { List } from 'semantic-ui-react'
import { prettyDate } from 'utils/date'

interface IProps {
  ad: IAd
  morph: string
}

export interface IAdStats {
  title: any
  iconName: any
  detail: any
}

export default ({ ad }: IProps) => {
  const { weight, date_of_birth, date } = ad
  const items: IAdStats[] = [
    {
      iconName: ['fas', 'tachometer-alt'],
      title: 'Vekt',
      detail: `${weight} g`,
    },
    {
      iconName: ['fas', 'birthday-cake'],
      title: 'Fdt',
      detail: date_of_birth && new Date(date_of_birth).toLocaleDateString(),
    },
    {
      iconName: ['fas', 'calendar-alt'],
      title: 'Lagt ut',
      detail: prettyDate(date),
    },
  ]
  return (
    <List
      horizontal={true}
      style={{
        justifyContent: 'space-evenly',
        display: 'flex',
        textAlign: 'center',
        flexWrap: 'wrap',
      }}
    >
      {items
        .filter(({ detail }) => detail)
        .map(({ title, detail, iconName }) => (
          <List.Item
            key={title}
            style={{
              margin: 0,
              flexGrow: 1,
              flexBasis: 0,
            }}
          >
            <FontAwesomeIcon icon={iconName} />
            <List.Content>
              <List.Header>{title}</List.Header>
              <List.Description>{detail}</List.Description>
            </List.Content>
          </List.Item>
        ))}
    </List>
  )
}



