import { BaseR, BaseT } from 'actions/baseInfo'
import { Category, IPromotion, MainCategory, Morph } from 'interfaces'
import { reduce } from 'utils/reduce'
export const initialCategoryState: IBaseState = {
  isFetching: false,
  loaded: false,
  categories: {},
  mainCategories: {},
  morphs: {},
  promotions: {},
}

export interface IBaseState {
  isFetching: boolean
  loaded: boolean
  categories: {
    [s: number]: Category,
  }
  mainCategories: {
    [s: number]: MainCategory,
  }
  morphs: {
    [s: number]: Morph,
  }
  promotions: {
    [s: number]: IPromotion,
  }
}

const reducer = (state: IBaseState = initialCategoryState, action: BaseT) => {
  switch (action.type) {
    case BaseR.recieveBaseInfo:
      const { categories, mainCategories, morphs, promotions } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        categories: reduce(categories),
        mainCategories: reduce(mainCategories),
        morphs: reduce(morphs),
        promotions: reduce(promotions),
      }
    case BaseR.resetIsFetching:
      return {
        ...state,
        isFetching: false,
      }

    default:
      return state
  }
}

import { persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'
export default persistReducer(
  {
    key: 'base',
    storage,
    blacklist: ['isFetching'],
  },
  reducer)



