import { Sex } from 'api/interfaces'
import Icon, { Loading } from 'components/utils/Icon'
import { FilterKeys, SortKeys } from 'interfaces'
import { Breeder } from 'interfaces/store'
import 'rc-slider/assets/index.css'
import * as React from 'react'
import {
  Button, Dropdown, Header, Input, Menu,
} from 'semantic-ui-react'
import { isEmpty } from 'utils/misc'
import { filterItems, getSortItems } from 'utils/sorting'
import { breedString, sexString } from 'utils/txt'

interface IState {
  value: number[]
  searchQuery: string
}

interface IProps {
  inverted?: boolean
}

class AuctionSort extends React.Component<IConnectProps, IState> {
  public state = {
    value: [],
    searchQuery: '',
  }
  public handleSortClick = (key: SortKeys, defaultAsc: boolean) => {
    if (this.props.settings.sortKey === key) {
      this.props.setSetting('sortAsc', !this.props.settings.sortAsc)
    } else {
      this.props.setSetting('sortKey', key)
      this.props.setSetting('sortAsc', defaultAsc)
    }
  }
  public handleFilterChange = (_e: any, { name, checked }: any) => {
    this.props.setSetting(name, checked)
  }
  public handleChange = (_e: any, { name, value }: any) => {
    this.props.setSetting(name, value)
  }

  public SortMenu = () => {
    const { sortKey } = this.props.settings
    const { categories, morphs, mainCategories } = this.props
    const sortItems = getSortItems({ categories, morphs, mainCategories })
    return (
      <>
        {Object.keys(sortItems)
          .map((key) => {
            const { icon, defaultAsc, sortFunc: _sortFunc, ...rest } = sortItems[key]
            return (
              <Dropdown.Item
                key={key}
                icon={<Icon icon={icon} {...sortKey === key && { color: 'green' }} />}
                {...rest}
                onClick={
                  () => this.handleSortClick(key as SortKeys, defaultAsc)
                }
              />
            )
          },
        )}
      </>
    )
  }

  public Search = () => {
    const { searchQuery } = this.props.settings
    return (
      <Input
        icon={<Icon
          className="no-margin"
          icon={searchQuery ? 'times' : 'search'}
          inverted={true}
          circular={true}
          link={true}
          onClick={() => {
            searchQuery && this.props.resetSetting()
          }}
        />}
        placeholder="Sk..."
        value={searchQuery}
        onChange={(e) => this.props.setSetting('searchQuery', e.currentTarget.value)}
      />
    )
  }

  public MainCategories = () => {
    const { mainCategories, setSetting, adMainCategories, categories, adCategories } = this.props
    const { filterCategories, filterMainCategories } = this.props.settings
    return (
      <>
        {Object.keys(adMainCategories).map(
          (key) => {
            const mainCategorySelected = filterMainCategories.indexOf(Number(key)) !== -1
            const { title, id } = mainCategories[Number(key)]
            return (
              <Dropdown
                key={id}
                item={true}
                style={{
                  minWidth: 140,
                }}
                trigger={(
                  <span
                    {...mainCategorySelected && {
                      style: {
                        fontWeight: 'bold',
                      },
                    }
                    }
                  >
                    {title} ({adMainCategories[key]}){(mainCategorySelected && !!filterCategories.length ? '*' : '')}
                  </span>
                )}
              >
                <Dropdown.Menu>
                  <Dropdown.Item
                    onClick={(_e) => {
                      setSetting(FilterKeys.filterMainCategories, [id])
                      setSetting(FilterKeys.filterCategories, [])
                    }}
                  >
                    Alle
                  </Dropdown.Item>
                  {Object.keys(adCategories)
                    .map((k) => categories[k])
                    .filter(
                      ({ main_category_id }) => main_category_id === id)
                    .map(
                      ({ id: idx, title: titlex }) => {
                        return (
                          <Dropdown.Item
                            key={idx}
                            active={filterCategories && filterCategories.indexOf(idx) !== -1}
                            onClick={(_e) => {
                              setSetting(FilterKeys.filterCategories, [idx])
                              setSetting(FilterKeys.filterMainCategories, [id])
                            }}
                          >
                            {titlex} ({adCategories[idx]})
                          </Dropdown.Item>
                        )
                      })}
                </Dropdown.Menu>
              </Dropdown>
            )
          })}
      </>
    )
  }

  public EnumDropDown = ({ filterKey, enumd, stringObj }: {
    filterKey: FilterKeys, enumd: any, stringObj: any,
  },
  ) => {
    return (
      <Dropdown
        item={true}
        trigger={(
          <span
            {...(this.props.settings[filterKey] as any[]).length && {
              style: {
                fontWeight: 'bold',
              },
            }
            }
          >
            {filterItems[filterKey].text}
          </span>
        )}
      >
        <Dropdown.Menu>
          <Dropdown.Item
            onClick={(_e) =>
              this.props.setSetting(filterKey, [])
            }
          >
            Alle
          </Dropdown.Item>
          {Object.keys(enumd)
            .map(
              (breed) => {
                return (
                  <Dropdown.Item
                    key={breed}
                    onClick={(_e) =>
                      this.props.setSetting(filterKey, [enumd[breed]])
                    }
                  >
                    {stringObj(enumd[breed])}
                  </Dropdown.Item>
                )
              },
          )
          }
        </Dropdown.Menu>
      </Dropdown>
    )
  }

  public shouldComponentUpdate() {
    return this.props.isLoaded
  }

  public render() {
    if (!this.props.isLoaded) {
      return <Loading active={true} />
    }
    const {
      auctionsPerPage, sortAsc,
      filterMainCategories, sortKey,
    } = this.props.settings
    const { SortMenu, MainCategories, Search, EnumDropDown } = this
    const {
      mainCategories, morphs, categories, inverted, setSetting, adMainCategories, filtersActive,
    } = this.props
    const sortItems = getSortItems({ categories, morphs, mainCategories })
    return (
      <>
        <Menu
          className="flex flex-center drop-sorter"
          inverted={inverted}
          stackable={true}
          style={{ margin: 0 }}
        >
          {/* Flatten if there are only a single item in the dropdown-menu */}
          {Object.keys(adMainCategories).length <= 1
            ? <MainCategories />
            : (
              <Menu.Menu>
                <Dropdown
                  text={(mainCategories && filterMainCategories.length)
                    ? mainCategories[filterMainCategories[0]].title
                    : 'Kategori'}
                  item={true}
                >
                  <Dropdown.Menu>
                    <Dropdown.Item
                      onClick={(_e) => {
                        setSetting(FilterKeys.filterCategories, [])
                        setSetting(FilterKeys.filterMainCategories, [])
                      }}
                    >
                      Alle
                    </Dropdown.Item>
                    <MainCategories />
                  </Dropdown.Menu>
                </Dropdown>
              </Menu.Menu>
            )}
          <Menu.Menu>
            <Dropdown
              text={`Filtrering ${
                filtersActive ? '*' : ''
                }`}
              item={true}
            >
              <Dropdown.Menu>
                {filtersActive && (
                  <Dropdown.Item
                    onClick={() => {
                      setSetting(FilterKeys.filterSex, [])
                      setSetting(FilterKeys.filterBreed, [])
                    }
                    }
                  >
                    Reset alle
                  </Dropdown.Item>
                )}
                <EnumDropDown
                  filterKey={FilterKeys.filterBreed}
                  enumd={Breeder}
                  stringObj={breedString}
                />
                <EnumDropDown
                  filterKey={FilterKeys.filterSex}
                  enumd={Sex}
                  stringObj={sexString}
                />
              </Dropdown.Menu>
            </Dropdown>
          </Menu.Menu>
          <Menu.Menu>
            <Dropdown
              item={true}
              trigger={
                <>
                  <span className="lightDisabledText">
                    Sortert etter: </span>
                  <Icon icon={sortItems[sortKey].icon} /> {sortItems[sortKey].text} </>
              }
              icon={(
                <>
                  <Icon icon={sortAsc ? 'sort-amount-up' : 'sort-amount-down'} className="ml-2" />
                  <Icon icon="caret-down" className="no-margin" />
                </>
              )}
            >
              <Dropdown.Menu>
                <SortMenu />
                <Header sub={true} textAlign="center">Auksjoner per side</Header>
                <Button.Group size="tiny" fluid={true} compact={true}>
                  {[12, 24, 48, 96].map((i) => (
                    <Button
                      key={i}
                      active={auctionsPerPage === i}
                      onClick={() => this.props.setSetting('auctionsPerPage', i)}
                    >
                      {i}
                    </Button>
                  ))}
                </Button.Group>
              </Dropdown.Menu>
            </Dropdown>
          </Menu.Menu>
          <Menu.Item>
            <Search />
          </Menu.Item>
        </Menu>
      </ >
    )
  }
}
interface IAdBase {
  adCategories: { [n: number]: number },
  adMainCategories: { [n: number]: number },
}

const adBaseInitial = (): IAdBase => ({
  adCategories: {},
  adMainCategories: {},
})
import { resetSettings, setSetting as _setSetting } from 'actions/settings'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ base, settings, ads }: IStoreState) => {
    const filtersActive = !!settings.filterSex.length ||
      !!settings.filterBreed.length
    let areEmpty = false
    for (const obj of [base.categories, ads.ads]) {
      if (!obj || isEmpty(obj)) {
        areEmpty = true
        break
      }
    }
    const adBase = (!areEmpty) ? Object.keys(ads.ads)
      .reduce(
        (r, key) => {
          const ad = ads.ads[Number(key)]
          const category = base.categories[ad.category]
          if (r.adCategories[ad.category]) {
            r.adCategories[ad.category] += 1
          } else {
            r.adCategories[ad.category] = 1
          }
          if (r.adMainCategories[category.main_category_id]) {
            r.adMainCategories[category.main_category_id] += 1
          } else {
            r.adMainCategories[category.main_category_id] = 1
          }
          return r
        },
        adBaseInitial(),
    ) : adBaseInitial()
    return {
      filtersActive,
      isLoaded: base.loaded && ads.loaded,
      categories: base.categories,
      mainCategories: base.mainCategories,
      morphs: base.morphs,
      settings,
      adCategories: adBase.adCategories,
      adMainCategories: adBase.adMainCategories,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      setSetting: (key: string, value: any) =>
        dispatch(_setSetting(key, value)),
      resetSetting: () =>
        dispatch(resetSettings()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(AuctionSort)



