import AppRoutes from 'AppRoutes'
import ContentLoader from 'components/utils/ContentLoader'
import Messages from 'components/utils/Messages'
import Footer from 'components/widgets/Footer'
import Hamburger from 'components/widgets/Hamburger'
import Nav from 'containers/Nav'
import * as React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { route } from 'utils/misc'
interface IProps {
  children: any
}

class AppC extends React.Component<IConnectProps> {
  public hamburger: Hamburger | null = null
  public componentDidMount() {
    const { userLoaded } = this.props
    const loginScreen = this.props.location.pathname === route.login()
    console.log(loginScreen, this.props.location.pathname)
    if (!loginScreen) {

      this.props.getCategories()
      if (!userLoaded) {
        console.log('getting userinfo')
        this.props.getUserInfo()
      }
    }
  }
  public shouldComponentUpdate({ location }: IConnectProps) {
    if (location.pathname !== this.props.location.pathname) {
      return true
    }
    return false
  }
  public onRouteChanged() {
    this.hamburger && this.hamburger.setState({ visible: false })
  }
  public render() {
    return (
      <div className="App">
        <ContentLoader />
        <Messages />
        <Nav {...this.props} />
        <AppRoutes />
        <Footer />
      </div >
    )
  }
}
import { getBaseInfo } from 'actions/baseInfo'
import { getUserInfo } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user }: IStoreState) => {
    return {
      userLoaded: !!user.user,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getUserInfo: () => dispatch(getUserInfo()),
      getCategories: () => dispatch(getBaseInfo()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps & RouteComponentProps<any>
export default connectCreator(AppC)



