import { UsersInfo } from 'interfaces'

export enum UsersR {
  recieveUsers = 'recieveUsers',
  resetIsFetching = 'resetIsFetching',
}
export type UsersT =
  { type: UsersR.resetIsFetching } |
  { type: UsersR.recieveUsers, users: UsersInfo[] }

export const recieveUsers = (users: UsersInfo[]): UsersT => {
  return {
    type: UsersR.recieveUsers,
    users,
  }
}



