import { IAd } from 'api/interfaces'
import AdStats from 'components/Auction/AdStats'
// import AuctionEnded from 'components/Auction/AuctionEnded'
import BidStats from 'components/Auction/BidStats'
import BaseField from 'components/utils/BaseField'
import DividerHeader from 'components/utils/DividerHeader'
import Icon, { Loading } from 'components/utils/Icon'
import Meta from 'components/utils/Meta'
import RequiresLogin from 'components/utils/RequiresLogin'
import ResponsiveImage from 'components/utils/ResponsiveImage'
// import ImageSlideShow from 'components/widgets/ImageSlideShow'
import Rating from 'components/widgets/Rating'
import SocialSharing from 'components/widgets/SocialSharing'
import TinyUserInfo from 'components/widgets/TinyUserInfo'
import * as  React from 'react'
import * as Loadable from 'react-loadable'
import {
  Button, Confirm, Divider, Grid, Header, Image,
  List, ListItemProps, Message, Modal, Popup, Responsive, Segment,
} from 'semantic-ui-react'
const isPast = require('date-fns/is_past')
import { LocalForm } from 'react-redux-form'
import { getImageUrl, stripHTML } from 'utils/misc'
import { sexIcon } from 'utils/sexIcon'
import { adPropertyDescriber } from 'utils/txt'
import { max, min } from 'utils/validators'

const ImageSlideShow = Loadable({
  loader: () => import('components/widgets/ImageSlideShow'),
  loading: Loading,
})
const AuctionEnded = Loadable({
  loader: () => import('components/Auction/AuctionEnded'),
  loading: Loading,
})

interface IProps {
  ad: IAd
  disableInput?: boolean
}

interface IState {
  userBid: number
  validBidForm: boolean
  bidConfirmationOpen: boolean
  buyNowConfirmationOpen: boolean
  imageModalOpen: boolean
  slideIndex: number
}

class AdPage extends React.PureComponent<IConnectProps, IState> {
  constructor(props: IConnectProps) {
    super(props)
    this.state = {
      imageModalOpen: false,
      slideIndex: 0,
      userBid: props.ad.minBid,
      validBidForm: false,
      bidConfirmationOpen: false,
      buyNowConfirmationOpen: false,
    }
  }
  public componentWillReceiveProps(props: IProps) {
    const minBid = props.ad.minBid
    if (minBid > this.state.userBid) {
      this.setState({ userBid: minBid })
    }

  }
  public handleChange = (_e: any, { name, value }: any) => {
    if (this.shouldDisableInput()) {
      return
    }
    this.setState({ [name]: value })
  }
  public shouldDisableInput = () => {
    if (!this.props.ad.active || this.props.disableInput) {
      return true
    }
    return false
  }
  public onConfirmBid = () => {
    if (this.shouldDisableInput()) {
      return
    }
    this.props.makeBid({
      auction_id: this.props.ad.id,
      ammount: this.state.userBid,
    }).then(this.hideBidConfirmation)
  }
  public onConfirmBuyNow = () => {
    if (this.shouldDisableInput()) {
      return
    }
    this.props.buyNow(this.props.ad.id)
      .then(this.hideBuyNowConfirmation)
  }
  public showBidConfirmation = () =>
    this.setState({ bidConfirmationOpen: true })

  public hideBidConfirmation = () =>
    this.setState({ bidConfirmationOpen: false })

  public showBuyNowConfirmation = () =>
    this.setState({ buyNowConfirmationOpen: true })

  public hideBuyNowConfirmation = () =>
    this.setState({ buyNowConfirmationOpen: false })

  public RenderBuyActions = () => {
    const { userBid } = this.state
    const { ad } = this.props
    const {
      askingPrice,
      buy_now_price,
      active,
      deadline,
      user,
    } = ad
    if (!active || isPast(deadline) || (this.props.user && user === this.props.user.id)) {
      return null
    }
    const bidHigherThanBuyNow = userBid > buy_now_price!
    return (
      <RequiresLogin
        style={{ display: 'flex', margin: '0 auto' }}
        showLoginButton={true}
        renderOnNotLoggedIn={(
          <DividerHeader
            as="h4"
            inverted={true}
            subheader="Registrer eller logg inn."
          >
            nsker du  kjpe/gi bud?
          </DividerHeader>
        )}
      >
        {(active && (askingPrice || buy_now_price)) && (
          <>
            <Divider hidden={true} />
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              {askingPrice && (
                <Button
                  onClick={this.showBidConfirmation}
                  disabled={bidHigherThanBuyNow}
                  label={{ basic: true, color: 'green', pointing: 'right', content: Number(userBid) + ' kr' }}
                  labelPosition="left"
                  content="Gi bud"
                  color="green"
                />
              )}
              {buy_now_price && (
                <Button
                  onClick={this.showBuyNowConfirmation}
                  label={{ basic: true, color: 'blue', pointing: 'left', content: Number(buy_now_price) + ' kr' }}
                  labelPosition="right"
                  content="Kjp n"
                  color="blue"
                />
              )}
            </div>
          </>
        )}
      </RequiresLogin >
    )
  }

  public RenderImages = () => {
    const { images } = this.props.ad
    return (
      <div
        style={{
          display: 'flex',
          flexWrap: 'wrap',
          justifyContent: 'center',
        }}
      >
        {images.map(({ public_id }, i) => {
          return (
            <ResponsiveImage
              publicId={public_id}
              key={public_id}
              {...i !== 0 && {
                cloudinaryProps: {
                  aspectRatio: 3 / 2,
                  crop: 'fit',
                },
                style: {
                  display: 'flex',
                  justifyContent: 'center',
                },
              }}
              semanticProps={{
                onClick: () => this.setState({ imageModalOpen: true, slideIndex: i }),
                bordered: true,
                fluid: true,
                alt: 'Bilde',
                ...i > 0 && {
                  fluid: false,
                  size: images.length > 3 ? 'tiny' : 'small',
                },
              }}
            />
          )
        },
        )}
      </div>
    )
  }

  public AuctionOwner = () => {
    const { auctionOwner } = this.props
    if (!auctionOwner) {
      return null
    }
    const { given_name, family_name, picture_url, average_rating, verified } = auctionOwner
    const [rating, rateCount] = average_rating
    return (
      <Header as="h4" inverted={true}>
        <Image
          circular={false}
        >
          <img src={picture_url} alt="" />
        </Image>
        <Popup
          position="top left"
          trigger={(
            <Header.Content>
              {verified && <Icon icon="shield-check" color="green" />}{given_name} {family_name}
              {!!rateCount && (
                <Header.Subheader>
                  <Rating rating={rating} /> ({rateCount})
                </Header.Subheader>
              )}
            </Header.Content>
          )}
        >
          <TinyUserInfo user={auctionOwner} />
        </Popup>
      </Header >
    )
  }

  public render() {
    const { RenderBuyActions, RenderImages, AuctionOwner } = this
    const { userBid, bidConfirmationOpen, buyNowConfirmationOpen, validBidForm, imageModalOpen, slideIndex,
    } = this.state
    const { ad, currentMorph, categoryName, highestBidder } = this.props
    const {
      askingPrice,
      buy_now_price,
      description_html,
      misconceptions_html,
      sex,
      ready_for_breeding,
      proven_breeder,
      genetic_bomb,
      diet,
      images,
      active,
      minBid,
    } = ad

    const extras: ListItemProps[] = []
    if (proven_breeder) {
      extras.push(adPropertyDescriber.proven_breeder())
    }
    if (genetic_bomb) {
      extras.push(adPropertyDescriber.genetic_bomb())
    }
    if (ready_for_breeding) {
      extras.push(adPropertyDescriber.ready_for_breeding())
    }
    if (diet) {
      extras.push(adPropertyDescriber.diet(diet))
    }
    const frontImagePublicId = images[0].public_id
    return (
      <>
        <Meta
          image={getImageUrl(frontImagePublicId)}
          title={categoryName}
          description={stripHTML(description_html)}
        />
        <DividerHeader
        >
          <Icon icon={sexIcon(sex)} />
          <Header.Content>
            {categoryName}
            <Header.Subheader>
              {currentMorph && currentMorph.title}
            </Header.Subheader>
          </Header.Content>
        </DividerHeader>
        <Segment inverted={true}>
          {!active && (
            <AuctionEnded ad={this.props.ad} />
          )}
          <Grid stackable={true} columns={2} widths="equal">

            <Grid.Column width={8}>
              <AuctionOwner />
              <RenderImages />
              <Segment color="olive" >
                <AdStats
                  ad={ad}
                  morph={currentMorph && currentMorph.title}
                />
              </Segment>
              <Responsive
                {...Responsive.onlyMobile}
              >
                <RenderBuyActions />
              </Responsive>
              <Segment inverted={true}>
                <Header inverted={true}>Beskrivelse</Header>
                <span dangerouslySetInnerHTML={{ __html: description_html }} />
              </Segment>

              {(misconceptions_html && !!misconceptions_html.length) && (
                <Segment inverted={true}>
                  <Header>Helse</Header>
                  <span dangerouslySetInnerHTML={{ __html: misconceptions_html }} />
                </Segment>
              )}
            </Grid.Column>
            <Grid.Column stretched={false} width={8}>
              {askingPrice && (
                <BidStats
                  ad={ad}
                  highestBidder={highestBidder}
                  segmentProps={{ textAlign: 'right' }}
                  inverted={true}
                />
              )}
              {!!extras.length && (
                <Segment inverted={true}>
                  <List inverted={true}>
                    {extras.map(
                      ({ key, icon, header, description }) => (
                        <List.Item
                          key={key}
                        >
                          <List.Content>
                            <List.Header>
                              <Icon icon={icon} fa={{ fixedWidth: true }} />
                              {header}
                            </List.Header>
                            <List.Description>{description}</List.Description>
                          </List.Content>

                        </List.Item>
                      ),
                    )}
                  </List>
                </Segment>
              )}
              <Responsive
                minWidth={Responsive.onlyTablet.minWidth}
              >
                <RenderBuyActions />
              </Responsive>
              <div
                style={{
                  marginTop: 10,
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'baseline',
                }}
              >
                <div>
                  <Header inverted={true} style={{ display: 'inline' }}>
                    Del p Facebook
                </Header>
                </div>
                <div>
                  <SocialSharing />
                </div>
              </div>
            </Grid.Column>
          </Grid>
          <Modal
            // Fixes bug introduced in Semantic-UI 2.3
            style={{ marginTop: 'auto', margin: 'auto' }}
            open={bidConfirmationOpen}
          >
            <Header>Bud p {categoryName} </Header>
            <Modal.Content>
              Er du sikker p at du vil legge inn bud p <b>kr {userBid}</b>?
              <Message icon={true} warning={true}>
                <Icon icon={['fas', 'exclamation-triangle']} size="large" />
                <Message.Content>
                  <Message.Header>
                    Alle bud er bindende
                </Message.Header>
                  <p>
                    Ved  by godtar du betingelsene.
                    </p>
                </Message.Content>
              </Message>
              <Segment>
                <LocalForm
                  className="ui form"
                  model="bid"
                  onChange={({ userBid: bid }: any) => this.setState({ userBid: bid })}
                  onUpdate={({ $form }) => this.setState({ validBidForm: $form.valid })}
                  initialState={{ userBid: minBid }}

                >
                  <BaseField
                    label="Ditt bud, kr"
                    placeholder="Ditt bud"
                    model={'bid.userBid'}
                    errors={{
                      ...min(
                        minBid,
                        `Budet m vre over minstebudet, som er p kr. ${minBid}`,
                      ),
                      ...buy_now_price && max(
                        buy_now_price - .01,
                        `Budet m vre lavere enn kjp n pris, som er p kr. ${buy_now_price}.
                          Du kan ogs kjpe n ved  klikke tilbake.`,
                      ),
                    }}
                  />
                </LocalForm>
              </Segment>
            </Modal.Content>
            <Modal.Actions>
              <Button
                negative={true}
                onClick={this.hideBidConfirmation}
                inverted={true}
              >
                Avbryt
              </Button>
              <Button
                positive={true}
                disabled={!validBidForm}
                onClick={this.onConfirmBid}
                inverted={true}
              >
                Legg inn bud
              </Button>
            </Modal.Actions>
          </Modal>
          <Confirm
            // Fixes bug introduced in Semantic-UI 2.3
            style={{ marginTop: 'auto', margin: 'auto' }}
            open={buyNowConfirmationOpen}
            onCancel={this.hideBuyNowConfirmation}
            onConfirm={this.onConfirmBuyNow}
            cancelButton="Avbryt"
            confirmButton="Kjp n"
            header={`Kjp ${name} n!`}
            content={(
              <Modal.Content>
                Er du sikker p at du vil kjpe denne til <b>kr {buy_now_price}</b>?
              <Message icon={true} warning={true}>
                  <Icon icon={['fas', 'exclamation-triangle']} size="large" />
                  <Message.Content>
                    <Message.Header>
                      Alle kjp er bindende
                </Message.Header>
                    <p>
                      Auksjonen vil avsluttes, og du vil bli satt i kontakt med selger for  kunne sluttfre handelen.
                </p>
                  </Message.Content>
                </Message>
                <Message
                  info={true}
                  icon={true}
                >
                  <Icon icon={['fas', 'info-circle']} size="large" />
                  <Message.Content>
                    <p>
                      Betalingen avtales mellom deg og selger.
                  </p>
                    <p>
                      Alts vil det <b>ikke bli trukket noe fra din konto</b> i dette steget.
                  </p>
                  </Message.Content>
                </Message>
              </Modal.Content>
            )}
          />
        </Segment>
        <ImageSlideShow
          images={images}
          open={imageModalOpen}
          handleAfterSlide={(i: any) => this.setState({ slideIndex: i })}
          slideIndex={slideIndex}
          onClose={() => this.setState({ imageModalOpen: false })}
        />
      </>
    )
  }
}

import { buyNow, makeBid } from 'actions/ad'
import { IBid } from 'api/interfaces'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ base, users, user }: IStoreState, { ad }: IProps) => {
    const { highestBidderId, category, morph } = ad
    const highestBidder = highestBidderId ? users[highestBidderId] : undefined
    const thisCategory = base.categories && base.categories[category]
    const categoryName = thisCategory ? thisCategory.title : ''
    const currentMorph = base.morphs[morph]
    return {
      currentMorph,
      categoryName,
      highestBidder,
      auctionOwner: users[ad.user],
      user: user.user,
    }
  },
  (dispatch: Dispatch<any>) => {
    return {
      makeBid: (bid: IBid) => dispatch(makeBid(bid)),
      buyNow: (auctionId: number) => dispatch(buyNow(auctionId)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(AdPage)



