import { ConversationR, ConversationT } from 'actions/conversation'
import { IConversation, IMessageFull } from 'api/interfaces'
import { parseDateNull } from 'utils/date'

export const initialConversationState = {
  isFetching: false,
  isPosting: false,
  loaded: false,
  conversation: {},
  message: {},
}

interface IMessageFullHash {
  [s: number]: IMessageFull,
}

export interface IConversationState {
  isFetching: boolean
  isPosting: boolean
  loaded: boolean
  conversation: {
    [s: number]: IConversation,
  }
  message: IMessageFullHash
}

const parseMessage = (message: any) => {
  return {
    ...message,
    date: parseDateNull(message.date),
  }
}

export default (state: IConversationState = initialConversationState, action: ConversationT) => {
  switch (action.type) {
    case ConversationR.recieveSingleConversation: {
      const { conversation } = action
      const { messages, ...rest } = conversation
      const messagesFull: IMessageFullHash = messages.reduce(
        (r, message) => {
          r[message.id] = { ...parseMessage(message), conversation: conversation.id }
          return r
        },
        {},
      )
      return {
        ...state,
        isFetching: false,
        loaded: true,
        message: {
          ...state.message,
          ...messagesFull,
        },
        conversation: {
          ...state.conversation,
          [conversation.id]: rest,
        },
      }
    }
    case ConversationR.clearMessages:
      return {
        ...state,
        conversation: {},
        message: {},
        loaded: false,
      }
    case ConversationR.recieveSingleMessage: {
      const { message } = action
      return {
        ...state,
        isPosting: false,
        message: {
          ...state.message,
          [message.id]: parseMessage(message),
        },
      }
    }
    case ConversationR.updateReadStatus: {
      const { ids } = action
      const messages = Object.keys(state.message)
        .map((k) => state.message[Number(k)])
        .filter(
          (message) => ids.indexOf(message.conversation) !== -1,
      ).map(
        (message) => ({ ...message, unread: false }),
      )

      return {
        ...state,
        isPosting: false,
        message: messages.reduce(
          (r, message) => {
            r[message.id] = message
            return r
          },
          { ...state.message },
        ),
      }
    }
    case ConversationR.recieveMessages: {
      const { messages } = action
      return {
        ...state,
        isPosting: false,
        isFetching: false,
        message: {
          ...messages.reduce(
            (r, message) => {
              r[message.id] = parseMessage(message)
              return r
            },
            { ...state.message },
          ),
        },
      }
    }
    case ConversationR.getConversation:
    case ConversationR.getMessages:
      return {
        ...state,
        isFetching: true,
      }
    case ConversationR.postReadStatusMultiple:
    case ConversationR.sendMessageInConversation:
      return {
        ...state,
        isPosting: true,
      }
    case ConversationR.resetIsFetching:
      return {
        ...state,
        isFetching: false,
      }

    default:
      return state
  }
}



