import Icon from 'components/utils/Icon'
import * as React from 'react'
import { Control, Errors } from 'react-redux-form'
import { Checkbox, Dropdown, Form, Input, Label } from 'semantic-ui-react'
import * as validators from 'utils/validators'

interface IField {
  label?: string
  model: string
  typeComponent?: any
  controlProps?: any
  [s: string]: any
  style?: any
  hideErrors?: boolean
  custom?: boolean
}

const controlType = (isDropdown: boolean, isCheckBox: boolean) => {
  if (isDropdown) {
    return Control.custom
  }
  if (isCheckBox) {
    return Control.checkbox
  }
  return Control
}

export default ({ style, typeComponent, hideErrors, label, model, errors, controlProps, custom, ...rest }: IField) => {
  const { required, minLength, maxLength, minDate, maxDate, min, max } = rest
  errors = {
    ...errors,
    ...maxLength && validators.maxLength(maxLength),
    ...minLength && validators.minLength(minLength),
    ...minDate && validators.minDate(minDate),
    ...maxDate && validators.maxDate(maxDate),
    ...max && validators.max(max),
    ...min && validators.min(min),
    ...required && validators.required(),
  }
  const isCheckBox = typeComponent === Checkbox
  const isDropdown = typeComponent === Dropdown
  const cProps: any = controlProps || {
    ...rest,
    ...isCheckBox && { label },
  }
  const ControlType = custom ? Control.custom : controlType(isDropdown, isCheckBox)

  return (
    <Form.Field style={style}>
      {(!isCheckBox && label) && (
        <label>{label} {required && (<Icon icon="asterisk" fa={{size: 'xs'}} />)}</label>
      )}
      <ControlType
        model={model}
        id={model}
        errors={errors}
        placeholder={rest.placeholder || label}
        validateOn="change"
        component={typeComponent || Input}
        controlProps={cProps}
        // etc.
        {...rest}
        {...isDropdown && {
          mapProps: {
            value: (props) => props.modelValue,
          },
        }}
      />
      {!hideErrors && (
        <Errors
          className="errors"
          model={model}
          show="touched"
          component={({ children }) => (
            <Label basic={true} color="orange" pointing="above">{children}</Label>
          )}
        />
      )}
    </Form.Field>
  )
}



