import { postPaymentPremium } from 'api/payment'
import { IPremium } from 'interfaces'
import { Dispatch } from 'react-redux'
import { errorCatcher, newShortMessage } from './message'

export enum PremiumR {
  getPremiums = 'getPremiums',
  recievePremiums = 'recievePremiums',
  resetIsFetching = 'resetIsFetchingPremiums',
  payForPremium = 'payForPremium',
}

const _resetFetching = (): PremiumT => {
  return {
    type: PremiumR.resetIsFetching,
  }
}

export type PremiumT =
  { type: PremiumR.resetIsFetching } |
  { type: PremiumR.getPremiums } |
  { type: PremiumR.recievePremiums, premium: IPremium[] } |
  { type: PremiumR.payForPremium }

export const recievePremiums = (
  premium: IPremium[],
): PremiumT => {
  return {
    type: PremiumR.recievePremiums,
    premium,
  }
}

export const getPremiumsT = (): PremiumT => {
  return {
    type: PremiumR.getPremiums,
  }
}

const _payForPremium = (): PremiumT => {
  return {
    type: PremiumR.payForPremium,
  }
}

export const payForPremium = (id: number, price: number, nonce: string, coupon?: string) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_payForPremium())
    return postPaymentPremium(id, price, nonce, coupon)
      .then(({ status, ...rest }) => {
        const message = status === 'succeeded'
          ? 'Betalingen var vellykket.'
          : `Betalingens status er: ${status}`
        dispatch(newShortMessage(
          message,
          'payForPremium',
        ))
        dispatch(_resetFetching())
        return { status, ...rest }
      },
    )
      .catch(errorCatcher(dispatch, 'payForPremium', _resetFetching))
  }
}



