import Icon, { Loading } from 'components/utils/Icon'
import LoginButtons from 'components/utils/LoginButtons'
// import LoginButtons from 'components/utils/LoginButtons'
import { UserInfo } from 'interfaces'
import * as React from 'react'
// TODO: Replace module react-google-login. Possibly with auth0.
// Current implemenation is not the best, as it throws a warning
import { Link, RouteComponentProps, withRouter } from 'react-router-dom'
import { Message } from 'semantic-ui-react'
import { route } from 'utils/misc'

interface IProps extends IConnectProps {
  children: any
  renderOnNotLoggedIn?: any
  showLoginButton?: boolean
  bypassOnIncompleteProfile?: boolean
  style?: React.CSSProperties
}

interface IState {
  FBSDKError: boolean
  GSDKError: boolean
}

class RequiresLogin extends React.Component<IProps, IState> {
  public state = {
    FBSDKError: false,
    GSDKError: false,
  }
  public interval: any = null
  public timeout: any = null
  public FB: any = null
  public G: any = null
  public loginResponse = ({ _token, _provider }: any) => {
    let token
    if (_token.accessToken) {
      token = _token.accessToken
    }
    if (_token.idToken) {
      token = _token.idToken
    }
    if (token) {
      this.props.sendAuthToken(token, _provider)
        .then(
          (user: UserInfo) => {
            if (user && !user.completed_profile) {
              this.props.history.push(route.profileForm())
            }
          },
      )
    }
  }
  public SDKErrorMessage = ({ provider }: { provider: string }) => (
    <Message
      warning={true}
      icon={<Icon icon="exclamation-triangle" />}
      header={`Blokkert innhold?`}
      style={{
        textAlign: 'left',
      }}
      content={(
        <>
          <p>Det ser ut som det var et problem med  laste {provider}-inloggingsmodulen.</p>
          <p>Det kan vre den ble blokkert, da flere nye nettleser gjr dette.</p>

          <p>Du kan logge inn ved andre metoder, eller tillate tracking p denne nettsiden.</p>

          <p>Vi beklager det intrufne.</p>
        </>
      )}
    />
  )
  public getSocialStatus = () => {
    const FBloading = this.FB && this.FB.state.isFetching
    const FBloaded = this.FB && this.FB.state.isLoaded
    const FBSDKError = !FBloaded && !FBloading

    const Gloading = this.G && this.G.state.isFetching
    const Gloaded = this.G && this.G.state.isLoaded
    const GSDKError = !Gloaded && !Gloading
    return {
      FBloading,
      FBloaded,
      FBSDKError,
      Gloading,
      Gloaded,
      GSDKError,
    }
  }
  public componentDidMount() {
    this.interval = setInterval(
      () => this.forceUpdate(), 400,
    )
    this.timeout = setTimeout(
      () => {
        if (this) {
          const { FBSDKError, GSDKError, FBloading, Gloading } = this.getSocialStatus()
          this.setState({ FBSDKError, GSDKError })
          if (!FBloading || !Gloading) {
            clearInterval(this.interval)
          }
        }
      },
      7000,
    )
  }
  public componentWillUnmount() {
    clearInterval(this.interval)
    clearTimeout(this.timeout)
  }
  public render() {
    const { isFetching, isLoggedIn, hasCompletedProfile, children, showLoginButton, renderOnNotLoggedIn } = this.props
    if (isFetching) {
      return <Loading active={true}/>
    }
    if (isLoggedIn) {
      if (!this.props.bypassOnIncompleteProfile && !hasCompletedProfile) {
        return (
          <Message inverted={true}>
            Du m fullfre profilen din. <Link to={route.profileForm()}>Trykk her for  gjre det n.</Link>
          </Message>
        )
      }
      return children
    }
    return (
      <>
        {renderOnNotLoggedIn && (renderOnNotLoggedIn)}
        {showLoginButton && (<LoginButtons/>)}
      </>
    )
  }
}
import { sendAuthToken } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user }: IStoreState) => {
    return {
      loaded: user.loaded,
      isLoggedIn: !!user.user,
      isFetching: user.isFetching,
      hasCompletedProfile: user.user && user.user.completed_profile,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      sendAuthToken: (idToken: string, provider: string) => dispatch(sendAuthToken(idToken, provider)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default withRouter(connectCreator(RequiresLogin))



