import Icon from 'components/utils/Icon'
import * as React from 'react'
import { Responsive, ResponsiveProps, Segment, Transition, TransitionProps } from 'semantic-ui-react'

interface IProps extends ResponsiveProps {
  trigger?: any
  before?: any
  transitionProps?: TransitionProps
  open?: boolean
  floating?: boolean
}
interface IState {
  visible: boolean
}

export default class extends React.PureComponent<IProps, IState> {
  public state = {
    visible: false,
  }
  constructor(props: IProps) {
    super(props)
    if (this.props.open !== undefined) {
      this.state = {
        visible: this.props.open,
      }
    }
  }
  public componentWillReceiveProps({ open }: IProps) {
    if (open !== undefined) {
      this.setState({ visible: open })
    }
  }

  public render() {
    const { floating, children, trigger, before, transitionProps, ...props } = this.props
    const { visible } = this.state
    return (
      <>
        <Responsive
          {...Responsive.onlyMobile}
          {...props}
        >
          {before}
          <span
            onClick={() => this.setState({ visible: !visible })}
          >
            {(trigger || trigger === null) ? trigger : (
              <Icon
                circular={true}
                inverted={true}
                className="no-margin"
                icon="chevron-circle-down"
                style={{
                  zIndex: 1998,
                }}
              />
            )}
          </span>
          <Transition
            visible={visible}
            animation="vertical flip"
            duration={500}
            {...transitionProps}
          >
            <Segment
              inverted={true}
              {...floating && {className: 'floating-menu'}}
            >
              <div className="flex flex-end">
                <Icon
                  icon="times"
                  onClick={() => this.setState({ visible: false })}
                  style={{
                    right: '5%',
                    zIndex: 2001,
                    marginRight: 30,
                  }}

                />
              </div>
              {children}
            </Segment>
          </Transition>
        </Responsive>
        <Responsive
          minWidth={Responsive.onlyTablet.minWidth}
        >
          {children}
        </Responsive>
      </>
    )
  }
}



