import Icon, { IIconProps } from 'components/utils/Icon'
import * as React from 'react'

interface IProps {
  iconProps?: IIconProps
  rating: number
  disabled?: boolean
  onRate?: (rating: number) => any
}
interface IState {
  userRating: number
}
const topRating = 100
const maxRating = [1, 2, 3, 4, 5]
const divisor = topRating / 5
export default class extends React.Component<IProps, IState> {
  public state = {
    userRating: 0,
  }
  public handleChange = (rating: number) => {
    if (!this.props.onRate) {
      return
    }
    this.setState({ userRating: rating })
    this.props.onRate(rating)
  }

  public render() {
    const { iconProps, rating, disabled } = this.props
    const { userRating } = this.state
    const _rating = userRating || rating
    return (
      <>
        {maxRating.map(
          (key) => {
            const active = _rating / divisor >= key
            const userSelected = key === userRating
            return (
              <Icon
                onClick={() => this.handleChange(key * divisor)}
                key={key}
                disabled={disabled}
                color={
                  active ?
                    'yellow'
                    :
                    undefined
                }
                fa={{
                  icon: active ? ['fas', 'star'] : ['fal', 'star'],
                  transform: userSelected  ? 'grow-4' : undefined,
                }}
                {...iconProps}
              />
            )
          },
        )}
      </>
    )
  }
}



