import { UserR, UserT } from 'actions/user'
import { IBid } from 'api/interfaces'
import { UserInfo } from 'interfaces'

export const initialUserState = {
  isFetching: false,
  areAuctionsFetching: false,
  areBidsFetching: false,
  loaded: false,
}

export interface IUserState {
  isFetching: boolean
  areAuctionsFetching: boolean
  areBidsFetching: boolean
  loaded: boolean
  user?: UserInfo
  bids?: IBid[]
}

export default (state: IUserState = initialUserState, action: UserT) => {
  switch (action.type) {
    case UserR.recieveUserInfo:
      const { user } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        user,
      }
    case UserR.recieveUserBids:
      const { bids } = action
      return {
        ...state,
        areBidsFetching: false,
        loaded: true,
        bids,
      }
    case UserR.recieveLoginUrl:
      const { url } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        url,
      }

    case UserR.getUserInfo:
    case UserR.cancelSubscription:
    case UserR.sendAuthToken:
      return {
        ...state,
        isFetching: true,
      }
    case UserR.setUserNotLoggedIn:
      return {
        ...state,
        isFetching: false,
        loaded: true,
      }
    case UserR.getUserBids:
      return {
        ...state,
        areBidsFetching: true,
      }
    case UserR.getUserAuctions:
      return {
        ...state,
        areAuctionsFetching: true,
      }
    case UserR.logoutApi:
      // tslint:disable-next-line
      const { user: omit, ...rest } = state
      return rest
    case UserR.resetIsFetching:
      return {
        ...state,
        isFetching: false,
      }
    case UserR.resetAuctionsAreFetching:
      return {
        ...state,
        areAuctionsFetching: false,
      }
    case UserR.resetBidsAreFetching:
      return {
        ...state,
        areBidsFetching: false,
      }

    default:
      return state
  }
}



