import * as React from 'react'
import { Button } from 'semantic-ui-react'
const fbAppId = process.env.REACT_APP_FB_APP_ID
import FontAwesomeIcon from '@fortawesome/react-fontawesome'

const serialize = (obj: any) => {
  const str = []
  for (const p in obj) {
    if (obj.hasOwnProperty(p)) {
      str.push(encodeURIComponent(p) + '=' + encodeURIComponent(obj[p]))
    }
  }
  return str.join('&')
}

interface Meta {
  description: string
  url: string
  image: string
  title: string
}

const getMeta = () => {
  const x = (document.getElementsByTagName('META') as any)
  const keys = [
    'description',
    'title',
    'url',
    'image',
  ]
  return keys.reduce(
    (r, key) => {
      r[`og:${key}`] = x[key].content
      return r
    },
    {},
  )
}
interface IProps {
  meta?: Meta
}

const SocialSharing = ({meta}: IProps) => {
  const obj = {
    app_id: fbAppId,
    display: 'popup',
    method: 'share_open_graph',
    action_type: 'og.shares',
    redirect_uri: process.env.REACT_APP_DOMAIN,
    action_properties: JSON.stringify({ object: meta || getMeta() }),
  }
  const fbUrl = `https://www.facebook.com/dialog/share?${serialize(obj)}`
  return (
    <>
      <Button
        as="a"
        circular={true}
        color="facebook"
        target="_blank"
        href={fbUrl}
        icon={true}
        onClick={(e) => {
          e.preventDefault()
          window.open(
            fbUrl,
            'mywin',
            'left=20,top=20,width=555,height=560,toolbar=1,resizable=0')
        }}
      >
        <FontAwesomeIcon
          icon={['fab', 'facebook']}
          className="no-margin"
          size="lg"
        />
      </Button>
    </>
  )
}

export default SocialSharing



