import { AdR, AdT } from 'actions/ad'
import { IAuctionStatus } from 'api/constants'
import { IAd } from 'api/interfaces'
import { parseDateNull } from 'utils/date'

export const initialAdState: IAdState = {
  isFetching: false,
  loaded: false,
  ads: {},
  highestPrice: 0,
  lowestPrice: Infinity,
}

export interface IAdState {
  isFetching: boolean
  loaded: boolean
  ads: {
    [n: number]: IAd,
  }
  highestPrice: number
  lowestPrice: number
}

let highestPrice = 0
let lowestPrice = Infinity

const parseAd = (ad: IAd) => {
  if (ad.currentPrice > highestPrice) {
    highestPrice = ad.currentPrice
  }
  if (ad.currentPrice < lowestPrice) {
    lowestPrice = ad.currentPrice
  }
  return {
    ...ad,
    date_of_birth: parseDateNull(ad.date_of_birth),
    deadline: parseDateNull(ad.deadline),
    date: parseDateNull(ad.date),
    active: ad.status === IAuctionStatus.active,
  }
}

const reducer = (state: IAdState = initialAdState, action: AdT) => {
  switch (action.type) {
    case AdR.recieveAds: {
      const { ads: ads, append } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        ads:
          ads.reduce(
            (r, thisAd) => {
              r[thisAd.id] = parseAd(thisAd)
              return r
            },
            { ...append && state.ads },
          ),
        highestPrice,
        lowestPrice,
      }
    }
    case AdR.recieveSingleAd: {
      const { ad } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        ads: {
          ...state.ads,
          [ad.id]: parseAd(ad),
        },
        highestPrice,
        lowestPrice,
      }
    }
    case AdR.getSingleAd:
    case AdR.getAds:
    case AdR.getSingleAd:
    case AdR.setStatus:
    case AdR.newAd:
      return {
        ...state,
        isFetching: true,
      }
    case AdR.resetIsFetching:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state
  }
}

import { createMigrate, persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'
const migrations = {
  // Just throw old dev-data away.
  1: () => ({}),
  2: () => ({}),
}
export default persistReducer(
  {
    key: 'ad',
    storage,
    version: 2,
    blacklist: ['isFetching'],
    migrate: createMigrate(migrations, { debug: false }),
  },
  reducer)



