const distanceInWordsToNow = require('date-fns/distance_in_words_to_now')
const differenceInSeconds = require('date-fns/difference_in_seconds')
const locale = require('date-fns/locale/nb')
const format = require('date-fns/format')
const isThisYear = require('date-fns/is_this_year')
const parse = require('date-fns/parse')

const dayInSeconds = 24 * 60 * 60

export const prettyDate = (date: string | Date, options = {}) => {
  const diff = differenceInSeconds(new Date(), date)
  if (diff < 2 * dayInSeconds) {
    return distanceInWordsToNow(date, {
      locale,
      addSuffix: true,
      ...options,
    })
  }

  if (diff < 7 * dayInSeconds) {
    return format(date, 'p dddd', { locale })
  }
  if (isThisYear(date)) {
    return format(date, 'DD MMMM', { locale })
  }
  return format(date, 'Do MMMM YYYY', { locale })
}

export const parseDateNull = (a: Date | null): Date => a ? parse(a) : a



