import * as React from 'react'
import { Pagination, PaginationProps } from 'semantic-ui-react'

interface IProps {
  count: number
}

class AuctionPagination extends React.Component<IConnectProps> {
  public render() {
    const { auctionPage, totalPages, setSetting,  ...rest } = this.props
    if (totalPages <= 1) {
      return null
    }
    return (
      <Pagination
        activePage={auctionPage}
        onPageChange={(_e, { activePage }) => setSetting('auctionPage', Number(activePage) - 1)}
        totalPages={totalPages}
        {...rest}
      />
    )
  }
}
import { setSetting as _setSetting } from 'actions/settings'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ settings }: IStoreState, {count}: IProps) => {
    return {
      totalPages: Math.ceil(((count) - 1) / settings.auctionsPerPage),
      auctionPage: settings.auctionPage + 1,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      setSetting: (key: string, value: any) =>
        dispatch(_setSetting(key, value)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
// This is just weird. No idea why the regular approach is not working on this component.
export default connectCreator(AuctionPagination) as PaginationProps & any



