import { Sex } from 'api/interfaces'
import { Breeder } from 'interfaces/store'
import { SemanticCOLORS } from 'semantic-ui-react'

interface IAdPropertyDescriber {
  key: string
  icon: any
  header: string
  description: string
  color: SemanticCOLORS
}

export const adPropertyDescriber = {
  proven_breeder: (): IAdPropertyDescriber => ({
    key: 'proven_breeder',
    icon: ['fas', 'trophy-alt'],
    header: 'Bevist avler',
    description: 'Reptilen er en bevist avler',
    color: 'red',
  }),
  genetic_bomb: (): IAdPropertyDescriber => ({
    key: 'genetic_bomb',
    icon: ['fas', 'bolt'],
    header: 'Genetikkbombe',
    description: 'Genetisk bombe',
    color: 'olive',
  }),
  ready_for_breeding: (): IAdPropertyDescriber => ({
    key: 'ready_for_breeding',
    icon: ['fas', 'star'],
    header: 'Klar for avl',
    description: 'Reptilen er klar for avl.',
    color: 'green',
  }),
  diet: (diet: string): IAdPropertyDescriber => ({
    key: 'diet',
    icon: ['fas', 'utensils'],
    header: 'Diett',
    description: diet,
    color: 'purple',
  }),
}

export const sexString = (value: Sex) => {
  switch (value) {
    case Sex.m:
      return 'Han 1.0'
    case Sex.f:
      return 'Hun 0.1'
    case Sex.o:
      return 'Ukjent 0.0.1'
    default:
      return 'Ukjent'
  }
}

export const breedString = (value: Breeder) => {
  switch (value) {
    case Breeder.proven:
      return 'Bevist avler'
    case Breeder.ready:
      return 'Klar for avl'
    case Breeder.ready_and_proven:
      return 'Klar og bevist avl'
    default:
      return 'Ukjent'
  }
}



