import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import * as React from 'react'
import { Icon as SemIcon, IconProps } from 'semantic-ui-react'

export interface IIconProps extends IconProps {
  icon?: any
  fa?: any
}

const Icon = ({ style, centered, icon, fa, ...props }: IIconProps) => (
  <SemIcon
    style={{
      ...centered && {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      },
      ...style,
    }}
    {...props}
  >
    <FontAwesomeIcon
      icon={icon}
      {...fa}
    />
  </SemIcon >
)

export const Loading = ({ active, ...props }: IIconProps & { active: boolean }) =>
  active ? <Icon fa={{ pulse: true }} icon="spinner" {...props} /> : null

export default Icon



