export enum SettingsR {
  getSettings = 'getSettings',
  setSetting = 'setSetting',
  resetSettings = 'resetSettings',
}

export type SettingsAT =
  { type: SettingsR.getSettings, settings: any } |
  { type: SettingsR.setSetting, key: string, value: any } |
  { type: SettingsR.resetSettings }

export const recieveAds = (settings: any): SettingsAT => {
  return {
    type: SettingsR.getSettings,
    settings,
  }
}

export const setSetting = (key: string, value: any) => {
  return {
    type: SettingsR.setSetting,
    key, value,
  }
}

export const resetSettings = () => {
  return {
    type: SettingsR.resetSettings,
  }
}



