import Icon from 'components/utils/Icon'
import { UsersInfo } from 'interfaces'
import * as React from 'react'
import { Header, Image, Label } from 'semantic-ui-react'
import { prettyDate } from 'utils/date'
import Rating from './Rating'

interface IProps {
  user: UsersInfo
}

export interface IBidStats {
  value: any
  color: any
  label: any
}

export default ({ user }: IProps) => {
  const { given_name, family_name, average_rating, picture_url, date, verified } = user
  const [rating, rateCount] = average_rating
  return (
    <>
      {verified && <Label color="green" corner="right"><Icon icon="check" /></Label>}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          flexDirection: 'column',
        }}
      >
        <Image
          fluid={false}
          style={{ maxWidth: '100%', display: 'block' }}
        >
          <img src={picture_url} alt="" />
        </Image>
        {!!rateCount && (
          <div>
            <Rating rating={rating}/> ({rateCount})
          </div>
        )}
      </div>
      <Header as="h4">
        {given_name} {family_name}
        <Header.Subheader>Registrerte seg {prettyDate(date)}</Header.Subheader>
        <Header.Subheader>{verified && 'Verifisert bruker'}</Header.Subheader>
      </Header>
    </>
  )
}



