import ResponsiveImage, { IResponsiveImageProps } from 'components/utils/ResponsiveImage'
import * as React from 'react'

// Since this logo will appear in many different places, we will only load a
// single size of it, instead of the numerous different smaller sizes.
// It will then just be scaled down.
// Otherwise, it will load many different sizes of this, which is a waste of
// resources.
export default ({cloudinaryProps, ...rest}: IResponsiveImageProps) =>
  <ResponsiveImage publicId="reptil_auksjonen_logo" cloudinaryProps={{width: 150, ...cloudinaryProps}} {...rest} />



