import * as React from 'react'
import { Button, Message } from 'semantic-ui-react'
import ResponsiveImage from './ResponsiveImage'

interface IState {
  hasError: boolean
  timeOfMount: Date
}

declare global {
  interface Window { Raven: any }
}

class ErrorBoundaryC extends React.Component<IConnectProps, IState> {
  public state: IState = {
    hasError: false,
    timeOfMount: new Date(),
  }
  constructor(props: IConnectProps) {
    super(props)
    this.state = {
      hasError: false,
      timeOfMount: new Date(),
    }
  }

  public componentDidCatch(error: Error, info: any) {
    // Display fallback UI
    this.setState({ hasError: true })
    window.Raven && window.Raven.captureException(error, { extra: { info}})
  }

  public render() {
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return (
        <Message error={true}>
          <Message.Header>Noe gikk visst galt her.</Message.Header>
          <ResponsiveImage
            style={{ marginTop: 20, marginBottom: 20 }}
            publicId="face_palm"
            semanticProps={{
              bordered: true,
              size: 'large',
              centered: true,
              rounded: true,
            }}
            cloudinaryProps={{
              width: 200,
              gravity: 'west',
              crop: 'lfill',
            }}
          />
          <p>Feilen er blitt automatisk rapportert, og vi jobber iherdig med  reparere.</p>
          <p>
            Om du har noe nyttig informasjon som kan hjelpe oss,
            setter vi prise p om du kan sende det til oss ved  trykke nedenfor.
          </p>
          <Button
            fluid={true}
            primary={true}
            onClick={() => window.Raven.lastEventId() && window.Raven.showReportDialog()}
          >Send rapport
          </Button>
          <Button
            fluid={true}
            positive={true}
            onClick={() => window.location.reload()}
          >
            Last inn p nytt
          </Button>
          <p>Om feilen vedvarer, kan du prve  nullstille applikasjonen, ved  trykke nedenfor.</p>
          <Button
            fluid={true}
            negative={true}
            onClick={() => {
              this.props.resetApp()
              setTimeout(
                () => window.location.reload(),
                800,
              )
            }}
          >
            Nullstill app.
          </Button>
        </Message>
      )
    }
    return this.props.children
  }
}

import { connect, Dispatch } from 'react-redux'
const connectCreator = connect(
  null,
  (dispatch: Dispatch<{}>) => {
    return {
      resetApp: () => dispatch({
        type: 'RESET',
      }),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export const ErrorBoundary = connectCreator(ErrorBoundaryC)

export default (WrappedComponent: any) => (props: any) => (
  <ErrorBoundary>
    <WrappedComponent {...props} />
  </ErrorBoundary>
)



