import Icon from 'components/utils/Icon'
import ResponsiveImage from 'components/utils/ResponsiveImage'
import * as React from 'react'
import { Link } from 'react-router-dom'
import { Button, Grid, Segment } from 'semantic-ui-react'
import { route } from 'utils/misc'
interface ISocial {
  href: string
  icon: any
  color: any
}
// const RKMedia = require('assets/RKMedia_light_optimized.svg')
import RKMedia from 'components/widgets/RKMedia'

const socialLinks: ISocial[] = [
  {
    href: `https://www.facebook.com/${process.env.REACT_APP_FB_SITE_ID}`,
    icon: ['fab', 'facebook'],
    color: 'facebook',
  },
  {
    href: `mailto:${process.env.REACT_APP_CONTACT_MAIL}`,
    icon: 'envelope',
    color: 'grey',
  },
  {
    href: `https://www.instagram.com/${process.env.REACT_APP_INSTAGRAM_PAGE_ID}`,
    icon: ['fab', 'instagram'],
    color: 'instagram',
  },
]

const Footer = () => (
  <footer>
    <Segment inverted={true} style={{ marginTop: 80 }}>
      <Grid
        centered={true}
        stackable={true}
        verticalAlign="middle"
        columns="equal"
      >
        <Grid.Column className="flex-center-row">
          <a href="https://www.reptilesofnorway.no" target="_blank">
            <ResponsiveImage
              cloudinaryProps={{ height: 100, width: undefined }}
              publicId="RepOfNorwLOGO.png"
            /></a>
        </Grid.Column>
        <Grid.Column textAlign="center">
          <div className="provider flex-center-row">
            <a href="https://www.rkmedia.no" target="_blank">
              <RKMedia frontColor="#ffffff" backColor="#1e70bf" />
              <div>Tjenesten er levert av RK Media.no</div>
            </a>
          </div>
        </Grid.Column>
        <Grid.Column className="flex-center-row" textAlign="center">
          <a href="https://www.digiconsulting.no" target="_blank">
            <ResponsiveImage
              cloudinaryProps={{ height: 100, width: undefined }}
              publicId="digiConsulting.png"
            /></a>
        </Grid.Column>
        <Grid.Row textAlign="center">
          <Grid.Column textAlign="center" width={4}>
            {socialLinks.map(({ icon, color, href }) => (
              <Button
                as="a"
                href={href}
                key={icon}
                circular={true}
                color={color}
                icon={true}
              >
                <Icon icon={icon} size="large" className="no-margin" />
              </Button>
            ))}
          </Grid.Column>
          <Grid.Column>
            <div className="d-flex flex-evenly blocks">
              <a
                className="m-block m-center"
                href="https://www.reptilauksjonen.no"
                target="_blank"
              >
                Reptiles of Norway, 4613 Kristiansand
              </a>
              <Link
                className="m-block m-center"
                to={route.tos()}
              >
                Betingelser, regler og vilkr
              </Link>
              <Link
                className="m-block m-center"
                to={route.privacy()}
              >
                Personvernsrklering
              </Link>
            </div>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment >
  </footer >
)
export default Footer



