import { IBid } from 'api/interfaces'
import {
  fetchUserAuctions,
  fetchUserBids,
  fetchUserInfo,
  logout,
  postAuthToken,
  userCancelSubscription,
  userPostProfile,
} from 'api/user'
import { IProfileForm } from 'forms/profile'
import { UserInfo } from 'interfaces'
import { Dispatch } from 'react-redux'
import { IStoreState } from 'store'
import { recieveAds } from './ad'
import { getBaseInfo } from './baseInfo'
import { clearMessages, getUnreadMessages } from './conversation'
import { errorCatcher, newShortMessage } from './message'
import { recieveUsers } from './users'

export enum UserR {
  getUserInfo = 'getUserInfo',
  sendAuthToken = 'sendAuthToken',
  cancelSubscription = 'cancelSubscription',
  logoutApi = 'logoutApi',
  recieveUserInfo = 'recieveUserInfo',
  postProfile = 'postProfile',
  recieveUserBids = 'recieveUserBids',
  recieveLoginUrl = 'recieveLoginUrl',
  setUserNotLoggedIn = 'setUserNotLoggedIn',
  resetIsFetching = 'resetIsFetchingUser',
  getUserAuctions = 'getUserAuctions',
  getUserBids = 'getUserBids',
  resetBidsAreFetching = 'resetBidsAreFetching',
  resetAuctionsAreFetching = 'resetAuctionsAreFetching',
}
const setUserNotLoggedIn = (): UserT => {
  return {
    type: UserR.setUserNotLoggedIn,
  }
}
const _resetFetching = (): UserT => {
  return {
    type: UserR.resetIsFetching,
  }
}

export type UserT =
  { type: UserR.resetIsFetching } |
  { type: UserR.getUserBids } |
  { type: UserR.getUserAuctions } |
  { type: UserR.setUserNotLoggedIn } |
  { type: UserR.resetBidsAreFetching } |
  { type: UserR.resetAuctionsAreFetching } |
  { type: UserR.getUserInfo } |
  { type: UserR.cancelSubscription } |
  { type: UserR.sendAuthToken } |
  { type: UserR.logoutApi } |
  { type: UserR.recieveUserInfo, user: UserInfo } |
  { type: UserR.postProfile, profile: IProfileForm } |
  { type: UserR.recieveUserBids, bids: IBid[] } |
  { type: UserR.recieveLoginUrl, url: string }

export const recieveUserInfo = (user: UserInfo): UserT => {
  return {
    type: UserR.recieveUserInfo,
    user,
  }
}

export const recieveUserBids = (bids: IBid[]): UserT => {
  return {
    type: UserR.recieveUserBids,
    bids,
  }
}
export const postProfile = (profile: IProfileForm): UserT => {
  return {
    type: UserR.postProfile,
    profile,
  }
}

export const recieveLoginUrl = (url: string): UserT => {
  return {
    type: UserR.recieveLoginUrl,
    url,
  }
}
export const getUserInfoT = (): UserT => {
  return {
    type: UserR.getUserInfo,
  }
}
const _resetFetchingAuctions = (): UserT => {
  return {
    type: UserR.resetAuctionsAreFetching,
  }
}
const _resetFetchingBids = (): UserT => {
  return {
    type: UserR.resetBidsAreFetching,
  }
}
const _getUserAuctions = (): UserT => {
  return {
    type: UserR.getUserAuctions,
  }
}

const _getUserBids = (): UserT => {
  return {
    type: UserR.getUserBids,
  }
}
export const cancelSubscriptionT = (): UserT => {
  return {
    type: UserR.cancelSubscription,
  }
}
export const _sendAuthToken = (): UserT => {
  return {
    type: UserR.sendAuthToken,
  }
}
export const _logoutApi = (): UserT => {
  return {
    type: UserR.logoutApi,
  }
}

type dispatcher = () => (dispatch: Dispatch<any>, getState: () => IStoreState) => any

export const getUserInfo: dispatcher = () => {
  return (dispatch) => {
    dispatch(getUserInfoT())
    return fetchUserInfo()
      .then(({ user }) => {
        if (user) {
          dispatch(recieveUserInfo(user))
          dispatch(getUnreadMessages())
          return user
        }
        dispatch(setUserNotLoggedIn())
        return user
      },
    ).catch(errorCatcher(dispatch, 'getUserInfo', _resetFetching))
  }
}

export const getUserAuctions: dispatcher = () => {
  return (dispatch) => {
    dispatch(_getUserAuctions())
    return fetchUserAuctions()
      .then(({ ads, users }) => {
        dispatch(recieveUsers(users))
        dispatch(recieveAds(ads, true))
        dispatch(_resetFetchingAuctions())
      },
    ).catch(errorCatcher(dispatch, 'getUserAuctions', _resetFetchingAuctions))
  }
}

export const getUserBids: dispatcher = () => {
  return (dispatch) => {
    dispatch(_getUserBids())
    return fetchUserBids()
      .then(({ ads, users, bids }) => {
        dispatch(recieveUsers(users))
        dispatch(recieveAds(ads, true))
        dispatch(recieveUserBids(bids))
      },
    ).catch(errorCatcher(dispatch, 'getUserBids', _resetFetchingBids))
  }
}

export const sendCancelSubscription: dispatcher = () => {
  return (dispatch, getState) => {
    const { user: _user } = getState()
    if (_user.isFetching) {
      return null
    }

    dispatch(cancelSubscriptionT())
    return userCancelSubscription()
      .then(({ user }) => {
        dispatch(newShortMessage(`Ditt abbonomant ble kansellert.`, 'sendCancelSubscription'))
        return dispatch(recieveUserInfo(user))
      },
    ).catch(errorCatcher(dispatch, 'cancelSubscription', _resetFetching))
  }
}

export const sendAuthToken = (idToken: string, provider: string) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_sendAuthToken())
    removeSensitiveUserData(dispatch)
    return postAuthToken(idToken, provider)
      .then(({ user }) => {
        dispatch(recieveUserInfo(user))
        dispatch(getBaseInfo())
        dispatch(getUnreadMessages())
        return user
      }).catch(errorCatcher(dispatch, 'sendAuthToken', _resetFetching))
  }
}

export const sendProfile = (profile: IProfileForm) => {
  return (dispatch: Dispatch<any>) => {
    return userPostProfile(profile)
      .then(({ user }) => {
        dispatch(recieveUserInfo(user))
        dispatch(newShortMessage(`Din profil ble oppdatert.`, 'sendProfile'))
        return user
      }).catch(errorCatcher(dispatch, 'sendAuthToken', _resetFetching))
  }
}

const removeSensitiveUserData = (dispatch: Dispatch<any>) => {
  dispatch(clearMessages())
}

export const logoutApi = () => {
  return (dispatch: Dispatch<any>) => {
    removeSensitiveUserData(dispatch)
    logout()
      .then((_af) => {
        return dispatch((_logoutApi()))
      })
      .catch(console.log)
  }
}



