import * as React from 'react'
import { Divider, Header, HeaderProps } from 'semantic-ui-react'

interface IProps extends HeaderProps {
  children: any
  subheader?: any
}

export default ({ children, subheader, ...rest }: IProps) => (
  <Divider horizontal={true} className="divider-header">
    <Header as="h2" inverted={true} {...rest}>
      {children}
      {subheader && (
        <Header.Subheader>{subheader}</Header.Subheader>
      )}
    </Header>
  </Divider>
)



