import Stat from 'components/widgets/Stat'
import * as React from 'react'
import { Label, LabelDetailProps, LabelProps, SemanticICONS, StatisticProps } from 'semantic-ui-react'
const distanceInWordsToNow = require('date-fns/distance_in_words_to_now')
const differenceInSeconds = require('date-fns/difference_in_seconds')
const noLocale = require('date-fns/locale/nb')
const isToday = require('date-fns/is_today')
const format = require('date-fns/format')
const parse = require('date-fns/parse')
const differenceInDays = require('date-fns/difference_in_days')
const isPast = require('date-fns/is_past')

enum Ending {
  LessThan1Minute = 'red',
  LessThan5Minutes = 'orange',
  LessThan3Hours = 'purple',
  Today = 'blue',
  Later = 'teal',
  Passed = 'grey',
}

interface IState {
  timeLeft: string
  ending: Ending
}

interface IProps {
  deadline: string | Date
  style?: any
  componentProps?: LabelProps | StatisticProps
  labelDetailProps?: LabelDetailProps
  as?: 'label' | 'stats' | 'pure'
  icon?: SemanticICONS
}

export default class extends React.Component<IProps, IState> {
  public interval: any
  public state = {
    timeLeft: '',
    ending: Ending.Later,
  }
  public getTimeString() {
    const deadline = parse(this.props.deadline) as Date
    const now = new Date()
    const passed = isPast(deadline)
    if (passed) {
      return distanceInWordsToNow(
        this.props.deadline, { includeSeconds: true, addSuffix: false, locale: noLocale }) + (passed ? ' siden' : '')
    }
    const days = differenceInDays(deadline, now)
    const timer = format(deadline.getTime() - new Date().getTime(), 'HH[t] mm[m] ss[s] ')
    return !!days ? `${days}\d ${timer}` : timer
  }
  public getEnding(): Ending {
    const { deadline } = this.props
    const minutes = differenceInSeconds(deadline, new Date()) / 60
    if (minutes < 0) {
      return Ending.Passed
    }
    if (minutes < 1) {
      return Ending.LessThan1Minute
    }
    if (minutes < 5) {
      return Ending.LessThan5Minutes
    }
    if (minutes < 60 * 3) {
      return Ending.LessThan3Hours
    }
    if (isToday(deadline)) {
      return Ending.Today
    }
    return Ending.Later
  }
  public tick = () => {
    this.setState({
      timeLeft: this.getTimeString(),
      ending: this.getEnding(),
    })
  }
  public componentDidMount() {
    if (this.props.deadline) {
      this.tick()
      this.interval = setInterval(this.tick, 1000)
    }
  }
  public componentWillUnmount() {
    clearInterval(this.interval)
  }
  public render() {
    if (!this.props.deadline) {
      return null
    }
    const { ending, timeLeft } = this.state
    const { componentProps, labelDetailProps, style, as, icon } = this.props
    const endingPhrase = ending === Ending.Passed ? 'Utgtt' : 'Slutter om'
    switch (as) {
      case 'pure':
        let endingClass = 'long'
        const endingLength = timeLeft.length
        if (endingLength < 10) {
          endingClass = 'short'
        } else if (endingLength < 20) {
          endingClass = 'medium'
        }
        return (
          <div
            className={`ui header no-header black inverted`}
          >
            <div className="timeleft-text">{endingPhrase}: </div>
            <div className={`timeleft ${endingClass}`}>{timeLeft}</div>
          </div>
        )
      case 'stats':
        return (
          <Stat
            label={endingPhrase}
            value={timeLeft}
            inverted={true}
            icon={icon as SemanticICONS}
            statProps={{
              color: ending,
            }}
          />
        )
      default:
        return (
          <Label
            size="large"
            color={ending}
            {...componentProps}
            style={style}
          > {endingPhrase}
            < Label.Detail {...labelDetailProps}>{timeLeft}</Label.Detail>
          </ Label >
        )
    }
  }
}



