import DividerHeader from 'components/utils/DividerHeader'
import RequiresLogin from 'components/utils/RequiresLogin'
import UserInfo from 'components/widgets/UserInfo'
import * as  React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container, Message } from 'semantic-ui-react'

function parseParms(str: string) {
  const pieces = str.substr(1).split('&')
  const data = {}
  let i
  let parts
  // process each query pair
  for (i = 0; i < pieces.length; i++) {
    parts = pieces[i].split('=')
    if (parts.length < 2) {
      parts.push('')
    }
    data[decodeURIComponent(parts[0])] = decodeURIComponent(parts[1])
  }
  return data
}

class Login extends React.Component<IConnectProps> {
  constructor(props: IConnectProps) {
    super(props)
    this.checkHashAndSubmit(props)
  }
  public checkHashAndSubmit(props: IConnectProps) {
    const hash = props.location.hash
    const hashed = hash && parseParms(hash) as any
    if (hashed && hashed.access_token) {
      this.props.sendAuthToken(hashed.access_token, 'facebook').then(console.log)
    }
  }
  public render() {
    return (
      <Container textAlign="center">
        <DividerHeader>
          Logg inn til Reptiles of Norway
        </DividerHeader>
        <RequiresLogin
          renderOnNotLoggedIn={(
            <Message
              info={true}
            >
              <Message.Header> Plogging gjennom tredjeparter </Message.Header>
              <p>Du kan logge p gjennom tredjeparter for  f tilgang til siden.</p>
              <p>Dette er en trygg og enkel mte  logge inn p.</p>
            </Message>
          )}
          showLoginButton={true}
        >

          <UserInfo />
        </RequiresLogin>
      </Container >
    )
  }
}
import { sendAuthToken } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
const connectCreator = connect(
  null,
  (dispatch: Dispatch<{}>) => {
    return {
      sendAuthToken: (idToken: string, provider: string) => dispatch(sendAuthToken(idToken, provider)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(Login)



