import { postHeaders, VERBS } from './constants'
import { myFetch } from './helpers'
import { IReviewResponse } from './interfaces'

export const postAdReview = (adId: number, rating: number, message: string) => myFetch(`/json/v1/ad/${adId}/review`, {
  body: { rating, message },
  method: VERBS.PATCH,
  headers: postHeaders,
}) as Promise<{ review: IReviewResponse }>

export const fetchAuctionReviewForAuction = (adId: number) => myFetch(`/json/v1/ad/${adId}/review`,
) as Promise<{ reviews: IReviewResponse[] }>



