/* tslint:disable:max-line-length*/
import Icon, { Loading } from 'components/utils/Icon'
import ResponsiveImage from 'components/utils/ResponsiveImage'
import * as React from 'react'
import { Link } from 'react-router-dom'
import { Comment, Header, Label, Message, Segment } from 'semantic-ui-react'
import { prettyDate } from 'utils/date'
import { isEmpty, route } from 'utils/misc'

interface IProps {
  only_unread: boolean
  limit?: number
  header?: React.ReactNode
  noContent?: React.ReactNode
}

class MessagesList extends React.Component<IConnectProps> {
  public render() {
    const { filteredMessages, ads, users, morphs, categories, limit, only_unread, header, noContent, isFetching } = this.props

    if (!filteredMessages.length) {
      if (isFetching) {
        return <Loading active={true} size="big" />
      }
      return noContent || (
        <Message>Du har ingen uleste beskjeder</Message>
      )
    }

    if (
      isEmpty(users) || isEmpty(ads) || isEmpty(morphs) || isEmpty(categories)) {
      return null
    }
    return (
      <>
        {!!filteredMessages.length ? header : noContent}
        {filteredMessages.map(
          ({ messages, auction_id }) => {
            const thisAuction = ads[Number(auction_id)]
            const category = thisAuction && categories[thisAuction.category]
            const morph = thisAuction && morphs[thisAuction.morph]
            const sorted = messages.sort(
              ({ date: a }, { date: b }) => a.getDay() - b.getDate(),
            )
            const sliced = limit ? sorted.slice(0, limit) : sorted
            const newestMessage = sorted[sorted.length - 1]
            return (
              <Segment key={auction_id}>
                {thisAuction && (
                  <>
                    <Header as="h3" dividing={true} className="flex flex-between">
                      <Link to={route.auction(auction_id)}>
                        <ResponsiveImage
                          publicId={thisAuction.images[0].public_id}
                          cloudinaryProps={{
                            width: 45,
                          }}
                        />
                        {' '}{category && category.title} <small>{morph && morph.title}</small>
                      </Link>
                      {only_unread && (
                        <Label
                          as="a"
                          style={{ height: 'min-content' }}
                          circular={true}
                          color="red"
                          icon={<Icon icon="check" className="no-margin" />}
                          onClick={() => this.props.postReadStatusMultiple(
                            [
                              {
                                id: newestMessage.conversation,
                                read_at: newestMessage.date,
                              },
                            ],
                          )}
                        />
                      )}
                    </Header>
                  </>
                )}
                <Link to={route.auction(auction_id)}>
                  <Comment.Group>
                    {sliced.map(
                      ({ message_html, user, id, date }, i) => {
                        const thisUser = users[user]
                        const moreMessages = sorted.length - sliced.length
                        return (
                          <Comment key={id}>
                            {thisUser && <Comment.Avatar
                              src={thisUser.picture_url}
                            />}
                            <Comment.Content>
                              <Comment.Author
                              >
                                {thisUser && thisUser.given_name}
                              </Comment.Author>
                              <Comment.Metadata>
                                <div>{prettyDate(date)}</div>
                              </Comment.Metadata>
                              <Comment.Text>
                                <div
                                  dangerouslySetInnerHTML={{ __html: message_html }}
                                />
                              </Comment.Text>
                              {i === sliced.length - 1 && !!moreMessages && (
                                <Comment.Actions>
                                  {moreMessages} meldinger til
                            </Comment.Actions>
                              )}
                            </Comment.Content>
                          </Comment>
                        )
                      },
                    )}
                  </Comment.Group>
                </Link>
              </Segment>
            )
          },
        )}
      </>)
  }
}

interface IAuctionMessage {
  auction_id: number
  messages: IMessageFull[]
  date: Date
}

import { postReadStatusMultiple } from 'actions/conversation'
import { IMessageFull, IReadStatus } from 'api/interfaces'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, users, ads, conversation, base }: IStoreState, { only_unread }: IProps) => {
    const messages = Object.keys(conversation.message).map((k) => conversation.message[Number(k)])
    const filtered = only_unread ? messages.filter(
      ({ unread, user: fromUserId }) => unread && user.user && user.user.id !== fromUserId,
    ) : messages
    const filteredMessages: { [n: number]: IAuctionMessage } =
      filtered
        .reduce(
          (r: { [n: number]: IAuctionMessage }, message) => {
            if (message.auction_id) {
              if (r[message.auction_id]) {
                r[message.auction_id].messages.push(message)
                if (r[message.auction_id].date < message.date) {
                  r[message.auction_id].date = message.date
                }
              } else {
                r[message.auction_id] = {
                  auction_id: message.auction_id,
                  messages: [message],
                  date: message.date,
                }
              }
            }
            return r
          },
          {},
      )
    return {
      isFetching: conversation.isFetching,
      categories: base.categories,
      morphs: base.morphs,
      filteredMessages: Object.keys(filteredMessages)
        .map((k) => filteredMessages[Number(k)])
        .sort(({ date: A }, { date: B }) => B.getDate() - A.getDate()),
      users,
      ads: ads.ads,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      postReadStatusMultiple: (updates: IReadStatus[]) =>
        dispatch(postReadStatusMultiple(updates)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default connectCreator(MessagesList)



