/* tslint:disable:max-line-length*/
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import { auctionStatusInfo, IAuctionStatus } from 'api/constants'
import { IAd } from 'api/interfaces'
import AuctionTable from 'components/Auction/AuctionTable'
import DividerHeader from 'components/utils/DividerHeader'
import Meta from 'components/utils/Meta'
import RequiresLogin from 'components/utils/RequiresLogin'
import * as React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container, Header, Icon } from 'semantic-ui-react'
import { route } from 'utils/misc'

class UserAuctions extends React.Component<IConnectProps> {
  public componentDidMount() {
    if (!this.props.areAuctionsFetching) {
      this.props.getUserAuctions()
    }
  }
  public onClick = (id: number) => this.props.history.push(route.auction(id))

  public render() {
    const { userAuctions } = this.props
    const auctionStatuses: { [s: string]: IAd[] } = userAuctions
      .reduce(
        (r, auction) => {
          if (r[auction.status]) {
            r[auction.status].push(auction)
          } else {
            r[auction.status] = [auction]
          }
          return r
        },
        {},
    )
    return (
      <Container>
        <Meta
          title="Mine auksjoner"
          description={'Auksjonsside for reptiler.'}
        />
        <DividerHeader
          subheader="Her kan du se en oversikt over alle auksjonene dine."
        >
          Dine auksjoner
        </DividerHeader>
        <RequiresLogin showLoginButton={true}>
          {Object.keys(auctionStatuses)
            .sort(
              (statusA, statusB) => auctionStatusInfo(statusA as IAuctionStatus).sortOrder - auctionStatusInfo(statusB as IAuctionStatus).sortOrder,
          )
            .map(
              (status) => {
                const auctions = auctionStatuses[status]
                const { color, text, icon, description } = auctionStatusInfo(status as IAuctionStatus)
                return (
                  <React.Fragment key={status}>
                    <Header as="h2" icon={true} inverted={true} textAlign="center">
                      <Icon color={color}>
                        <FontAwesomeIcon icon={icon} className="no-margin"/>
                      </Icon>
                      {text}
                      <Header.Subheader>
                        {description}
                      </Header.Subheader>
                    </Header>
                    <AuctionTable
                      tableProps={{
                        color,
                      }}
                      key={status}
                      auctions={auctions}
                      onClick={this.onClick}
                    />
                  </React.Fragment>
                )
              },
          )}
        </RequiresLogin>
      </Container >
    )
  }
}
import { getUserAuctions } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, users, ads }: IStoreState) => {
    return {
      areAuctionsFetching: user.areAuctionsFetching,
      users,
      userAuctions: Object.keys(ads.ads)
        .map((k) => ads.ads[Number(k)])
        .filter(({ user: usr }) => {
          return user.user && usr === user.user.id
        }),
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getUserAuctions: () =>
        dispatch(getUserAuctions()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(UserAuctions)



