import { PremiumR, PremiumT } from 'actions/premium'
import { IPremium } from 'interfaces'
import { reduce } from 'utils/reduce'
export const initialPremiumState = {
  isFetching: false,
  loaded: false,
  premium: {},
}

export interface IPremiumState {
  isFetching: boolean
  loaded: boolean
  premium: {
    [s: number]: IPremium,
  }
}

const reducer = (state: IPremiumState = initialPremiumState, action: PremiumT) => {
  switch (action.type) {
    case PremiumR.recievePremiums:
      const { premium } = action
      return {
        ...state,
        isFetching: false,
        loaded: true,
        premium: reduce(premium),
      }
    case PremiumR.resetIsFetching:
      return {
        ...state,
        isFetching: false,
      }
    case PremiumR.payForPremium:
      return {
        ...state,
        isFetching: true,
      }

    default:
      return state
  }
}

export default reducer



