import { IReviewResponse } from 'api/interfaces'
import {
  fetchAuctionReviewForAuction,
  postAdReview,
} from 'api/review'
import { Dispatch } from 'react-redux'
import { errorCatcher } from './message'

export enum ReviewR {
  getReviews = 'getReviews',
  getReviewsForAuction = 'getReviewsForAuction',
  recieveSingleReview = 'recieveSingleReview',
  recieveReviews = 'recieveReviews',
  resetIsPosting = 'resetIsPosting',
  postReview = 'postReview',
  resetIsFetching = 'resetIsFetching',
}

export type ReviewT =
  { type: ReviewR.getReviews } |
  { type: ReviewR.getReviewsForAuction } |
  { type: ReviewR.resetIsFetching } |
  { type: ReviewR.resetIsPosting } |
  { type: ReviewR.recieveSingleReview, review: IReviewResponse } |
  { type: ReviewR.recieveReviews, reviews: IReviewResponse[] } |
  { type: ReviewR.postReview }

const _getReviewsForAuction = (): ReviewT => {
  return {
    type: ReviewR.getReviewsForAuction,
  }
}

const _postReview = (): ReviewT => {
  return {
    type: ReviewR.postReview,
  }
}
//
const recieveReviews = (reviews: IReviewResponse[]): ReviewT => {
  return {
    type: ReviewR.recieveReviews,
    reviews,
  }
}
const recieveSingleReview = (review: IReviewResponse): ReviewT => {
  return {
    type: ReviewR.recieveSingleReview,
    review,
  }
}

const _resetFetching = (): ReviewT => {
  return {
    type: ReviewR.resetIsFetching,
  }
}

const _resetPosting = (): ReviewT => {
  return {
    type: ReviewR.resetIsPosting,
  }
}

export const getReviewsForAuction = (id: number) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_getReviewsForAuction())
    fetchAuctionReviewForAuction(id)
      .then(({ reviews }) => {
        dispatch(recieveReviews(reviews))
      },
    )
      .catch(errorCatcher(dispatch, 'getReviewsForAuction', _resetFetching))
  }
}

export const postReview = (auctionId: number, rating: number, message: string) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_postReview())
    return postAdReview(auctionId, rating, message)
      .then(({ review }) => {
        dispatch(recieveSingleReview(review))
        dispatch(_resetPosting())
      })
      .catch(errorCatcher(dispatch, 'postReview', _resetPosting))
  }
}



