import FourOhFour from 'components/utils/404'
import * as  React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container } from 'semantic-ui-react'
import AdPage from './AdPage'

class Ad extends React.Component<IConnectProps> {
  public componentDidMount() {
    const { isFetching } = this.props
    const { id } = this.props.match.params
    if (!isFetching && id && !isNaN(id)) {
      this.props.getAd(id)
    }
  }
  public render() {
    const { ad, isFetching } = this.props
    const notFound = !isFetching && !ad
    return (
      <Container>
        {notFound && (<FourOhFour />)}
        {ad && (<AdPage ad={ad} />)}
      </Container >
    )
  }
}

import { getAd } from 'actions/ad'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ ads }: IStoreState, ownProps: RouteComponentProps<any>) => {
    const { id } = ownProps.match.params
    const ad = ads.ads && ads.ads[id]
    return {
      ad,
      id,
      isFetching: ads.isFetching,
    }
  },
  (dispatch: Dispatch<any>) => {
    return {
      getAd: (id: number) =>
        dispatch(getAd(id)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(Ad)



