import * as React from 'react'
import { Link } from 'react-router-dom'
import { Header, Message, Segment } from 'semantic-ui-react'
import { route } from 'utils/misc'
import ResponsiveImage from './ResponsiveImage'

export default () => (
  <Link to={route.auctionList()}>
    <Segment text="center" inverted={true} textAlign="center">
      <Header as="h2" textAlign="center" inverted={true}>
        <Header.Content>
          Oioioi. Her har det visst skjedd noe.
        </Header.Content>
        <Header.Subheader>
          <Message
            color="black"
            info={true}
            header="Kunne ikke finne siden du letet etter"
            content="Vi er p saken. Trykk for  g tilbake til hovedsiden, eller bruk menyen oppe."
          />
        </Header.Subheader>

      </Header>
      <ResponsiveImage
        // circular={true}
        publicId="face_palm"
        semanticProps={{
          bordered: true,
          size: 'large',
          centered: true,
        }}
        cloudinaryProps={{
          width: 450,
          height: 335,
          gravity: 'west',
          crop: 'lfill',
        }}
      />
    </Segment>
  </Link>
)



