import { fetchProrationPreview } from 'api/payment'
import DividerHeader from 'components/utils/DividerHeader'
import Icon, { Loading } from 'components/utils/Icon'
import Meta from 'components/utils/Meta'
import Logo from 'components/widgets/Logo'
import { IPremium } from 'interfaces'
import * as React from 'react'
import Script from 'react-load-script'
import { RouteComponentProps } from 'react-router-dom'
import {
  Button, Card, Container, Dimmer, Form, Input, Loader, Message, Segment, Tab,
} from 'semantic-ui-react'
import { prettyCurrency, route } from 'utils/misc'

interface IState {
  cost?: number
  isFetching: boolean
  coupon: string
  activeIndex: number
}
import * as Loadable from 'react-loadable'

const Payment = Loadable({
  loader: () => import('components/payment/Payment'),
  loading: Loading,
})

class Premium extends React.Component<IConnectProps, IState> {
  public state = {
    cost: 0,
    isFetching: false,
    coupon: '',
    activeIndex: 0,
  }
  public getAuctionsLeftWithoutPackage = () => {
    const { user } = this.props.user
    const { premiumsHash } = this.props
    if (!user || !premiumsHash) {
      return 0
    }
    const auctionsLeft = user.auctions_left
    const currentPremium = user.premium_package && premiumsHash[user.premium_package.premium_id]
    const currentPremiumAmmount = Number(currentPremium && currentPremium.ammount)
    return auctionsLeft - currentPremiumAmmount
  }
  public renderPayment = ({ name, path, id, price, description, ammount, recurring }: IPremium) => {
    const auctionsLeftAfterThisUpgrade = this.getAuctionsLeftWithoutPackage() + ammount
    const { hasPremium } = this.props
    return (
      <Card
        className="inverted-card premium-card"
        inverted={true}
        key={id}
        onClick={() => {
          this.props.history.push(route.premium(path))
          if (!recurring || !hasPremium) {
            return
          }
          this.setState({ isFetching: true })
          fetchProrationPreview(id)
            .then(
              ({ cost }) => {
                this.setState({ cost, isFetching: false })
              },
          ).catch(
            (e) => {
              this.props.newMessage(e)
              this.setState({ isFetching: false })
            },
          )
        }}
      >
        <Logo />
        <Card.Content>
          <Card.Content extra={true}>
            <Card.Header
              as="h2"
            >
              <div>{name}</div>
              <div>
                {prettyCurrency(price)}
                {recurring && (
                  <small>/mnd</small>
                )}
              </div>
            </Card.Header>
            <Card.Description className="lightDisabledTextss">
              {description}
            </Card.Description>
            <Button
              disabled={recurring && auctionsLeftAfterThisUpgrade < 0}
              inverted={true}
              basic={true}
              size="massive"
            >
              Kjp n
            </Button>
          </Card.Content>
        </Card.Content>
      </Card>
    )
  }
  public onPay = ({ token }: stripe.TokenResponse) => {
    const { selectedPayment } = this.props
    const { isFetching, coupon } = this.state
    if (isFetching || !selectedPayment) {
      return
    }
    if (token) {
      this.props.payForPremium(
        selectedPayment.id,
        selectedPayment.price,
        token.id,
        coupon,
      ).then(
        ((e) => {
          if (e && (e as any).status === 'succeeded') {
            this.props.history.push(route.member())
          }
        }),
      ).catch(newMessage)
    }
  }
  public SelectedPaymentMessage = () => {
    const { isFetching, cost } = this.state
    const { selectedPayment } = this.props
    if (!selectedPayment) {
      return null
    }
    const { recurring, name, ammount, price } = selectedPayment
    return (
      <>
        <Dimmer.Dimmable as={Segment} dimmed={isFetching}>
          <Dimmer active={isFetching}>
            <Loader>Henter data...</Loader>
          </Dimmer>
          {recurring ? (

            <Message info={true}>
              <Message.Header>
                Kjp av pakken {name}
              </Message.Header>
              <p>
                Du kjper n pakken {name} til <b>kr. {price}/mnd.</b>
              </p>
              <p>
                Dette er en mnedlig pakke som fornyes automatisk.
                Du kan nr som helst endre eller avbestille fornyelsen.
              </p>
              <p>
                Her vil du f <b>{ammount}</b> annonser hver mned.
              </p>
              {cost > 0 && (
                <p>
                  Dette vil vre en oppgradering, og du blir dermed fakturert <b>kr. {cost}</b>.
              </p>
              )}
              {cost < 0 && (
                <p>
                  Dette vil vre en nedgradering, og du blir rabbattert <b>kr. {-cost}</b> ved neste faktura.
              </p>
              )}

            </Message>
          )
            : (
              <Message info={true}>
                <Message.Header>
                  Kjp av pakken {name}
                </Message.Header>
                <p>
                  Du kjper n pakken {name} til <b>kr. {price}.</b>
                </p>
                <p>
                  Her vil du f <b>{ammount}</b> annonser.
                </p>
              </Message>
            )
          }
        </Dimmer.Dimmable>
        <Segment>
          <Form>
            <Payment onPay={this.onPay} />
            <Form.Field>
              <label>Eventuell kupong</label>
              <Input
                value={this.state.coupon}
                onChange={(_e, { value }) => this.setState({ coupon: value })}
              />
            </Form.Field>
          </Form>
        </Segment>
      </>
    )
  }
  public Premiums = ({ premiums, header }: { premiums: IPremium[], header: string }) => (
    <Card.Group>
      <DividerHeader>{header}</DividerHeader>
      {premiums.sort(
        ({ price: aPrice }, { price: bPrice }) => aPrice - bPrice,
      ).map(this.renderPayment)}
    </Card.Group>
  )
  public componentWillReceiveProps(nextProps: IConnectProps) {
    const { selectedPayment } = nextProps
    if (selectedPayment && !selectedPayment.recurring && !this.props.selectedPayment && this.state.activeIndex !== 1) {
      this.setState({ activeIndex: 1 })
    }
  }
  public panes = () => {
    const { sortedPremiums } = this.props
    const { Premiums } = this
    return [
      {
        menuItem: { key: 'recurring', icon: <Icon icon="envelope" />, content: 'Mnedspakker' },
        render: () => (
          <Tab.Pane inverted={true}>
            <Premiums
              header="Mnedspakker"
              premiums={sortedPremiums.recurring}
            />
          </Tab.Pane >
        ),
      },
      {
        menuItem: { key: 'nonRecurring', icon: <Icon icon="envelope" />, content: 'Enkeltannonser' },
        render: () => (
          <Tab.Pane  >
            <Premiums
              header="Enkeltannonser"
              premiums={sortedPremiums.nonRecurring}
            />
          </Tab.Pane >
        ),
      },
    ]
  }
  public render() {
    const { sortedPremiums, hasPremium } = this.props
    const { SelectedPaymentMessage, Premiums } = this
    const { activeIndex } = this.state
    return (
      <Container className="premiums-container">
        <Script
          attributes={{ id: 'stripe-js', async: true }}
          url="https://js.stripe.com/v3/"
        />
        <Meta
          title="Premium"
          description={'Auksjonsside for reptiler.'}
        />
        <SelectedPaymentMessage />
        {(hasPremium && !!sortedPremiums.nonRecurring.length) ? (
          <Tab
            className="tab"
            menu={{
              inverted: true,
              attached: true,
            }}
            panes={this.panes()}
            activeIndex={activeIndex}
            onTabChange={(_e, { activeIndex: v }) => this.setState({ activeIndex: Number(v) })}
          />
        ) : (
            <Premiums
              header="Mnedspakker"
              premiums={sortedPremiums.recurring}
            />
          )}
      </Container>
    )
  }
}
import { newMessage } from 'actions/message'
import { payForPremium } from 'actions/premium'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ premium, user }: IStoreState, props: RouteComponentProps<any>) => {
    const pathName = props.match.params.path_name
    const premiums = Object.keys(premium.premium).map((k) => premium.premium[Number(k)])
    const selectedPayment = pathName && premiums.find(
      ({ path }) => path.toLowerCase() === pathName.toLowerCase(),
    )
    const sr: {
      recurring: IPremium[],
      nonRecurring: IPremium[],
    } = {
        recurring: [],
        nonRecurring: [],
      }
    const sortedPremiums = premiums.reduce(
      (r, p) => {
        if (p.recurring) {
          r.recurring.push(p)
        } else {
          r.nonRecurring.push(p)
        }
        return r

      },
      sr,
    )
    const hasPremium = user.user && user.user.premium_package
    return {
      selectedPayment,
      user,
      hasPremium,
      isFetching: premium.isFetching,
      premiums,
      sortedPremiums,
      premiumsHash: premium.premium,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      payForPremium: (id: number, ammount: number, nonce: string, coupon: string) =>
        dispatch(payForPremium(id, ammount, nonce, coupon)),
      newMessage: (e: any) => dispatch(newMessage(e)),

    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(Premium)



