import { postHeaders, VERBS } from './constants'
import { myFetch } from './helpers'

interface IProrationPreview {
  cost: number,
  from_plan: number,
  to_plan: number
}

export const fetchProrationPreview = (planId: number) =>
  myFetch(`/checkout/preview?id=${planId}`) as Promise<IProrationPreview>
export const postPaymentPremium = (premiumId: number, ammount: number, nonce: string, coupon?: string) => myFetch(
  '/checkout/premium',
  {
    method: VERBS.POST,
    body: {
      nonce,
      premium_id: premiumId,
      ammount,
      coupon,
    },
    headers: postHeaders,
  },

) as Promise<{ id: string, status: string }>



