import { IAd } from 'api/interfaces'
import Stat from 'components/widgets/Stat'
import TinyUserInfo from 'components/widgets/TinyUserInfo'
import { UsersInfo } from 'interfaces'
import * as React from 'react'
import { Header, Segment, SemanticCOLORS, Statistic, StatisticProps } from 'semantic-ui-react'
import AuctionTimeLeft from './AuctionTimeLeft'

interface IProps {
  ad: IAd,
  highestBidder: UsersInfo | undefined
  segmentProps?: StatisticProps
  inverted?: boolean
}

export interface IBidStats {
  value: any
  color: SemanticCOLORS
  label: any
  icon: any
  popup?: any
}
export default ({ ad, highestBidder, segmentProps, inverted }: IProps) => {
  const { active, bidCount, currentPrice, minBid, deadline } = ad
  const userVerified = !!highestBidder && highestBidder.verified
  const items: IBidStats[] = [
    {
      icon: userVerified ? 'user-plus' : 'user',
      color: 'blue',
      value: highestBidder && highestBidder.given_name,
      label: active ? 'Budleder' : 'Kjper',
      popup: highestBidder && <TinyUserInfo user={highestBidder} />,
    },
    {
      icon: 'money-bill-alt',
      color: 'green',
      value: Number(currentPrice),
      label: `${bidCount ? 'bud' : ''}pris`,
    },
    {
      icon: 'money-bill',
      color: 'orange',
      value: active && Number(minBid),
      label: 'Minste gyldige bud',
    },
    {
      icon: 'hashtag',
      color: 'blue',
      value: bidCount,
      label: 'Bud',
    },
  ]
  return (
    <Segment inverted={inverted} {...segmentProps}>
      <Header textAlign="left" as="h3">Budinfo</Header>
      <Statistic.Group
        inverted={inverted}
        horizontal={true}
        size="mini"
        style={{
          alignItems: 'center',
        }}
      >
        {items
          .filter(({ value }) => value)
          .map(({ color, value, label, icon, popup }) =>
            <Stat {...{ key: label, inverted, label, value, icon, popup, statProps: { color } }} />,
        )
        }
        {active && <AuctionTimeLeft deadline={deadline} as="stats" icon="clock" />}
      </Statistic.Group>
    </Segment>
  )
}



