/* tslint:disable:max-line-length*/
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import { AuctionStatusInfo, IAuctionStatus } from 'api/constants'
import { IAd } from 'api/interfaces'
import AuctionTable from 'components/Auction/AuctionTable'
import DividerHeader from 'components/utils/DividerHeader'
import Meta from 'components/utils/Meta'
import RequiresLogin from 'components/utils/RequiresLogin'
import * as React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container, Header, Icon } from 'semantic-ui-react'
import { route } from 'utils/misc'

enum BidStatus {
  overbid, highBidder, lost, won,
}

const auctionBidStatuses: { [s: string]: AuctionStatusInfo } = {
  [BidStatus.highBidder]: {
    text: 'Du leder',
    description: 'Gratulerer, du leder disse auksjonene.',
    color: 'green',
    icon: 'badge-check',
    sortOrder: 1,
  },
  [BidStatus.overbid]: {
    text: 'Overbud',
    description: ' nei! Noen har lagt inn bud over dine bud.',
    color: 'red',
    icon: 'money-bill',
    sortOrder: 0,
  },
  [BidStatus.lost]: {
    text: 'Vant ikke',
    description: 'Uff da, du vant dessverre ikke disse auksjonene. Men det finnes alltids flere reptiler.',
    color: 'yellow',
    icon: 'frown',
    sortOrder: 3,
  },
  [BidStatus.won]: {
    text: 'Vunnede',
    description: 'HURRA! Du vant disse auksjonene!',
    color: 'purple',
    icon: 'star',
    sortOrder: 2,
  },
}

class UserAuctions extends React.Component<IConnectProps> {
  public componentDidMount() {
    if (!this.props.areBidsFetching) {
      this.props.getUserBids()
    }
  }
  public onClick = (id: number) => this.props.history.push(route.auction(id))

  public render() {
    const { userAuctions, userId } = this.props
    const auctionStatuses: { [s: string]: IAd[] } = userAuctions
      .reduce(
        (r, auction) => {
          const userIsHighBidder = userId === auction.highestBidderId
          const ended = auction.status !== IAuctionStatus.active
          // const userDidBid = userbBidAuctionIds && userbBidAuctionIds.indexOf(auction.id) !== -1
          let status
          if (userIsHighBidder) {
            status = ended ? BidStatus.won : BidStatus.highBidder
          } else {
            status = ended ? BidStatus.lost : BidStatus.overbid
          }
          if (r[status]) {
            r[status].push(auction)
          } else {
            r[status] = [auction]
          }
          return r
        },
        {},
    )
    return (
      <Container>
        <Meta
          title="Mine bud"
          description={'Auksjonsside for reptiler.'}
        />
        <DividerHeader
          subheader="Her kan du se en oversikt over dine bud."
        >
          Dine bud
        </DividerHeader>
        <RequiresLogin showLoginButton={true}>
          {Object.keys(auctionStatuses)
            .sort(
              (statusA, statusB) => auctionBidStatuses[statusA].sortOrder - auctionBidStatuses[statusB].sortOrder,
          )
            .map(
              (status) => {
                const auctions = auctionStatuses[status]
                const { color, text, icon, description } = auctionBidStatuses[status]
                return (
                  <React.Fragment key={status}>
                    <Header
                      as="h2"
                      icon={true}
                      inverted={true}
                      textAlign="center"
                    >
                      <Icon color={color}>
                        <FontAwesomeIcon icon={icon} className="no-margin"/>
                      </Icon>
                      {text}
                      <Header.Subheader>
                        {description}
                      </Header.Subheader>
                    </Header>
                    <AuctionTable
                      tableProps={{
                        color,
                      }}
                      key={status}
                      auctions={auctions}
                      onClick={this.onClick}
                    />
                  </React.Fragment>
                )
              },
          )}
        </RequiresLogin>
      </Container >
    )
  }
}
import { getUserBids } from 'actions/user'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, users, ads }: IStoreState) => {
    const userbBidAuctionIds = user.user && user.user.bid_auctions
    return {
      userId: user.user && user.user.id,
      areBidsFetching: user.areBidsFetching,
      users,
      userbBidAuctionIds,
      userAuctions: Object.keys(ads.ads)
        .map((k) => ads.ads[Number(k)])
        .filter(({ id }) => userbBidAuctionIds && userbBidAuctionIds.indexOf(id) !== -1),
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getUserBids: () =>
        dispatch(getUserBids()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(UserAuctions)



