import {
  fetchConversation,
  fetchNewMessagesUpdateReadStatus,
  fetchOverview,
  fetchUnread,
  postMessageInConversation,
  updateReadStatusMultiple,
} from 'api/conversation'
import { IConversationWithMessages, IMessageFull, IMessagesResponse, IReadStatus } from 'api/interfaces'

import { Dispatch } from 'react-redux'
import { IStoreState } from 'store'
import { recieveAds } from './ad'
import { errorCatcher } from './message'
import { recieveUsers } from './users'

export enum ConversationR {
  getConversation = 'getConversation',
  updateReadStatus = 'updateReadStatus',
  getMessages = 'getMessages',
  clearMessages = 'clearMessages',
  postReadStatusMultiple = 'postReadStatusMultiple',
  sendMessageInConversation = 'sendMessageInConversation',
  recieveSingleConversation = 'recieveSingleConversation',
  recieveSingleMessage = 'recieveSingleMessage',
  recieveMessages = 'recieveMessages',
  recieveConversations = 'recieveConversations',
  getUserConversations = 'getUserConversations',
  postMessageInConversation = 'postMessageInConversation',
  resetIsFetching = 'resetIsFetchingConverstaion',
}

const _resetFetching = (): ConversationT => {
  return {
    type: ConversationR.resetIsFetching,
  }
}

export type ConversationT =
  { type: ConversationR.resetIsFetching } |
  { type: ConversationR.postReadStatusMultiple } |
  { type: ConversationR.getConversation } |
  { type: ConversationR.getMessages } |
  { type: ConversationR.clearMessages } |
  { type: ConversationR.sendMessageInConversation } |
  {
    type: ConversationR.recieveConversations,
    conversations: IConversationWithMessages[],
  } |
  {
    type: ConversationR.updateReadStatus,
    ids: number[],
  } |
  {
    type: ConversationR.recieveSingleMessage,
    message: IMessageFull,
  } |
  {
    type: ConversationR.recieveMessages,
    messages: IMessageFull[],
  } |
  {
    type: ConversationR.recieveSingleConversation,
    conversation: IConversationWithMessages,
  }

export const recieveConversations = (
  conversations: IConversationWithMessages[],
): ConversationT => {
  return {
    type: ConversationR.recieveConversations,
    conversations,
  }
}

export const _updateReadStatus = (
  ids: number[],
): ConversationT => {
  return {
    type: ConversationR.updateReadStatus,
    ids,
  }
}

export const recieveSingleConversation = (
  conversation: IConversationWithMessages,
): ConversationT => {
  return {
    type: ConversationR.recieveSingleConversation,
    conversation,
  }
}

export const clearMessages = (): ConversationT => {
  return {
    type: ConversationR.clearMessages,
  }
}

export const recieveSingleMessage = (
  message: IMessageFull,
): ConversationT => {
  return {
    type: ConversationR.recieveSingleMessage,
    message,
  }
}

export const recieveMessages = (
  messages: IMessageFull[],
): ConversationT => {
  return {
    type: ConversationR.recieveMessages,
    messages,
  }
}

const _getConversation = (): ConversationT => {
  return {
    type: ConversationR.getConversation,
  }
}
const _postReadStatusMultiple = (): ConversationT => {
  return {
    type: ConversationR.postReadStatusMultiple,
  }
}

const _getMessages = (): ConversationT => {
  return {
    type: ConversationR.getMessages,
  }
}

const _sendMessageInConversation = (): ConversationT => {
  return {
    type: ConversationR.sendMessageInConversation,
  }
}

export const getConversation = (conversationId: number, updateReadStatus?: Date) => {
  return (dispatch: Dispatch<any>) => {
    dispatch(_getConversation())
    if (updateReadStatus) {
      return fetchNewMessagesUpdateReadStatus(conversationId, updateReadStatus)
        .then(
          ({ conversation, users }) => {
            conversation && dispatch(recieveSingleConversation(conversation))
            users && dispatch(recieveUsers(users))
            return { conversation, users }
          },
      ).catch(errorCatcher(dispatch, 'fetchNewMessagesUpdateReadStatus', _resetFetching))

    }
    return fetchConversation(conversationId)
      .then(
        ({ conversation, users }) => {
          conversation && dispatch(recieveSingleConversation(conversation))
          users && dispatch(recieveUsers(users))
          return { conversation, users }
        },
    )
      .catch(errorCatcher(dispatch, 'getConversation', _resetFetching))
  }
}

const handleMessageResponse = (dispatch: Dispatch<any>) =>
  ({ messages, users, ads }: IMessagesResponse) => {
    users && dispatch(recieveUsers(users))
    ads && dispatch(recieveAds(ads, true))
    messages && dispatch(recieveMessages(messages))
    return { messages, users }
  }

export const getUnreadMessages = () => {
  return (dispatch: Dispatch<any>, getState: () => IStoreState) => {
    dispatch(_getMessages())
    const { users, ads } = getState()
    const userIds = Object.keys(users)
    const auctionIds = Object.keys(ads.ads)
    return fetchUnread({ ads: auctionIds, users: userIds })
      .then(handleMessageResponse(dispatch))
      .catch(errorCatcher(dispatch, 'getUnreadMessages', _resetFetching))
  }
}

export const getOverview = () => {
  return (dispatch: Dispatch<any>, getState: () => IStoreState) => {
    dispatch(_getMessages())
    const { users, ads } = getState()
    const userIds = Object.keys(users)
    const auctionIds = Object.keys(ads.ads)
    return fetchOverview({ ads: auctionIds, users: userIds })
      .then(handleMessageResponse(dispatch))
      .catch(errorCatcher(dispatch, 'getOverview', _resetFetching))
  }
}

export const newMessageInConversation =
  (conversationId: number, message: string) => {
    return (dispatch: Dispatch<any>) => {
      dispatch(_sendMessageInConversation())
      return postMessageInConversation(conversationId, message)
        .then(({ message: msg }) => {
          dispatch(recieveSingleMessage(msg))
          return msg
        },
      )
        .catch(errorCatcher(dispatch, 'newMessageInConversation', _resetFetching))
    }
  }

export const postReadStatusMultiple =
  (updates: IReadStatus[]) => {
    return (dispatch: Dispatch<any>) => {
      dispatch(_postReadStatusMultiple())
      return updateReadStatusMultiple(updates)
        .then(({ status }) => {
          if (status === 'OK') {
            const ids = Object.keys(updates).map((k) => updates[k]).map(
              ({ id }) => id,
            )
            dispatch(_updateReadStatus(ids))
          }
        },
      )
        .catch(errorCatcher(dispatch, 'getConversation', _resetFetching))
    }
  }



