import { Category, IPremium, IPromotion, MainCategory, Morph } from 'interfaces'
import { IAuctionStatus } from './constants'

import { postHeaders, VERBS } from './constants'
import { myFetch } from './helpers'
import { IAdNew, IAdResponse, IAdResponseMulti, IBid } from './interfaces'

export const postAd = (ad: IAdNew) => myFetch('/json/v1/ad/', {
  body: ad,
  method: VERBS.POST,
  headers: postHeaders,
}) as Promise<IAdResponse>

export const postAdChangeStatus = (adId: number, status: IAuctionStatus) => myFetch(`/json/v1/ad/${adId}`, {
  body: { status },
  method: VERBS.PATCH,
  headers: postHeaders,
}) as Promise<IAdResponse>

export const postBid = (bid: IBid) => myFetch('/json/v1/bid/', {
  body: bid,
  method: VERBS.POST,
  headers: postHeaders,
}) as Promise<IAdResponse>

export const postBuyNow = (auctionId: number) => myFetch('/json/v1/buy_now/', {
  body: { auction_id: auctionId },
  method: VERBS.POST,
  headers: postHeaders,
}) as Promise<IAdResponse>

export const fetchAds = (daysOffset: number = 300) =>
  myFetch(
    `/json/v1/ad/${daysOffset ? '?days=' + daysOffset : ''}`,
  ) as Promise<IAdResponseMulti>

export const fetchAd = (id: number) => myFetch(`/json/v1/ad/${id}`) as Promise<IAdResponse>

export const fetchBaseInfo = () => myFetch(
  '/json/v1/base/') as Promise<{
    categories: Category[],
    main_categories: MainCategory[],
    morphs: Morph[],
    premiums: IPremium[],
    promotions: IPromotion[],
  }>



