const escapeRegexp = (s: string) => {
  return s.replace(/[-\/\\^$*+?.()|[\]{}]/g, '\\$&')
}

const searchPriceRangeRegex = /^(\d*[\.,]?\d*)-?(\d*[\.,]?\d*)?$/

export const searchPriceRange = (query: string, price: number) => {
  const m = searchPriceRangeRegex.exec(query)
  if (m === null) {
    return false
  }
  const min = Number(m[1])
  const max = Number(m[2]) || Infinity
  return {
    min,
    max,
    match: min <= price && price <= max,
  }
}

