export const sorter = (
  sortby: string, asc: boolean = true, sortFunc = (a: any) => a, defaultSort: (A: any, B: any) => number,
) => {
  return (a: any, b: any) => {
    const A = sortFunc(a[sortby])
    const B = sortFunc(b[sortby])
    const P = (asc ? 1 : -1)
    const N = (asc ? -1 : 1)

    if (A === null) {
      return N
    }
    if (B === null) {
      return P
    }
    if (A > B) {
      return N
    }
    if (A < B) {
      return P
    }
    return defaultSort(a, b)
  }
}

export const auctionFilter = (
  { ad, settings, categories, morphs, mainCategories }: {
    ad: IAd,
    settings: IStoreSettings,
    mainCategories: {
      [s: string]: MainCategory,
    },
    categories: {
      [s: string]: Category,
    },
    morphs: {
      [s: string]: Morph,
    },
  },
) => {
  if (!ad) {
    return false
  }
  const { deadline, currentPrice, sex, category, ready_for_breeding, proven_breeder, morph, genetic_bomb } = ad
  const { filterDeadline, filterEnded, filterNegDeadline,
    filterMinPrice, filterMaxPrice, filterSex,
    filterCategories, filterNegCategories, filterMorphs,
    filterNegMorphs, filterBreed, filterGeneticBomb,
    filterMainCategories, searchQuery } = settings
  if (!filterDeadline && !deadline) {
    return false
  }
  if (filterSex.length && filterSex.indexOf(sex) === -1) {
    return false
  }
  if (filterBreed.length) {
    if (filterBreed.indexOf(Breeder.ready) !== -1) {
      return ready_for_breeding
    }
    if (filterBreed.indexOf(Breeder.proven) !== -1) {
      return proven_breeder
    }
  }
  if (!filterGeneticBomb && genetic_bomb) {
    return false
  }
  if (!filterEnded && !!deadline && !!isPast(deadline)) {
    return false
  }
  if (!filterNegDeadline && !!deadline && !isPast(deadline)) {
    return false
  }
  if (filterMinPrice > currentPrice) {
    return false
  }
  if (filterMaxPrice > 0 && filterMaxPrice < currentPrice) {
    return false
  }
  if (filterCategories.length) {
    const index = filterCategories.indexOf(Number(category))
    if (filterNegCategories) {
      if (index !== -1) {
        return false
      }
    } else if (index === -1) {
      return false
    }
  }
  if (filterMainCategories.length) {
    const mainCategoryId = categories[Number(category)].main_category_id
    const index = filterMainCategories.indexOf(mainCategoryId)
    if (filterNegCategories) {
      if (index !== -1) {
        return false
      }
    } else if (index === -1) {
      return false
    }
  }
  if (filterMorphs.length) {
    const index = filterMorphs.indexOf(Number(morph))
    if (filterNegMorphs) {
      if (index !== -1) {
        return false
      }
    } else if (index === -1) {
      return false
    }
  }
  if (searchQuery) {
    const escaped = escapeRegexp(searchQuery)
    const priceMatch = searchPriceRange(searchQuery, ad.minBid)
    if (priceMatch && priceMatch.match) {
      return true
    } else {
      const thisCategory = categories[Number(category)]
      const thisMorph = morphs[Number(morph)]
      const mainCategory = mainCategories[thisCategory.main_category_id]
      // Need nullchecking in case of bad previous data.
      const searchFields = [
        thisCategory && thisCategory.title,
        thisMorph && thisMorph.title,
        mainCategory && mainCategory.title,
      ]

      if (!searchFields.join(' ').match(new RegExp(`\\b${escaped}`, 'i'))) {
        return false
      }
    }
  }
  return true
}



