export enum ProfileFields {
  mobile = 'profile.mobile',
  address1 = 'profile.address1',
  address2 = 'profile.address2',
  post_code = 'profile.post_code',
  post_area = 'profile.post_area',
  agreedTOS = 'profile.agreedTOS',
}

export interface IProfileForm {
  mobile: string
  address1: string
  address2: string
  post_code: number | ''
  post_area: string
  agreedTOS: boolean
}

export const initialProfileState: IProfileForm = {
  mobile: '',
  address1: '',
  address2: '',
  post_code: '',
  post_area: '',
  agreedTOS: false,
}



