import Icon, { Loading } from 'components/utils/Icon'
import * as React from 'react'
import { Button, ButtonProps, Icon as UIIcon } from 'semantic-ui-react'

interface IProps extends ButtonProps {
  [s: string]: any
  loading?: boolean
  error?: boolean
  icon: any
}

export default ({ icon, loading, error, children, ...props }: IProps) => (
  <Button
    style={{
      minWidth: 300,
      marginBottom: 5,
    }}
    labelPosition="left"
    size="big"
    icon={true}
    {...props}
  >
    <UIIcon.Group
      className="big icon login-icons"
    >
      <Icon icon={icon} className="no-margin" />
      <Loading
        active={!!loading}
        color="yellow"
      />
      {error && (
        <Icon
          icon="exclamation-triangle"
          corner={true}
          color="yellow"
        />)}
    </UIIcon.Group>
    {children}
  </Button>
)



