import { MessageR, MessageT } from 'actions/message'
import { IMessage } from 'interfaces'
export const initialErrorState = {}

export interface IMessageState {
  [s: string]: IMessage
}

export default (state: IMessageState = initialErrorState, action: MessageT) => {
  switch (action.type) {
    case MessageR.newMessage:

      const { message } = action
      if (!message) {
        return state
      }
      return {
        ...state,
        [message.id]: message,
      }
    case MessageR.dismissMessage:
      const { id } = action
      // tslint:disable-next-line
      const { [id]: omit, ...rest } = state
      return rest
    default:
      return state
  }
}



