import SocialButton from 'components/widgets/SocialButton'
import * as React from 'react'
import { ButtonProps } from 'semantic-ui-react'

const APP_ID = process.env.REACT_APP_FB_APP_ID as string
const DOMAIN = process.env.REACT_APP_DOMAIN as string
{/* tslint:disable-next-line */ }
const url = `https://www.facebook.com/v3.0/dialog/oauth?client_id=${APP_ID}&response_type=token&redirect_uri=${encodeURIComponent(
  `${DOMAIN}/login`,
)}&scope=email`

export default (props: ButtonProps) => (
  <SocialButton
    onClick={() => {
      window.open(
        url, '_self')
    }}
    color="facebook"
    icon={['fab', 'facebook']}
    {...props}
  />
)



