import AdList from 'components/Auction/AdList'
import AuctionPagination from 'components/Auction/AuctionPagination'
import AuctionSort from 'components/Auction/AuctionSort'
import DividerHeader from 'components/utils/DividerHeader'
import Icon from 'components/utils/Icon'
import Meta from 'components/utils/Meta'
import Hamburger from 'components/widgets/Hamburger'
import * as React from 'react'
import { Divider, Loader, Message, Segment } from 'semantic-ui-react'
import { auctionFilter, getSortItems } from 'utils/sorting'

class Auction extends React.Component<IConnectProps> {
  public componentDidMount() {
    const { isFetching } = this.props
    if (!isFetching) {
      this.props.getAds()
    }
  }
  public shouldComponentUpdate(nextProps: IConnectProps) {
    if (!nextProps.isLoaded || nextProps.isFetching || nextProps.isBaseFetching) {
      return false
    }
    return true
  }

  public render() {
    const { sortKey, sortAsc, auctionPage, auctionsPerPage } = this.props.settings
    const { isFetching, categories, morphs, mainCategories, ads } = this.props
    const sortItems = getSortItems({ categories, morphs, mainCategories })
    const filteredAuctions = Object.keys(ads).map((k) => ads[Number(k)]).filter((ad) => auctionFilter({
      ad,
      settings: this.props.settings,
      categories,
      morphs,
      mainCategories,
    }))
    return (
      <Segment inverted={true} >
        <Meta
          title="Auksjoner"
          description={'Auksjonsside for reptiler.'}
        />
        <DividerHeader>
          Reptilauksjonen
        </DividerHeader>
        <Hamburger
          before="Filtrering og sortering"
        >
          <AuctionSort inverted={true} />
        </Hamburger>
        <Divider hidden={true} />
        {!!filteredAuctions.length ?
          (
            <AdList
              ads={filteredAuctions}
              sortBy={sortKey}
              maxEntries={auctionsPerPage}
              page={auctionPage}
              sortOrder={sortAsc}
              sortFunc={sortItems[sortKey].sortFunc}
            />
          ) : (
            <div className="flex flex-center">
              <Message
                compact={true}
                color="black"
              >
                <Message.Header>
                  {isFetching
                    ? (
                      <Loader active={true} size="big" />
                    )
                    : (
                      <>
                        <Icon icon="bug" />
                        Ingen reptiler  vise
                      </>

                    )}
                </Message.Header>
              </Message>
            </div>
          )
        }
        <Divider hidden={true} />
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <AuctionPagination count={filteredAuctions.length} />
        </div>
      </Segment >
    )
  }
}
import { getAds } from 'actions/ad'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ base, settings, ads }: IStoreState) => {
    return {
      ads: ads.ads,
      isLoaded: ads.loaded && base.loaded,
      isFetching: ads.isFetching,
      isBaseFetching: base.isFetching,
      categories: base.categories,
      mainCategories: base.mainCategories,
      settings,
      morphs: base.morphs,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getAds: () =>
        dispatch(getAds()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export default connectCreator(Auction)



