/* tslint:disable:max-line-length*/
import DividerHeader from 'components/utils/DividerHeader'
import Icon from 'components/utils/Icon'
import Meta from 'components/utils/Meta'
import RequiresLogin from 'components/utils/RequiresLogin'
import MessagesList from 'components/widgets/MessageList'
import * as React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Container, Header, Label, Menu, Message, Tab } from 'semantic-ui-react'
import { route } from 'utils/misc'
const debounce = require('lodash-es/debounce').default

enum Panes {
  unread, overview,
}

const paneInfo = {
  [Panes.unread]: {
    path: route.unreadMessages(),
    index: Panes.unread,
  },
  [Panes.overview]: {
    path: route.messagesOverview(),
    index: Panes.overview,
  },
}

class Messages extends React.PureComponent<IConnectProps> {
  public panes = () => ([
    {
      menuItem: (
        <Menu.Item key={Panes.unread}>
          Uleste
          {!!this.props.unread && (
            <Label
              color="red"
              floating={true}
              circular={true}
            >
              {this.props.unread}
            </Label>
          )}
        </Menu.Item>
      ),
      render: () => (
        <Tab.Pane  >
          <Header>Dine uleste meldinger</Header>
          <MessagesList
            only_unread={true}
            noContent={((
              <Message
                content="Du har ingen uleste meldinger"
              />
            ))}
          />
        </Tab.Pane >
      ),
    },
    {
      menuItem: { key: 'users', icon: <Icon icon="envelope" />, content: 'Oversikt' },
      render: () => (
        <Tab.Pane  >
          <Header>Dine samtaler</Header>
          <MessagesList
            only_unread={false}
            limit={1}
            noContent={((
              <Message
                content="Ingen samtaler  vise."
              />
            ))}
          />
        </Tab.Pane >
      ),
    },
  ])

  public componentWillMount() {
    if (this.props.loggedIn) {
      const activePane = this.getPane()
      if (activePane === Panes.overview) {
        this.props.getOverview()
      }
    }
  }
  public componentWillReceiveProps({ loggedIn, isFetching }: IConnectProps) {
    if (!this.props.loggedIn && loggedIn && !isFetching) {
      const activePane = this.getPane()
      if (activePane === Panes.overview) {
        this.props.getOverview()
      }
    }
  }

  public getPane = () => {
    for (const { path, index } of Object.keys(paneInfo).map((k) => paneInfo[k])) {
      if (path === this.props.location.pathname) {
        return index
      }
    }
    return Panes.overview
  }

  public handleTabChange = (_e: any, { activeIndex: i }: { activeIndex: Panes }) => {
    const pathname = paneInfo[i].path
    this.props.history.push(pathname)
    if (i === Panes.overview) {
      this.props.getOverview()
    } else if (i === Panes.unread) {
      this.props.getUnreadMessages()
    }
  }

  public render() {
    const activeIndex = this.getPane()
    return (
      <Container>
        <Meta
          title="Meldinger"
          description={'Meldinger fra kjp og salg.'}
        />

        <DividerHeader>
          Samtaler
        </DividerHeader>
        <RequiresLogin showLoginButton={true}>
          <Tab
            menu={{
              color: 'blue',
              inverted: true,
              attached: true,
            }}
            panes={this.panes()}
            activeIndex={activeIndex}
            onTabChange={this.handleTabChange}
          />
        </RequiresLogin>
      </Container >
    )
  }
}

import { getOverview, getUnreadMessages } from 'actions/conversation'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ conversation, user }: IStoreState) => {
    const userId = user.user && user.user.id
    const unread = !userId ? 0 : (Object.keys(conversation.message))
      .reduce(
        (r, key) => {
          const m = conversation.message[Number(key)]
          return r + Number(userId !== m.user && m.unread)
        },
        0,
    )
    return {
      isFetching: conversation.isFetching,
      unread,
      loggedIn: !!userId,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getUnreadMessages: debounce(() => dispatch(getUnreadMessages()), 5000, { leading: true }),
      getOverview: debounce(() => dispatch(getOverview()), 5000, { leading: true }),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & RouteComponentProps<any>
export default connectCreator(Messages)



