import { Sex } from 'api/interfaces'

export const sexIcon = (sex: Sex) => {
  switch (sex) {
    case Sex.f:
      return ['fas', 'mars']
    case Sex.m:
      return ['fas', 'venus']
    case Sex.o:
      return 'genderless'
    default:
      return 'genderless'
  }
}



