import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import Icon from 'components/utils/Icon'
import Hamburger from 'components/widgets/Hamburger'
import Logo from 'components/widgets/Logo'
import Nav from 'components/widgets/Nav'
import * as React from 'react'
import { Link, RouteComponentProps } from 'react-router-dom'
import { Button, Responsive, Segment } from 'semantic-ui-react'
import { route } from 'utils/misc'
interface IProps {
  children: any
}

class AppC extends React.PureComponent<IConnectProps> {
  public hamburger: Hamburger | null = null
  public onRouteChanged() {
    this.hamburger && this.hamburger.setState({ visible: false })
  }
  public render() {
    const { unread, isLoggedIn } = this.props
    return (
      <>
        <Responsive
          {...Responsive.onlyMobile}
          as={Segment}
          inverted={true}
          className="lightText"
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}
        >
          <div className="title">
            <Link
              to={route.home()}
              style={{ color: 'inherit' }}
            >
              <Logo
                semanticProps={{
                  size: 'tiny',
                  style: {
                    marginBottom: -10,
                  },
                }}
              />
              Reptilauksjonen
            </Link>
          </div>
          <div className="flex">
            {!!unread && (
              <Link
                className="fa-layers fa-fw unread-messages-icon"
                to={route.unreadMessages()}
              >
                <FontAwesomeIcon
                  icon="envelope"
                />
                <span className="fa-layers-counter">
                  {unread}
                </span>
              </Link>
            )}
            {isLoggedIn ? (

              <Hamburger
                floating={true}
                ref={(instance) => this.hamburger = instance}
                trigger={(
                  <Button
                    icon={<Icon icon="bars" className="no-margin" />}
                    inverted={true}
                    basic={true}
                  />
                )}
              >
                <Nav
                  flatten={true}
                  onNavigate={(_p) => this.onRouteChanged()}
                  {...this.props}
                />
              </Hamburger>
            ) : (
                <Link to={route.login()} className="login-icon">
                  <Icon icon="sign-in-alt" className="no-margin" size="big" />
                  <div>Logg inn</div>
                </Link>
              )}
          </div>
        </Responsive>
        <Responsive
          minWidth={Responsive.onlyTablet.minWidth}
        >
          <Segment
            style={{
              marginBottom: 0,
              display: 'flex',
              alignItems: 'center',
            }}
            inverted={true}
            size="massive"
          >
            <Nav {...this.props} />
          </Segment>
        </Responsive>
      </>
    )
  }
}
import { connect } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ conversation, user }: IStoreState) => {
    const userId = user.user && user.user.id
    return {
      isLoggedIn: !!userId,
      unread: !userId ? 0 : (Object.keys(conversation.message))
        .reduce(
          (r, key) => {
            const m = conversation.message[Number(key)]
            return r + Number(userId !== m.user && m.unread)
          },
          0,
      ),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps & RouteComponentProps<any>
export default connectCreator(AppC)



