import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import * as React from 'react'
import { Transition } from 'semantic-ui-react'

interface IState {
  showSuccess: boolean
}

class ContentLoader extends React.PureComponent<IConnectProps, IState> {
  public state = {
    showSuccess: false,
  }
  public componentWillReceiveProps(nextProps: IConnectProps) {
    const { isAnyFetching } = this.props
    const showSuccess = !nextProps.isAnyFetching && isAnyFetching
    if (showSuccess) {
      this.setState({
        showSuccess,
      })
    }
  }
  public render() {
    const { isAnyFetching } = this.props
    const { showSuccess } = this.state
    return (
      <div
        style={{
          position: 'fixed',
          right: '5%',
          bottom: '5%',
          top: 'unset',
          left: 'unset',
          zIndex: 1099,
        }}
      >
        {isAnyFetching && (
          <FontAwesomeIcon icon={['fas', 'spinner']} pulse={true} style={{ color: '#0da345' }} size="2x" />
        )}
        <Transition
          transitionOnMount={true}
          mountOnShow={true}
          visible={showSuccess}
          animation="fade up"
          onShow={() => setTimeout(() => this.setState({ showSuccess: false }), 1500)}
          duration={500}
        >
          <FontAwesomeIcon icon={['fas', 'check']} style={{ color: '#0d69a3' }} size="2x" />
        </Transition>
      </div >
    )
  }
}
enum LoadKeys {
  isPosting = 'isPosting',
  isFetching = 'isFetching',
}
import { getBaseInfo } from 'actions/baseInfo'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, ads, base, premium, conversation, review }: IStoreState) => {
    let isAnyFetching = user.areBidsFetching || user.areAuctionsFetching
    for (const m of [user, ads, base, premium, conversation, review]) {
      if (m[LoadKeys.isFetching]) {
        isAnyFetching = true
        break
      }
      if (m[LoadKeys.isPosting]) {
        isAnyFetching = true
        break
      }

    }
    return {
      isAnyFetching,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      getCategories: () => dispatch(getBaseInfo()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export default connectCreator(ContentLoader)



