import { IAuctionForm } from 'forms/auction'
import { IProfileForm } from 'forms/profile'
import { IStoreSettings } from 'interfaces'
import * as createRavenMiddleware from 'raven-for-redux'
import reducers from 'reducers'
import { IAdState } from 'reducers/ad'
import { IBaseState } from 'reducers/baseInfo'
import { IConversationState } from 'reducers/conversation'
import { IMessageState } from 'reducers/message'
import { IPremiumState } from 'reducers/premium'
import { IReviewState } from 'reducers/review'
import { IUserState } from 'reducers/user'
import { IUsersState } from 'reducers/users'
import { applyMiddleware, combineReducers, compose, createStore } from 'redux'
import reduxReset from 'redux-reset'
import thunkMiddleware from 'redux-thunk'

declare global {
  interface NodeModule {
    hot?: any
  }
}
declare global {
  interface Window { Raven: any }
}
const Raven = require('raven-js')
// tslint:disable-next-line
const release = `${process.env.REACT_APP_VERSION}: ${process.env.REACT_APP_BUILD_TIME} (${process.env.REACT_APP_GITSHA})`
/* tslint:disable-next-line */
console.log(release)
Raven.config(
  process.env.REACT_APP_RAVEN_DNS,
  {
    release,
  }).install()
window.Raven = Raven

export interface IStoreState {
  user: IUserState
  users: IUsersState
  base: IBaseState
  ads: IAdState
  review: IReviewState
  message: IMessageState
  auction: IAuctionForm
  profile: IProfileForm
  settings: IStoreSettings
  premium: IPremiumState
  conversation: IConversationState
  forms: any
}
const componseEnhancers = (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose

import { persistStore } from 'redux-persist'
const store = configureStore()
export const persistor = persistStore(store)

function configureStore() {
  const persisted = persistReducer(
    {
      key: 'root',
      whitelist: ['users'],
      storage,
    },
    combineReducers(reducers))
  const Store = createStore(
    persisted,
    componseEnhancers(
      applyMiddleware(
        thunkMiddleware,
        createRavenMiddleware(Raven),
      ),
      reduxReset(),
    ),
  )

  if (module.hot) {
    // Enable Webpack hot module replacement for reducers
    module.hot.accept('reducers', () => {
      const nextRootReducer = require('reducers/index').default

      Store.replaceReducer(nextRootReducer)
    })
  }

  return Store
}
export default store
import { persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'



