import Icon from 'components/utils/Icon'
import { DefconLevel } from 'interfaces'
import * as React from 'react'
import { Message, MessageProps, Transition } from 'semantic-ui-react'

const defConLevelToMessageProps = (defcon: DefconLevel): MessageProps & { iconName: any } => {
  switch (defcon) {
    case DefconLevel.danger:
      return { negative: true, iconName: 'exclamation-triangle' }
    case DefconLevel.success:
      return { positive: true, iconName: 'check' }
    case DefconLevel.info:
      return { info: true, iconName: 'info' }
    default:
      return { iconName: 'info' }
  }
}

interface IState {
  visible: {
    [s: string]: boolean,
  }
}

class Messages extends React.Component<IConnectProps, IState> {
  public state = {
    visible: {},
  }
  public componentWillReceiveProps({ errorObjs }: IConnectProps) {
    this.setState({
      visible: errorObjs.reduce(
        (r, { key }) => {
          r[key] = true
          return r
        },
        {},
      ),
    })
  }
  public render() {
    const { errorObjs } = this.props
    if (!errorObjs.length) {
      return null
    }
    return (
      <div
        style={{
          position: 'fixed',
          top: '0%',
          left: 5,
          right: 5,
          zIndex: 9999,
        }}
      >
        {errorObjs.map(({ messages, key }) => {
          const visible = this.state.visible[key]
          const dismiss = () => {
            this.setState(
              ({ visible: _v }) => (
                { visible: { ..._v, [key]: false } }
              ))
          }
          const firstMessage = messages.messages[0]
          const { iconName: _i, ...props } = firstMessage && defConLevelToMessageProps(firstMessage.defcon_level)
          setTimeout(dismiss, 8000)
          return (
            < Transition
              transitionOnMount={true}
              animation="fade down"
              key={key}
              onComplete={() => {
                if (!visible) {
                  this.props.dismissMessage(key)
                }
              }}
              visible={visible}
            >
              <Message
                size="tiny"
                {...props}
              >
                <Icon
                  icon="times"
                  style={{ float: 'right', margin: 0 }}
                  onClick={dismiss}
                />
                {messages.messages.map(({ message, defcon_level, validation_errors }, i) => {
                  const { iconName } = defConLevelToMessageProps(defcon_level)
                  return (
                    <React.Fragment key={i}>
                      <Message.Header>
                        <Icon icon={iconName} size="large" />
                        {message} {key === 'update' && (
                          <a
                            href=""
                            onClick={() => {
                              window.location.reload()
                            }}
                          >Last inn p nytt for  aktivere.
                          </a>
                        )}
                      </Message.Header>
                      {validation_errors && Object.keys(validation_errors).length && (
                        <Message.List>
                          {Object.keys(validation_errors).map(
                            (k) => (
                              <Message.Item key={k}>
                                {validation_errors[k]} ({k})
                              </Message.Item>),
                          )}
                        </Message.List>
                      )}
                    </React.Fragment>
                  )
                })}
              </Message>
            </Transition>
          )
        })}
      </div>
    )
  }
}

import { dismissMessage } from 'actions/message'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ message }: IStoreState) => {
    return {
      errorObjs: Object.keys(message)
        .map((key) => ({ key, messages: message[key] })),
      message,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      dismissMessage: (id: string) =>
        dispatch(dismissMessage(id)),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export default connectCreator(Messages)



