import SocialButton from 'components/widgets/SocialButton'
import * as React from 'react'
import Script from 'react-load-script'
import { ButtonProps } from 'semantic-ui-react'

interface IState {
  googleLoaded: boolean
  google: any
}

declare global {
  interface Window { gapi: any }
}

interface IProps extends ButtonProps {
  onLogin: (token: string) => any
}

class GoogleLogin extends React.Component<IProps, IState> {
  public state: IState = {
    googleLoaded: false,
    google: undefined,
  }

  public render() {
    const { googleLoaded, google } = this.state
    const { onLogin, ...rest } = this.props
    return (
      <>
        <Script
          attributes={{ id: 'g-platform', defer: true }}
          url={`https://apis.google.com/js/platform.js`}
          onLoad={() => {
            window.gapi.load('auth2', () => {
              window.gapi.auth2.init({
                client_id: process.env.REACT_APP_G_CLIENT_ID as string,
              }).then(
                (_g: any) => {
                  this.setState({
                    googleLoaded: true,
                    google: window.gapi.auth2.getAuthInstance(),
                  })
                },
              )
            })
          }}
        />
        <SocialButton
          onClick={() => {
            google.signIn({
              prompt: 'select_account',
            }).then(
              ({ Zi }: any) => {
                if (Zi && Zi.id_token) {
                  onLogin(Zi.id_token)
                }
              },
            ).catch(
              console.log,
            )
          }}
          loading={!googleLoaded}
          disabled={!googleLoaded}
          color="google plus"
          icon={['fab', 'google']}
          {...rest}
        />
      </>
    )
  }
}

export default GoogleLogin



