import Icon from 'components/utils/Icon'
import * as React from 'react'
import { Popup, Statistic, StatisticProps } from 'semantic-ui-react'

interface IProps {
  label: string
  value: any
  icon?: any
  inverted?: boolean
  statProps?: StatisticProps
  popup?: any
}

class Stat extends React.Component<IProps> {
  public render() {
    const { statProps, inverted, popup, label, value, icon } = this.props
    return (
      <Statistic
        inverted={inverted}
        style={{
          justifyContent: 'space-between', display: 'flex', width: '100%',
        }}
        {...statProps}
      >
        <Statistic.Value
          style={{ order: 1, marginLeft: 20, textAlign: 'right' }}
        >
          {(typeof icon === 'string' || icon instanceof Array) ? (
            <Icon icon={icon} style={{marginRight: 5}} />
          ) : icon}
          {popup
            ? (
              <Popup
                position="top left"
                trigger={(
                  <span>
                    {value}
                  </span>
                )}
              >
                {popup}
              </Popup>
            ) : (
              <>
                {value}
              </>
            )}
        </Statistic.Value>
        <Statistic.Label
          style={{ order: 0, textAlign: 'left' }}
        >
          {label}
        </Statistic.Label>
      </Statistic>
    )
  }
}

export default Stat



